const CI_TYPE_ICON_SZ = 24; //px, aligned with now-icon size='lg'
import {t} from 'sn-translate';

//Images that are taken from the db_image
const SEVERITIES_TO_GREY_MAP = {
    1: 'severity_critical_grey.png',
    2: 'severity_major_grey.png',
    3: 'severity_minor_grey.png',
    4: 'severity_warning_grey.png',
    5: 'severity_ok_grey.png'
};


const createIndicatorJsx = (count, severity, percentage) => {
    const {label, iconUrl, isEnabled, value} = severity;
    const indicatorIconUrl = isEnabled ? iconUrl : SEVERITIES_TO_GREY_MAP[value];
    const indicatorLabel = `${count} ${label} (${percentage}%)`;
    const indicatorTooltip = t('{0} {1} services', severity.isEnabled ? 'Hide' : 'Show', label);
    return (
        <noc-element-with-tooltip  tooltip={indicatorTooltip}>
            <button
                aria-selected={String(severity.isEnabled)}
                className='indicator-button' >

                <now-image
                    className='indicator-button-icon'
                    src={indicatorIconUrl}
                    height={CI_TYPE_ICON_SZ}
                    width={CI_TYPE_ICON_SZ}
                />
                <span className='indicator-button-label'>{indicatorLabel}</span>
            </button>
        </noc-element-with-tooltip>
    );
};

export const nocSeverityIndicatorView = (state) => {
    const {
        severity,
        count,
        percentage
    } = state.properties;

    const indicatorJsx = createIndicatorJsx(count, severity, percentage);
    return (
        <section className='noc-severity-indicator'>
            {indicatorJsx}
        </section>
    );
};

