import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {ACTION_TYPES} from './effectGetFilterCountHandler';
import {isEmpty as _isEmpty} from 'lodash';

/**
 *
 * @param value
 * @param prevValue
 * @param dispatch
 *
 * @deprecated This is a backward compatibility fix due to a problem in Seismic 6.0.x
 */
export const effectFilterPropertyOnChange = (value, prevValue, dispatch) => {
	if (value !== prevValue) {
		LOGGING.info('Filter property has changed, dispatching query parse', {extra: value.filter});
		dispatch(ACTION_TYPES.QUERY_PARSE_REQUEST_REQUESTED, {sysparm_query: value.filter});
	}
};

export const effectFilterPropertyChanged = (coeffects) => {
	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	if (payload && payload.name === 'filter') {
		const filter = payload.value;

		// check the inner value of the filter object
		if (filter && !_isEmpty(filter.filter)) {
			LOGGING.info('Filter property has changed, dispatching query parse', {extra: filter.filter});
			dispatch(ACTION_TYPES.QUERY_PARSE_REQUEST_REQUESTED, {sysparm_query: filter.filter});
		} else {
			LOGGING.info('Filter property is empty, resetting value');
			updateProperties({filterSize: 0});
		}
	}
};