import nocTileMapConfigEffect from './nocTileMapConfigEffect';
import nocTilemapLayoutConfigEffect from './nocTilemapDynamicLayoutConfigEffect';
import searchTermEffect from './searchTermEffect';
import {CONSTANTS, LOGGING, createActionConfig, colorsAndIconsEffect} from '@devsnc/library-opws-mapping-shared';
import categorizedFieldsEffect from './categorizedFieldsEffect';

const initFlowHandler = {
    effect(coeffects) {
        const {state, dispatch} = coeffects;
        // const {workspace} = state.properties;
        //temporary removing workspace for WS integration
        const workspace = 'nocpit';

        /* TODO FIXME this flow initializes from onConnect(). 'workspace' meant to listen on selectable property from noc-app.
        * but it's not ready yet onConnect
        */

        LOGGING.log('dispatch NOC_TILE_MAP_CONFIG_REQUEST_INIT');
        dispatch(nocTileMapConfigEffect.ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_INIT, {workspace});

        LOGGING.log('dispatch NOC_TILE_MAP_LAYOUT_CONFIG_REQUEST_INIT');
        dispatch(nocTilemapLayoutConfigEffect.ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_CONFIG_INIT_REQUESTED, {
            queryConditions:  'ORDERBYwidth^ORDERBYheight'
        });
    }
};


export const nocTilemapDataBehavior = {
    name: 'nocTilemapDataBehavior',
    actionHandlers: {
        /**
         * TILE_MAP_CONFIG
         */
        [CONSTANTS.ACTION_TYPES.NOC_COMPONENT_CONNECTED]:
            createActionConfig(initFlowHandler),

        [nocTileMapConfigEffect.ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_INIT]:
            createActionConfig(nocTileMapConfigEffect.effectHandlers.handleNocTileMapConfigInit),

        [nocTileMapConfigEffect.ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_SUCCEEDED]:
            createActionConfig(nocTileMapConfigEffect.effectHandlers.handleNocTileMapConfigSucceeded),

        // [nocTileMapConfigEffect.ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_FAILED]:
        //     createActionConfig(nocTileMapConfigEffect.effectHandlers.handleNocTileMapConfigFailed),

        [searchTermEffect.ACTION_TYPES.SEARCH_TERM_CHANGED]:
            createActionConfig(searchTermEffect.effectHandlers.handleNocSearchTerm),

        [CONSTANTS.ACTION_TYPES.NOC_TILE_MAP_CONFIG_SET]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.onTilemapConfigSet),

        [categorizedFieldsEffect.ACTION_TYPES.NOC_GET_CHOICE_FIELDS]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.getChoices),

        [categorizedFieldsEffect.ACTION_TYPES.NOC_GET_COLORS_AND_ICONS]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.getColorsAndIcons),

        /**
         * CATEGORIZED_FIELDS
         */
        [categorizedFieldsEffect.ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_SUCCEEDED]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.setChoicesFieldResource),

        [categorizedFieldsEffect.ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_SET_STATE]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.mergeContentAndIcons),

        /**
         * CATEGORIZED_FIELDS_COLORS
         */
        [colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST]:
            createActionConfig(colorsAndIconsEffect.effectHandlers.colorsFetchedHandler),

        [colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST_SUCCEEDED]:
            createActionConfig(colorsAndIconsEffect.effectHandlers.handleColorsResponse),

        [CONSTANTS.ACTION_TYPES.NOC_COLORS_AND_ICONS_SET]:
            createActionConfig(categorizedFieldsEffect.effectHandlers.mergeContentAndIcons),

        /**
         * DYNAMIC_LAYOUT_CONFIG
         */
        [nocTilemapLayoutConfigEffect.ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_CONFIG_INIT_REQUESTED]:
            createActionConfig(nocTilemapLayoutConfigEffect.effectHandlers.getDynamicLayoutConfig),

        [nocTilemapLayoutConfigEffect.ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_SUCCEEDED]:
            createActionConfig(nocTilemapLayoutConfigEffect.effectHandlers.storeDynamicLayoutConfig)
    }
};