import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, LOGGING, createNocUpdateStateEffect, checkForError, onNextFrame} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty, isString as _isString, isUndefined as _isUndefined} from 'lodash';
import {constructBreadcrumb} from './utils/serviceGroupBreadcrumb';
import {constructServiceGroupsBuckets} from './utils/serviceGroupBuckets';
import constructServiceGroupsTree from './utils/serviceGroupTree';

const ACTION_TYPES = {
    GET_SERVICE_GROUP_TREE_REQUEST: 'GET_SERVICE_GROUP_TREE_REQUEST',
    GET_SERVICE_GROUP_TREE_REQUEST_STARTED: 'GET_SERVICE_GROUP_TREE_REQUEST_STARTED',
    GET_SERVICE_GROUP_TREE_REQUEST_SUCCEEDED: 'GET_SERVICE_GROUP_TREE_REQUEST_SUCCEEDED',
    GET_SERVICE_GROUP_TREE_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
    SERVICE_GROUP_TREE_RESOURCE_STATE_UPDATED: 'SERVICE_GROUP_TREE_RESOURCE_STATE_UPDATED',
    SERVICE_GROUP_SELECT_DIFFERENT_GROUP: 'SERVICE_GROUP_SELECT_DIFFERENT_GROUP'
};

const getServiceGroupsTreeHandler = {
    effect(coeffects) {
        const {dispatch, action, state} = coeffects;
        LOGGING.log(`getServiceGroupsTreeEffect->getServiceGroupsTreeHandler called by action ${action.type}`);
        const {timestampForTiles} = state.properties;

        if (timestampForTiles)
            dispatch(ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST, {
                timestamp: timestampForTiles,
                includeBusinessServices: true
            });
    }
};

const getServiceGroupsTreeRequest = createHttpEffect('/api/sn_nocpit/nocpit/GroupTree', {
    method: 'GET',
    queryParams: ['timestamp', 'includeBusinessServices'],
    startActionType: ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST_STARTED,
    successActionType: ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST_SUCCEEDED,
    errorActionType: ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST_FAILED
});


const handleGetServiceGroupsTreeResponse = async coeffects => {
    const {
		dispatch,
		updateState,
		action: {payload, type}
    } = coeffects;
    
    LOGGING.log(`getServiceGroupsTreeHandler->handleGetServiceGroupsTreeResponse called by action ${type}`);

	const errors = checkForError({payload, dispatch, caller: 'handleGetServiceGroupsTreeResponse'});
	if (errors)
        return;
        
    updateState({
        serviceGroupsTreeResource: {
            succeeded: true,
            data: payload || {}
        }
    });

    await onNextFrame();

    dispatch(ACTION_TYPES.SERVICE_GROUP_TREE_RESOURCE_STATE_UPDATED, {});
};

const createServiceGroupsTreeModel = createNocUpdateStateEffect((state, action, getAfterStateUpdate) => {
    LOGGING.log(`getServiceGroupsTreeHandler->createServiceGroupsTreeModel called by action ${action.type}`);
    const {properties, populationMapper, serviceGroupsTreeResource: {data = {}} = {}} = state;
    const {tilemapsFormat, timestampForTiles, severities} = properties;
    const {businessServiceGroup} = tilemapsFormat || {};

    //Verify population timestamp is equal to the one within the tree
    if (!severities) {
        return state;
    }

    if (timestampForTiles !== data.timestamp) {
        //TODO Arik: what was the reason for this?
        // LOGGING.info('timestampForTiles !== data.timestamp --> did not render the tiles before!');
    }

    const breadcrumb = (businessServiceGroup) ? constructBreadcrumb(data, businessServiceGroup) : null;
    const buckets = (businessServiceGroup) ?
        (!_isEmpty(breadcrumb) ? constructServiceGroupsBuckets(populationMapper, breadcrumb.selectedNodeRef) : null): null;
    const tree = constructServiceGroupsTree(severities, data);

    const servicesCount = getNumOfUniqueServices(buckets);

    const newState = {
        ...state,
        properties: {
            ...state.properties,
            serviceGroups: {
                ...state.properties.serviceGroups,
                servicesCount,
                tree,
                breadcrumb,
                buckets
            }
        }
    };

    getAfterStateUpdate().then(dispatch => {
        LOGGING.log(`getServiceGroupsTreeHandler->createServiceGroupsTreeModel->getAfterStateUpdate`);
    });

    return newState;
});

/**
 * Update the noc_user table -> tilemapsFormat section
 * expect to get in payload :{
 *     item: {
 *         id
 *     }
 * }
 * @type {{effect(*): void}}
 */
const updateUserSelectedServiceGroup = {
    effect(coeffects) {
        const {dispatch, action, state} = coeffects;
        LOGGING.log(`getServiceGroupsTreeHandler->updateUserSelectedServiceGroup called by action ${action.type}`);

        const {tilemapsFormat} = state.properties;
        const {item: {id} = {}} = action.payload;
        if (!_isString(id))
            throw new Error(`updateUserSelectedServiceGroup expected to get an object {item: {id :''}} got->${action.payload} `);

        //Id can be empty string, in case of reset Service Group and moving back to regular services
        //if (tilemapsFormat && newServiceGroupId !== undefined && newServiceGroupId !== null) {
        if (tilemapsFormat) {
            const newTilemapsFormat = {
                tilemapsFormat: {
                    ...tilemapsFormat,
                    businessServiceGroup: id
                }
            };

            dispatch(CONSTANTS.ACTION_TYPES.TILEMAPS_FORMAT_SAVE_REQUESTED, newTilemapsFormat);//Update noc-user table
        }
    }
};

const reduceService = (servicesIds, service) => {
    servicesIds.add(service.id);
    return servicesIds;
};

const reduceBucket = (servicesIds, bucket) => bucket.population.reduce(reduceService, servicesIds);

const getNumOfUniqueServices = (buckets) => {
    if (buckets === null || _isUndefined(buckets))
        return 0;
    const servicesIds = buckets.reduce(reduceBucket, new Set());
    return servicesIds.size;
};

const effectHandlers = {
    getServiceGroupsTreeHandler,
    getServiceGroupsTreeRequest,
    handleGetServiceGroupsTreeResponse,
    createServiceGroupsTreeModel,
    updateUserSelectedServiceGroup
};

export default {
    ACTION_TYPES,
    effectHandlers
};