import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {checkForError, LOGGING, onNextFrame} from '@devsnc/library-opws-mapping-shared';
import {get as _get} from 'lodash';
import VIEW_CONSTANTS from './constants';
import {userViewsUtils} from './userViewsUtils';

const userViewNamesFetched = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_noc_user_view_names, {
	startActionType: VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_NAMES_REQUEST_STARTED,
	successActionType: VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_NAMES_REQUEST_SUCCEEDED,
	errorActionType: VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_NAMES_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const requestUserViewNamesHandler = async coeffects => {
	LOGGING.info('Received requestUserViewNamesHandler', {caller: 'getUserViewNamesEffect'});

	const {
		properties,
		action: {payload},
		dispatch
	} = coeffects;

	if (checkForError({payload, dispatch, caller: 'requestUserViewNamesHandler'}))
		return;

	const userId = _get(properties, 'userMeta.userId', null);
	const {nocConfig} = properties;
	// const {workspace} = properties;
	//temporary removing workspace for WS integration
	const workspace = 'nocpit';

	if (!userId || !workspace || !nocConfig)
		return;

	const variables = getVariables(workspace, userId);

	await onNextFrame();

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_NAMES_REQUEST, variables);
};


const RESPONSE_RESULT_PATH = 'data.GlideRecord_Query.noc_user_view._results';
const handleUserViewNamesResponse = coeffects => {
	const caller = 'getUserViewNamesEffect.handleUserViewNamesResponse';
	LOGGING.info('Received handleUserViewNamesResponse', {caller});

	const {
		state,
		updateProperties,
		action: {payload},
		dispatch
	} = coeffects;

	if (checkForError({payload, dispatch, caller}))
		return;

	const nocViews = _get(payload, RESPONSE_RESULT_PATH);
	LOGGING.debug(`Received views`, {extra: nocViews, caller});
	const originViewId = _get(state, 'userViewResource.originViewId') || '';

	const views = userViewsUtils(nocViews, originViewId);

	updateProperties({views});
};

const getUserViewNamesFailed = coeffects => {
	LOGGING.error('Received getUserViewNamesFailed', {caller: 'getUserViewNamesEffect.getUserViewNamesFailed'});
};

const effectHandlers = {
	userViewNamesFetched,
	requestUserViewNamesHandler,
	handleUserViewNamesResponse,
	getUserViewNamesFailed
};

/**
 * Can get specific view or all view of the user (including the empty ones)
 * @param workspace
 * @param userId
 * @param viewName
 * @returns {{queryConditions: *}}
 */
const getVariables = (workspace, userId, viewName = null) => {
	let q = `user_id=${userId}^ORuser_idISEMPTY^workspace=${workspace}`;

	if (viewName)
		q += `^view_name=${viewName}`;

	return {
		queryConditions: q
	};
};

export default {
	effectHandlers,
	getVariables
};
