import {filter as _filter, get as _get, map as _map} from 'lodash';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {CONSTANTS, checkForError, LOGGING, onNextFrame} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	SEVERITY_CHOICE_REQUEST: 'SEVERITY_CHOICE_REQUEST',
	SEVERITY_CHOICE_REQUEST_STARTED: 'SEVERITY_CHOICE_REQUEST_STARTED',
	SEVERITY_CHOICE_REQUEST_SUCCEEDED: 'SEVERITY_CHOICE_REQUEST_SUCCEEDED',
	SEVERITY_CHOICE_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
	query getSeverityChoices($tableName: String!, $fieldName: String!) {
	  GlideLayout_Query {  
		choiceDataRetriever(tableName: $tableName, fieldName: $fieldName) {
		  choice {
			value
			label
		  }
		}
	  }
	}
`;

const handleSeverityChoicesFetched = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.SEVERITY_CHOICE_REQUEST_STARTED,
	successActionType: ACTION_TYPES.SEVERITY_CHOICE_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.SEVERITY_CHOICE_REQUEST_FAILED,
	variableList: ['tableName', 'fieldName']
});

const transform = result => {
	//remove from the result the choices: 'None' and 'Clear'
	//transform of population query takes care to eliminate 'Clear' severity and display it as 'Info'
	let transformed = _filter(result, s => s.value !== '' && s.value !== '0');

	transformed = _map(transformed,
		(s, idx) => ({
			value: s.value,
			label: s.label,
			sequence: idx
		}));

	return transformed;
};

const handleSeverityChoicesResponded = async coeffects => {
	LOGGING.log('handleSeverityChoicesResponded');

	const {
		dispatch,
		updateState,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleSeverityChoicesResponded'});
	if (errors)
		return;

	const records = _get(payload, 'data.GlideLayout_Query.choiceDataRetriever.choice', []);
	const groups = transform(records);

	updateState({
		severityChoicesResource: {
			result: groups
		}
	});

	await onNextFrame();
	dispatch(CONSTANTS.ACTION_TYPES.NOC_SEVERITY_CHOICES_SET, {});
};

const getVariables = () => {
	return {
		tableName: 'cmdb_ci_service_auto',
		fieldName: 'severity'
	};
};

const effectHandlers = {
	handleSeverityChoicesFetched,
	handleSeverityChoicesResponded
};

export default {
	ACTION_TYPES,
	getVariables,
	effectHandlers
};

