import {forIn as _forIn} from 'lodash';
import {comparatorByField} from '../comparators/ordinalComparator';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';

/**
 *
 * @param groupedPopulation
 * @param state
 * @returns {*}
 */
export const sortByPopulation = (groupedPopulation, state) => {
	const {properties: {tilemapsFormat}} = state;
    const {businessServiceGroup} = tilemapsFormat;
    const groupBy = (businessServiceGroup) ? null : tilemapsFormat.groupByField;

	const sortByField = (tilemapsFormat.sortByField === groupBy) ?
		CONSTANTS.DEFAULTS.NOC_FALLBACK_SORT_FIELD : tilemapsFormat.sortByField;

	return runSortByStrategy(groupedPopulation, sortByField,
		tilemapsFormat.sortByOrder === CONSTANTS.GLOBALS.ASC);
};

/**
 * Return the array sorted by sortByField
 *
 * @param population
 * @param sortByField
 * @param ascending
 * @returns {*}
 */
const runSortByStrategy = (groupedPopulation, sortByField, ascending) => {
	const newGroupedPopulation = groupedPopulation;

	// sort each group internally
	if (sortByField) {
		_forIn(newGroupedPopulation, group => {
			group.population = sortGroup(group, sortByField, ascending);
		});
	}

	return newGroupedPopulation;
};


/**
 *
 * @param group
 * @param sortByField
 * @param ascending
 * @returns {Array}
 */
const sortGroup = (group, sortByField, ascending) => {
	const {population} = group;

	let sortedPopulation = population.sort(comparatorByField(sortByField, ascending));

	return sortedPopulation;
};
