import severityChoicesEffect from './severityChoicesEffect';
import prefilterPopulationEffect from './prefilterPopulationEffect';
import {CONSTANTS, LOGGING, createActionConfig, colorsAndIconsEffect} from '@devsnc/library-opws-mapping-shared';

const requestInitialSeverityDataHandler = {
	effect(coeffects) {
		const {dispatch} = coeffects;
		
		LOGGING.log('dispatch COLORS_REQUEST');
		dispatch(colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST,
			colorsAndIconsEffect.getVariables(['severity']));

		LOGGING.log('dispatch SEVERITY_CHOICE_REQUEST');
		dispatch(severityChoicesEffect.ACTION_TYPES.SEVERITY_CHOICE_REQUEST,
			severityChoicesEffect.getVariables());
	}
};

export const nocSeverityDataBehavior = {
	name: 'nocSeverityDataBehavior',
	actionHandlers: {
		[CONSTANTS.ACTION_TYPES.NOC_CONFIG_SET_PHASE_2]:
			createActionConfig(requestInitialSeverityDataHandler),

		[colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST]:
			createActionConfig(colorsAndIconsEffect.effectHandlers.colorsFetchedHandler),
	
		[colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST_SUCCEEDED]:
			createActionConfig(colorsAndIconsEffect.effectHandlers.handleColorsResponse),

		[severityChoicesEffect.ACTION_TYPES.SEVERITY_CHOICE_REQUEST]:
			createActionConfig(severityChoicesEffect.effectHandlers.handleSeverityChoicesFetched),
		
		[severityChoicesEffect.ACTION_TYPES.SEVERITY_CHOICE_REQUEST_SUCCEEDED]:
			createActionConfig(severityChoicesEffect.effectHandlers.handleSeverityChoicesResponded),
			
//		[severityChoicesEffect.ACTION_TYPES.SEVERITY_CHOICE_REQUEST_FAILED]:
//			createActionConfig(severityChoicesEffect.effectHandlers.handleSeverityChoicesResponded),

		[CONSTANTS.ACTION_TYPES.NOC_POPULATION_SET_PHASE_2]:
			createActionConfig(prefilterPopulationEffect.effectHandlers.prefilterPopulationHandler),

		[CONSTANTS.ACTION_TYPES.NOC_COLORS_AND_ICONS_SET_PHASE_2]:
			createActionConfig(prefilterPopulationEffect.effectHandlers.prefilterPopulationHandler),

		[CONSTANTS.ACTION_TYPES.NOC_SEVERITY_CHOICES_SET]:
			createActionConfig(prefilterPopulationEffect.effectHandlers.prefilterPopulationHandler),

		[CONSTANTS.ACTION_TYPES.SEVERITY_FILTER_SET]:
            createActionConfig(prefilterPopulationEffect.effectHandlers.prefilterPopulationHandler)
	}
};
