import {
	forOwn as _forOwn,
	get as _get,
	keys as _keys,
	isUndefined as _isUndefined,
	reduce as _reduce
} from 'lodash';

// TODO Arik: Remove all recordHasSection() functions, useless if we expect default properties
export const SECTIONS = {
	uberFilter: {
		/**
		 * Returns true if given gql record has specified section in it
		 * @param record
		 */
		recordHasSection(record) {
			return !_isUndefined(_get(record, 'filter.value'));
		},

		/**
		 * Transform record (gql response) into client model representation.
		 * @param record response from gql request.
		 */
		recordToModel(record) {
			if (!record || !this.recordHasSection(record))
				return {};

			const uberFilter = {
				filter: _get(record, 'filter.value') || ''
			};

			return {
				uberFilter
			};
		},


		/**
		 * Transform client model representation into qgl mutation vars
		 * @param model client model representation of relevant portion of user view.
		 */
		modelToMutationVar(model) {
			if (!model)
				return {};

			const {filter} = model;

			const raw = {
				filter
			};

			return raw;
		}
	},
	severityFilter: {
		recordHasSection(record) {
			return !_isUndefined(_get(record, 'enabled_severities.value'));
		},

		recordToModel(record) {
			if (!record || !this.recordHasSection(record))
				return {};

			const severityFilter = {
				enabledSeverities: {}
			};

			const sevKeys = _get(record, 'enabled_severities.value') || '';

			//enabledSeverities transformed to map {severityValue -> true}
			_forOwn(sevKeys.split(','), sev => {
				severityFilter.enabledSeverities[sev] = true;
			});

			return {severityFilter};
		},

		modelToMutationVar(model) {
			if (!model)
				return {};

			const {enabledSeverities} = model;
			let enabledSeveritiesArr = _keys(enabledSeverities);
			enabledSeveritiesArr = enabledSeveritiesArr.filter(val => enabledSeverities[val]).sort();

			const raw = {
				enabled_severities: enabledSeveritiesArr.join(',')
			};

			return raw;
		}

	},
	tilemapsFormat: {
		recordHasSection(record) {
			return !_isUndefined(_get(record, 'sort_by_field.value'));
		},

		recordToModel(record) {
			if (!record || !this.recordHasSection(record))
				return {};

			const tilemapsFormat = {
				sortByField: _get(record, 'sort_by_field.value') || '',
				sortByOrder: _get(record, 'sort_by_order.value') || '',
				groupByField: _get(record, 'group_by_field.value') || '',
				groupByOrder: _get(record, 'group_by_order.value') || '',
				businessServiceGroup: _get(record, 'business_service_group.value') || '',
				tileSize: _get(record, 'tile_size.value') || ''
			};
			return {tilemapsFormat};
		},

		modelToMutationVar(model) {
			if (!model)
				return {};

			const raw = {
				sort_by_field: model.sortByField,
				sort_by_order: model.sortByOrder,
				group_by_field: model.groupByField,
				group_by_order: model.groupByOrder,
				business_service_group: model.businessServiceGroup,
				tile_size: model.tileSize
			};

			return raw;
		}

	},
	alertPreferences: {
		recordHasSection(record) {
			return !_isUndefined(_get(record, 'alerts_correlated_view.value'));
		},

		recordToModel(record) {
			if (!record || !this.recordHasSection(record))
				return {};

			const alertPreferences = {
				isAlertsCorrelatedView: _get(record, 'alerts_correlated_view.value') || false,
				defaultAlertsSorting: _get(record, 'alerts_panel_default_sorting.value') || ''
			};

			return {alertPreferences};
		},

		modelToMutationVar(model) {
			if (!model)
				return {};

			const raw = {
				alerts_correlated_view: model.isAlertsCorrelatedView,
				alerts_panel_default_sorting: model.defaultAlertsSorting
			};

			return raw;
		}
	}
};

const reduceRecordToModel = (input, section) => {
	const {record, result} = input;
	return {
		record,
		result: {
			...result,
			...section.recordToModel(record)
		}
	};
};

export const transformToModel = record => {
	const reduced = _reduce(SECTIONS, reduceRecordToModel, {
		record,
		result: {}
	});

	return reduced.result;
};

const reduceModelToMutationVars = (input, section, sectionName) => {
	const {model, result} = input;
	const modelSection = model[sectionName];
	return {
		model,
		result: {
			...result,
			...section.modelToMutationVar(modelSection)
		}
	};
};

export const getMutationVars = (workspace, viewName, userId, sysId, originViewId, model) => {
	const reduced = _reduce(SECTIONS, reduceModelToMutationVars, {
		model,
		result: {
			workspace,
			sys_id: sysId,
			origin_view_id: originViewId,
			view_name: viewName,
			user_id: userId
		}
	});

	return reduced.result;
};

export const transformRecordToMutationVars = (workspace, viewName, userId, sysId, originViewId, record) => {
	const model = transformToModel(record);
	return getMutationVars(workspace, viewName, userId, sysId, originViewId, model);
};
