import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import {CONSTANTS, createActionConfig} from '@devsnc/library-opws-mapping-shared';
import {view} from './view';
import {transformState} from './transformState';
import styles from './nocSeverityIndicatorBar.scss';
import nocSeverityIndicatorEffect from './nocSeverityIndicatorBarEffect';

/**
 * Create custom element
 */
createNocElement('noc-severity-indicators-bar', {
	view,
	transformState,
	styles,
	properties: {
		severities: {
			default: null
		},
		population: {
			default: []
		},
		severityFilter: {
			default: null
		},
	},
	actionHandlers: {
		[nocSeverityIndicatorEffect.ACTION_TYPES.NOC_SEVERITY_INDICATOR_CLICKED]:
            createActionConfig(nocSeverityIndicatorEffect.effectHandlers.filterSeverities)
	}
});
