import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {fitPopoverOnInsert} from './fitPreviewPopover';
import {nocServicePreviewBehaviors} from '../noc-service-preview-behaviors';
import '../noc-popover';
import '../noc-service-preview';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';

export const ARIA_PREVIEW_POPOVER_ID = 'noc_preview_popover';

/**
 * Creating popover with service preview with given servicePreviewData
 * @param popoverConfig contains isOpen flag and popover coordinates
 * @param servicePreviewData data to provide for service preview.
 * @returns {*}
 */
export const generatePreviewPopover = args => {
	const {
		dispatch,
		popover,
		servicePreviewData,
		servicePreviewId,
		popoverKey,
		popoverTargetRef,
		popoverContainerRef
	} = args;

	const popoverContentRef = createRef();
	const {positionStyle = {}} = popover;
	
	const fitPopoverArgs = {
		dispatch,
		id: popoverKey,
		popoverTargetRef,
		popoverContentRef,
		popoverContainerRef,
		currPositionStyle: positionStyle
	};

	const workspace = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/workspace`;
	return (
		<noc-popover
			key={popoverKey}
			onCloseAction={nocServicePreviewBehaviors.ACTION_TYPES.NOC_CLEAR_SERVICE_PREVIEW}
			positionStyle={positionStyle}
			id={ARIA_PREVIEW_POPOVER_ID}
			hook-insert={fitPopoverOnInsert(fitPopoverArgs)}>

			<noc-service-preview
				tabindex='0'
				workspace={workspace}
				servicePreviewData={servicePreviewData}
				ref={popoverContentRef}/>
				
		</noc-popover>
	);
};