import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import nocChangeEffect from '../noc-changes-notificator-behavior/nocChangeEffect';

const ACTION_TYPES = {
	NOC_VISIBILITY_CHANGED: 'NOC_VISIBLE_CHANGED'
};

const handleVisibilityChangeEffect = ({dispatch, action}) => {
	const {payload: {isVisible}} = action;

	LOGGING.info(`Setting NOCpit visibility to ${isVisible}`, {caller: 'handleVisibilityChangeEffect'});

	dispatch(isVisible ? nocChangeEffect.ACTIONS_TYPES.NOC_ON_START_CHANGE_TIMER : nocChangeEffect.ACTIONS_TYPES.NOC_ON_STOP_CHANGE_TIMER);
};

const effectHandlers = {
	handleVisibilityChangeEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};