import nocChangeEffect from './nocChangeEffect';
import nocOnChangeEffect from './nocOnChangeEffect';
import {createActionConfig, CHANGE_EVENT_CONSTANTS} from '@devsnc/library-opws-mapping-shared';

const {GLOBAL_ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;

export const nocChangesNotificatorBehavior = {
	name: 'nocChangesNotificatorBehavior',
	actionHandlers: {
		[CHANGE_EVENT_CONSTANTS.GLOBAL_ACTION_TYPES.NOC_ON_START_REPEATED_CHANGES]:
			createActionConfig(nocChangeEffect.effectHandlers.REQUEST_REPEATED_CHANGE_NOTIFICATION),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_START_CHANGES]:
			createActionConfig(nocChangeEffect.effectHandlers.REQUEST_CHANGE_NOTIFICATION),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_READ_CHANGES]: 
			createActionConfig(nocChangeEffect.effectHandlers.REQUEST_CHANGE_NOTIFICATION_EFFECT_HANDLER),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_READ_CHANGES_QUERY]: 
			createActionConfig(nocChangeEffect.effectHandlers.CHANGE_RESPONSE_REQUEST_SENT),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_READ_CHANGES_SUCCESS]: 
			createActionConfig(nocChangeEffect.effectHandlers.CHANGE_RESPONSE_SUCCESS),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_READ_CHANGES_ERROR]: 
			createActionConfig(nocChangeEffect.effectHandlers.CHANGE_RESPONSE_ERROR),

		[GLOBAL_ACTION_TYPES.NOC_EVENT_HASH_CHANGED]: 
			createActionConfig(nocOnChangeEffect.effectHandlers.nocOnChangeEffect),

		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_STOP_CHANGE_TIMER]:
			createActionConfig(nocChangeEffect.effectHandlers.REQUEST_STOP_CHANGE_TIMER),
		[nocChangeEffect.ACTIONS_TYPES.NOC_ON_START_CHANGE_TIMER]:
			createActionConfig(nocChangeEffect.effectHandlers.REQUEST_START_CHANGE_TIMER)
	}
};