import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, LOGGING, checkForError} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	ADD_TO_FAVORITES_REQUEST: 'ADD_TO_FAVORITES_REQUEST',
	ADD_TO_FAVORITES_REQUEST_STARTED: 'ADD_TO_FAVORITES_REQUEST_STARTED',
	ADD_TO_FAVORITES_REQUEST_SUCCEEDED: 'ADD_TO_FAVORITES_REQUEST_SUCCEEDED',
	ADD_TO_FAVORITES_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};




const addToFavoritesRequest = createHttpEffect('/api/sn_nocpit/nocpit/AddToFavorites', {
	method: 'PUT',
	queryParams: ['workspace', 'ciId'],
	startActionType: ACTION_TYPES.ADD_TO_FAVORITES_REQUEST_STARTED,
	successActionType: ACTION_TYPES.ADD_TO_FAVORITES_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.ADD_TO_FAVORITES_REQUEST_FAILED
});


const addToFavoritesResponse = coeffects => {
	LOGGING.log('addToFavoritesResponse');

	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'addToFavoritesResponse'});
	if (errors)
		return;

	const {favorite} = payload.result;

	updateProperties({
		path: 'servicePreviewData.favorite',
		operation: 'set',
		value: favorite
	});
};


const effectHandlers = {
	addToFavoritesRequest,
	addToFavoritesResponse
};


export default {
	ACTION_TYPES,
	effectHandlers
};
