import {CONSTANTS, LOGGING, onNextFrame, getValue} from '@devsnc/library-opws-mapping-shared';
import {get as _get, keyBy as _keyBy} from 'lodash';

const ACTION_TYPES = {
};

/**
 * Filter iterator function to pass only services of enabled severities.
 * @param it currently iterated population unit (a.k.a. service object)
 * 'this' contains mapping of severities and mapping of enabledGroups
 * @returns {Boolean}
 */
//Keep it 'function', don't change to arrow. Array.filter expects 'this' context.
const enabledPopulationCounterAndFilter = function(it) {
	const severityValue = getValue(it.severity);
	const {severities, enabledGroups} = this;
	const sev = severities[severityValue];

	if (!sev) {
	    it.isEnabled = false;
	    return false;
	}

    const severityState = enabledGroups[severityValue];
    it.isEnabled = Boolean(severityState);
	++sev.populationSize;
	return enabledGroups[severityValue];
};

//Keep it 'function', don't change to arrow. Array.map expects 'this' context.
const groupsMapper = function(gr) {
	const {colorsAndIcons, enabledGroups} = this;

	const ctx = colorsAndIcons[gr.value] || colorsAndIcons[0]; //value '0' used as default fallback
	const isEnabled = enabledGroups[gr.value] || false;

	return {
		...gr,
		populationSize: 0,
		color: ctx.colorCode,
		iconUrl: ctx.iconUrl,
		isEnabled
	};
};

const filterPopulation = (population, groups, enabledGroups, colorsAndIcons) => {
	const severitiesArr = groups.map(groupsMapper, {colorsAndIcons, enabledGroups});

	const severities = _keyBy(severitiesArr, 'value');
	const enabledPopulation = population.filter(enabledPopulationCounterAndFilter, {severities, enabledGroups});

	return {
		severities,
		enabledPopulation
	};
};

const prefilterPopulationHandler = async coeffects => {
	const {
	    state,
        properties,
        action,
        dispatch,
        updateProperties
    } = coeffects;

    LOGGING.log(`prefilterPopulationHandler called by Action '${action.type}'`);

	const {population} = properties;
	const groups = _get(state, "severityChoicesResource.result", null);
	const colorsAndIcons = _get(state, "colorsAndIconsResource.result", null);

	if (!population || !groups || !colorsAndIcons)
		return;

	const enabledGroups = _get(properties, 'severityFilter.enabledSeverities', {});
	const {
		severities,
		enabledPopulation
	} = filterPopulation(population, groups, enabledGroups, colorsAndIcons);

	LOGGING.log('prepareGroupsHandler applies.');

    updateProperties({
        severities,
        enabledPopulation
    });

    await onNextFrame();
    dispatch(CONSTANTS.ACTION_TYPES.NOC_POPULATION_GROUPS_SET, {});
};

const effectHandlers = {
	prefilterPopulationHandler
};

export default {
	ACTION_TYPES,
	effectHandlers
};
