import {createHttpEffect} from '@servicenow/ui-effect-http';
import {checkForError, CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';

/**
 * This is the table which is used for taking the filter conditions from,
 * Currently hardcoded to the default table, should be tabken from noc_user_view table property
 * @type {string}
 */
const QUERY_PARSE_TABLE = 'cmdb_ci_service_auto';

export const ACTION_TYPES = {
	QUERY_PARSE_REQUEST_REQUESTED: 'QUERY_PARSE_REQUEST_REQUESTED',
	QUERY_PARSE_REQUEST_STARTED: 'QUERY_PARSE_REQUEST_STARTED',
	QUERY_PARSE_REQUEST_SUCCEEDED: 'QUERY_PARSE_REQUEST_SUCCEEDED',
	QUERY_PARSE_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

export const getQueryParseEffectRequest = createHttpEffect('/api/now/ui/query_parse/' + QUERY_PARSE_TABLE, {
	method: 'GET',
	queryParams: ['sysparm_query'],
	startActionType: ACTION_TYPES.QUERY_PARSE_REQUEST_STARTED,
	successActionType: ACTION_TYPES.QUERY_PARSE_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.QUERY_PARSE_REQUEST_FAILED
});

/**
 *
 * @param payload
 * @returns {*}
 */
const parseQueryResponse = (payload) => {
	if (!payload) {
		return 0;
	}

	const {parsed_query = []} = payload.result;
	return parsed_query.flat().length;
};

export const handleQueryParseResponse = coeffects => {
	const caller = 'handleQueryParseResponse';
	LOGGING.info('Received response', {caller});

	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller});
	if (errors) {
		return;
	}

	const filterSize = parseQueryResponse(payload);
	updateProperties({filterSize});
};

export const effectHandlers = {
	getQueryParseEffectRequest,
	handleQueryParseResponse
};
