import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import {nocTilemapDataBehavior} from '../noc-tilemap-data-behavior';
import {nocPopoverBehavior} from '../noc-popover-behavior';
import {nocServicePreviewBehaviors} from '../noc-service-preview-behaviors';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {transformState} from './transformState';

import {view} from './view.js';
import styles from './nocTilemapContainer.scss';
import resizeBehavior from "@servicenow/behavior-resize";
import {nocTilemapSelectGroupBehavior} from '../noc-tilemap-data-behavior/nocTilemapSelectGroupBehavior';
import nocTilemapSelectGroupEffect from '../noc-tilemap-data-behavior/nocTilemapSelectGroupEffect';

createNocElement('noc-tilemap-container', {
    view,
    transformState,
    onBootstrap(host, dispatch) {
        dispatch(CONSTANTS.ACTION_TYPES.NOC_COMPONENT_CONNECTED, {}, {stopPropagation: true});
    },

    styles,

    properties: {
        nocAppRef: {
            default: null
        },

        //current timestamp of changes manager.
        timestamp: {
            default: null,

            //when STATUS timestamp changes, request servicePreview refresh (the effect will refresh only if relevant)
            onChange(value, prevValue, dispatch) {
                if (value !== prevValue && !isNaN(parseInt(value)))
                    dispatch(CONSTANTS.ACTION_TYPES.REFRESH_SERVICE_PREVIEW_REQUESTED, {timestamp: value});
            }
        },

        /**
         {groupByField: "cost"
         groupByOrder: "asc"
         maxColumnsInRow: 3
         maxItemsInColumn: 50
         sortByField: "location"
         sortByOrder: "asc"}
         */
        tilemapsFormat: {
            default: null,
            onChange(value, prevValue, dispatch) {
                //In case changing from regular services into service group we need to reset the drill down if exists
                const oldVal = (prevValue && prevValue.businessServiceGroup) ? prevValue.businessServiceGroup : null;
                const newVal = (value && value.businessServiceGroup) ? value.businessServiceGroup : null;
                if (newVal && (newVal !== oldVal)) {
                    //Reset the drill down since  Service Group Exists
                    dispatch(nocTilemapSelectGroupEffect.ACTION_TYPES.TILEMAP_SELECT_REGULAR_GROUP, {name: ''});
                }
            }
        },

        /**
         {groupByFields: (4) [{}, {}, {}, {}]
         hoverTooltipFields: (2) [{}, {}]
         searchFields: [{}]
         sortByFields: (4) [{}, {}, {}, {}]}
         **/
        tilemapsConfig: {
            selectable: true,
            default: {}
        },

        /***
         * tree,
         * breadcrumb,
         * buckets
         */
        serviceGroups: {
            default: null
        },

        population: {
            default: null
        },

        severities: {
            default: null
        },

        categorizedModel: {
            selectable: true,
            default: null
        },

        choicesFieldsModel: {
            selectable: true,
            default: null
        },

        servicePreviewData: {
            selectable: true,
            default: null
        },

        popover: {
            selectable: true,
            default: {}
        },

        searchTerm: {
            selectable: true,
            default: ''
        },

        workspace: {
            //bringing workspace directly from parent. Cannot listen on selectable, it's not resolved onBootstrap().
            default: CONSTANTS.DEFAULTS.WORKSPACE
        },

        impactedServicesAlertIds: {
            default: null
        },

        isImpactedServicesLoading: {
            default: false
        },

        selectedAlert: {
            default: null
        },
    },
    behaviors: [
        {behavior: resizeBehavior, options: {timer: {wait: 1000}}},
        nocTilemapDataBehavior.behavior,
        nocPopoverBehavior.behavior,
        nocServicePreviewBehaviors.behaviors.nocServicePreviewBehavior,
        nocTilemapSelectGroupBehavior
    ]
});