import {isObject as _isObject, values as _values, sortBy as _sortBy} from 'lodash';

const sortSeverities = severities => _sortBy(_values(severities), 'sequence');

export const transformState = state => {
	const {
		severities, 
		population = {length: 0}
	} = state.properties;

	if (!_isObject(severities))
		return state;
	const stateView = {
		sortedSeverities: sortSeverities(severities),
		populationSize: population.length
	};

	return stateView;
};