import '../sn-noc-severity-indicator';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {t} from 'sn-translate';

/**
 * Keep function, `this` context is in use by Array.map().
 * @param {*} sev
 */
const buildIndicator = function(sev) {

	const {populationSize} = this;

	const key = `indicator_toggle_${sev.value}`;

	const groupSz = sev.populationSize;

	const percentage = (!groupSz || !populationSize) ? '0' : (100 * (groupSz / populationSize)).toFixed(1);

	return (<noc-severity-indicator
		key={key}
		severity={sev}
		count={groupSz}
		percentage={percentage}/>);
};

const buildIndicatorsJsx = (sortedSeverities, populationSize) => {
	return sortedSeverities.map(buildIndicator, {populationSize});
};

const BAR_LABEL = t('Breakdown by severities');

export const view = (state, dispatch) => {
	const {
		sortedSeverities = [],
		populationSize
	} = state;

	const indicators = buildIndicatorsJsx(sortedSeverities, populationSize);

	return (
		<Fragment>
			<fieldset  aria-label={BAR_LABEL}>
				{indicators}
			</fieldset>
		</Fragment>
	);
};
