import {CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';

/**
 *
 * @param coeffects
 */
const openManageViewsEffect = coeffects => {
	const {
		dispatch,
		properties: {views},
	} = coeffects;

	const {defaultViewSysId: sys_id} = views;
	const table = 'noc_user_view';

	LOGGING.info(`Open form: record '${sys_id}' on table '${table}'`);

	dispatch(CONSTANTS.ACTION_TYPES.ITEM_SELECTED, {
		table,
		sys_id,
		parent_table: table,
		parent_sys_id: sys_id
	});
};

const effectHandlers = {
	openManageViewsEffect
};

export default {
	effectHandlers
};
