/**
 * Effect handler for search tile map according to search term provided by the user.
 */
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
		SEARCH_TERM_CHANGED: CONSTANTS.ACTION_TYPES.SEARCH_TERM_CHANGED
};

const handleNocSearchTerm = ({updateProperties, action}) => {
	const {payload: {value}} = action;
	LOGGING.info('handleNocSearchTerm', {extra: value});
	updateProperties({searchTerm: value});
};

/**
 * list of effects handled in this module
 *
 */
const effectHandlers = {
	handleNocSearchTerm
};


export default {
	ACTION_TYPES,
	effectHandlers
};
