import '../noc-tilemap-toolbar';
import '../sn-noc-pills-bar';
import '../sn-noc-tilemap-layout';
import '@devsnc/library-opws-mapping-shared/src/components/noc-search';
import '@servicenow/now-heading';
import '@devsnc/library-opws-mapping-shared/src/components/sn-opws-loader';
import {t} from 'sn-translate';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {i18n} from '@devsnc/library-opws-mapping-shared';
import {cloneDeep as _cloneDeep} from 'lodash';

const LABEL_SEARCH = t('Search');
const DESC_SEARCH = t('Filter population by a keyword');
const CLEAR_SEARCH = t('Clear search query');

const BREADCRUMB_HOME = CONSTANTS.TEXT.HOME;
const EMPTY_ICON_PATH = '/images/sa.serviceTree/sn-es-no-records.svg';
const NO_SERVICES_TO_DISPLAY_MSG = t('No services to display');
const TRY_SOMETHING_ELSE_MSG = t('Try filtering by something less specific');

const createBreadcrumbJsx = state => {
	const {selectedGroup, properties} = state;
	const {tilemapsFormat: {businessServiceGroup}, serviceGroups} = properties;

	const isServiceGroupMode = businessServiceGroup && serviceGroups && serviceGroups.breadcrumb; //Check that we have
																								  // indeed sys_id &&
																								  // serviceGroups
	const breadcrumb = (isServiceGroupMode) ? _cloneDeep(serviceGroups.breadcrumb) : {items: [{name: BREADCRUMB_HOME}]};

	if (selectedGroup) {
		breadcrumb.items.push({id: selectedGroup, name: selectedGroup, type: 'group'});
	}


	return <noc-breadcrumb
		key='noc_breadcrumb_key'
		component-id={CONSTANTS.DEFAULTS.NOC_BREADCRUMB_COMPONENT_COMPONENT_ID}
		data={breadcrumb}
		className='noc-breadcrumb-container'/>;
};

const canLoadTilemap = (choicesFieldsModel, groupedPopulation, windowSize) => {
	return (choicesFieldsModel && groupedPopulation && windowSize.width);
};

const getEmptySection = () => {
	return <section className='header-no-data'>
		<div className="noc-empty-state">
            <span className='noc-tilemap-empty-icon'>
                <img src={EMPTY_ICON_PATH} alt='sn-empty-state'/>
            </span>
			<div className="noc-empty-state-row -behind"/>
			<now-heading className="noc-empty-state-row-header"
						 label={NO_SERVICES_TO_DISPLAY_MSG}
						 variant='header-tertiary'
						 level="1"/>

			<div className="noc-empty-state-row">
				{TRY_SOMETHING_ELSE_MSG}
			</div>
		</div>
	</section>;
};

const getLoaderSection = (debounceTime = 300) => {
	return <sn-opws-loader
		debounce-time={debounceTime}
		className='sn-opws-loader'
		component-id='tilemap-loader'
		label={i18n.NOC_LOADER_LABEL_SERVICES}/>;
};

const getTilemap = (state) => {
	const {
		servicePreviewResource,
		groupedPopulation = [],
		properties: {
			nocAppRef,
			tilemapsFormat,
			tilemapsConfig: {layoutSegmentsEquallyDivided},
			choicesFieldsModel,
			popover
		},
		selectedGroup,
		dynamicLayoutConfig
	} = state;

	const windowSize = state.behaviors.resize.size;
	return <sn-noc-tilemap-layout
		className='tilemap-layout now-p-block-start--xl'
		nocAppRef={nocAppRef}
		dynamic-layout-config={dynamicLayoutConfig}
		groups={groupedPopulation}
		can-segments-divided-equally={layoutSegmentsEquallyDivided}
		selectedGroup={selectedGroup}
		servicePreviewResource={servicePreviewResource}
		popover={popover}
		windowSize={windowSize}
		tilemapsFormat={tilemapsFormat}
		choices-fields-model={choicesFieldsModel}
		key={CONSTANTS.DEFAULTS.NOC_TILEMAP_LAYOUT_COMPONENT_ID}
		component-id={CONSTANTS.DEFAULTS.NOC_TILEMAP_LAYOUT_COMPONENT_ID}/>;
};

const createTilemap = (state) => {
	const {
		groupedPopulation,
		properties: {choicesFieldsModel, isImpactedServicesLoading}
	} = state;
	const windowSize = state.behaviors.resize.size;

	// if we are going to show the spinner due to impact services loading, don't debounce
	if (isImpactedServicesLoading) {
		return getLoaderSection(0);
	}

	if (!canLoadTilemap(choicesFieldsModel, groupedPopulation, windowSize)) {
		return getLoaderSection();
	}
	
	return groupedPopulation.length > 0 ? getTilemap(state) : getEmptySection();
};

export const view = (state, dispatch) => {
	const {
		properties: {
			tilemapsFormat,
			tilemapsConfig: {groupByFields, sortByFields},
		},
		selectedGroup,
		dynamicLayoutConfig
	} = state;

	if (!tilemapsFormat || !groupByFields || !sortByFields || !dynamicLayoutConfig) {
		return null;
	}

	const searchTerm = `@${CONSTANTS.DEFAULTS.NOC_TILEMAP_CONTAINER_COMPONENT_ID}/searchTerm`;
	const breadcrumb = createBreadcrumbJsx(state);
	const nocSearch = <noc-search className='search' clearSearchLabel={CLEAR_SEARCH} searchDescription={DESC_SEARCH}
								  searchLabel={LABEL_SEARCH} searchTerm={searchTerm}/>;

	const serviceGroups = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/serviceGroups`;
	const selectedAlert = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/selectedAlert`;
	const severities = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/severities`;
	const nocConfig = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/nocConfig`;
	const sidePanel = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/sidePanel`;
	const workspace = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/workspace`;

	const pillsBar = <sn-noc-pills-bar selected-alert={selectedAlert} severities={severities}/>;
	const tilemapGrid = createTilemap(state);

	return (
		<div className='tilemap-container-layout'>
			<noc-tilemap-toolbar
				className='tilemap-toolbar'
				noc-config={nocConfig}
				side-panel={sidePanel}
				groupByFields={groupByFields}
				sortByFields={sortByFields}
				selectedGroup={selectedGroup}
				tilemapsFormat={tilemapsFormat}
				selected-alert={selectedAlert}
				service-groups={serviceGroups}
				workspace={workspace}
				key={CONSTANTS.DEFAULTS.NOC_TILEMAP_TOOLBAR_COMPONENT_ID}
				component-id={CONSTANTS.DEFAULTS.NOC_TILEMAP_TOOLBAR_COMPONENT_ID}
			/>
			<section className='tools-section'>
				{breadcrumb}
				{nocSearch}
			</section>
			{pillsBar}
			{tilemapGrid}
		</div>
	);
};
