import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import {view} from './view';
import styles from './nocTilemap.scss';

createNocElement('noc-tilemap', {
	view,
	styles,

	initialState: {
		tooltip: null
	},

	properties: {
		nocAppRef: {
			default: null
		},
		servicePreviewData: {
			default: null
		},
		//unique id for service preview state. used as key to prevent jsx redraw of preview popover
		servicePreviewId: {
			default: 'none'
		},

		popoverKey: {
			default: 'none'
		},

		popover: { //FIXME selectables not working: update not received when component node persists
			default: null
		},

		// FIXME Arik: S18 change - not used?
		severities: {
			default: null
		},

		data: {
			default: {}
		},

		tilemapsFormat: {
			default: null
		},

		timestampForTiles: {
			default: '-1'
		},

		tilemapsConfig: {
			default: {}
		},

		choicesFieldsModel: {
			default: null
		},

		/**
		 * When regular group is drilled down.
		 * Only for regular services (not working with in Service Groups mode)
		 */
		selectedGroup: {
			default: null
		}
	}
});
