import "@servicenow/now-content-tree";

export const view = (state, dispatch) => {
    const {serviceGroups} = state.properties;
    if (!serviceGroups)
        return null;

    let expandedIds = [], expandedItems;
    if (serviceGroups.breadcrumb) {
        expandedIds = serviceGroups.breadcrumb.items.reduce((result, group) => {
            if (group.id)
                result.push(group.id);
            return result;
        }, []);

        expandedItems = expandedIds.map( (item, index, arr) =>{
            return arr.slice(0, index + 1);
        })

    }

    return (
        <section className='severity-colors'>
            <now-content-tree
                show-dividers
                show-highlighted-value
                expandedItems = {expandedItems}
                selectedItems = {[expandedIds]}
                items={serviceGroups.tree}/>
        </section>
    );
};
