import {LOGGING, checkForError} from '@devsnc/library-opws-mapping-shared';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {createUpdateStateEffect} from '@seismic/effect-update-state';

const ACTION_TYPES = {
	NOC_TILEMAP_DYNAMIC_LAYOUT_CONFIG_INIT_REQUESTED: 'NOC_TILEMAP_DYNAMIC_LAYOUT_CONFIG_INIT_REQUESTED',
	NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_STARTED: 'NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_STARTED',
	NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_SUCCEEDED: 'NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_SUCCEEDED',
	NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const getRecords = result => result ? result.data.GlideRecord_Query.noc_layout._results : [];

const nocLayoutQuery = `
query getDynamicLayoutConfig($queryConditions: String!) {
  GlideRecord_Query {
    noc_layout(queryConditions: $queryConditions, pagination: {limit: 100, offset: 0}) {
      _results {
        width {
          value
        } 
        height {
          value
        }          
        max_items {
          value
        }        
        columns {
          value
        }
        breakpoint {
          value
        }
      }
    }
  }
}
`;

const getDynamicLayoutConfig = createGraphQLEffect(nocLayoutQuery, {
	startActionType: ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_STARTED,
	successActionType: ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.NOC_TILEMAP_DYNAMIC_LAYOUT_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const storeDynamicLayoutConfig = coeffects => {
	const {dispatch, updateState, action: {payload}} = coeffects;
	const caller = 'storeDynamicLayoutConfig';
	LOGGING.info('After getting response from the graphQL', {caller});

	const errors = checkForError({payload, dispatch, caller});
	if (errors) {
		return;
	}

	const dynamicLayoutConfig = getRecords(payload).map(item => {
		return {
			width: item.width.value,
			height: item.height.value,
			maxItems: item.max_items.value,
			columns: item.columns.value,
			breakpoint: item.breakpoint.value
		};
	});

	updateState({dynamicLayoutConfig});
};

const effectHandlers = {
	getDynamicLayoutConfig,
	storeDynamicLayoutConfig
};
export default {
	ACTION_TYPES,
	effectHandlers
};
