import '@servicenow/now-record-common';
import '../sn-noc-alerts-panel';
import {t} from 'sn-translate';

const FILTER_BY_ALERT = t('Filter services by alerts');
const sidePanelPayload = {
    sidePanelStatePropertyName: 'sidePanel'
};

const getAlertsTab = (selectedAlert, alertPreferences) => {
    const actionAttributes = {
        headerTitle: FILTER_BY_ALERT,
        selectedAlert,
        alertPreferences
    };

    return {
        actionComponent: 'sn-noc-alerts-panel',
        actionAttributes,
        order: 0,
        assignmentId: 'sn-noc-alerts-panel',
        name: 'sn-noc-alerts-panel',
        tooltip: FILTER_BY_ALERT,
        icon: 'list-fill'
    };
};

export const getNocSidePanelJsx = (state, isEm) => {
    const {
        sidePanel, 
        selectedAlert,
        alertPreferences
    } = state.properties;

    const actions = [
        getAlertsTab(selectedAlert, alertPreferences)
    ];

    const wrapperClasses = {
        'noc-side-panel': true,
        'is-open': sidePanel
    };

    const nocSidePanelJsx = isEm ? 
        <now-record-common-sidebar 
            class={wrapperClasses}
            append-to-payload={sidePanelPayload}
            actions={actions} 
            manage-opened
            opened={sidePanel}
            /> :
            null;
            
    return nocSidePanelJsx;
};

