import {CONSTANTS, snOpwsAlertCardActionTypes} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
    OPEN_ALERT_FORM_INVOKED: snOpwsAlertCardActionTypes.OPEN_ALERT_FORM_INVOKED
};
const openAlertForm = coeffects => {
    const {
        action: {payload},
        dispatch
    } = coeffects;

    const {
        alertId
    } = payload;

    dispatch(CONSTANTS.ACTION_TYPES.ITEM_SELECTED, {
        table: CONSTANTS.TABLES_NAMES.EM_ALERT,
        sys_id: alertId,
        parent_table: CONSTANTS.TABLES_NAMES.EM_ALERT,
        parent_sys_id: alertId
    });
};

const effectHandlers = {
    openAlertForm
};

export default {
    ACTION_TYPES,
    effectHandlers

};