import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {
	getDisplayValue,
	getValue,
	invokeOnNextFrame
} from '@devsnc/library-opws-mapping-shared';
import {
	isEmpty as _isEmpty,
	snakeCase as _snakeCase,
	isUndefined as _isUndefined,
	kebabCase as _kebabCase} from 'lodash';

import {ARIA_PREVIEW_POPOVER_ID, generatePreviewPopover} from './generatePreviewPopover';
import {createOpenPreviewHandler} from './openPreviewHandler';
import nocTilemapSelectGroupEffect from '../noc-tilemap-data-behavior/nocTilemapSelectGroupEffect';
import serviceGroups from '../noc-service-groups-tree-behaviors/nocServiceGroupsEffect';

import '@servicenow/now-tooltip';
import '@servicenow/now-text-link';
import '@devsnc/library-opws-mapping-shared/src/components/noc-element-with-tooltip';
import {t} from 'sn-translate';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {ORPHANS} from '../noc-service-groups-tree-behaviors/utils/serviceGroupBuckets';

//mapping each tile element inside current <noc-tilemap> by tileId,
//to find it for focus restore when popover closes.
const tileIdToElmMap = new Map();

const _generateTileKey = (tilemapId, tileId) => `tile_${tilemapId}_${tileId}`;

const addTileToMap = (tileKey) => (oldVnode, vnode) => {
	//this function works from both `hook-insert` and `hook-update`
	const node = vnode || oldVnode;
	const {elm} = node; //current element on DOM.
	LOGGING.debug(`tileIdToElmMap addTileToMap ${tileKey}`);
	tileIdToElmMap.set(tileKey, elm);
};

const removeTileFromMap = (tileKey) => () => {
	LOGGING.debug(`tileIdToElmMap removeTileFromMap ${tileKey}`);
	tileIdToElmMap.delete(tileKey);
};

const getTileElm = (tileKey) => {
	const elm = tileIdToElmMap.get(tileKey);
	LOGGING.debug(`tileIdToElmMap getTileElm ${tileKey}. connected? ${elm && elm.isConnected}`);
	return (elm && elm.isConnected) ? elm : null;
};

const focusTarget = (tileKey) => () => invokeOnNextFrame(() => {
	const elm = getTileElm(tileKey);

	if (elm) {
		elm.focus();
	}
});

/**
 * Capitalize words in tooltip
 *
 * @type {{textTransform: string}}
 */
const style = {
	textTransform: 'capitalize'
};

const isRtl = () => {
	return (String(document.dir).toLowerCase() || 'ltr') !== 'ltr';
};

const generateTooltipField = (tile, field) => {
	const keyValuePair = [getDisplayValue(field), getDisplayValue(tile[getValue(field)])];
	if (isRtl()) {
		keyValuePair.reverse();
	}

	return keyValuePair.join(': ');
};

/**
 * Go over all selected fields for hover tooltip and construct the list
 * of field name and value, i.e.
 *      Name: Service Name
 *      Severity: Major
 *
 * @param tile
 * @param fields
 */
const generateTooltipContent = (tile, fields) => {
	//If tooltip fields are empty, then return null to disable any overhead of tooltip rendering.
	if (!fields || fields.length === 0) {
		return null;
	}

	const tooltipContent = fields.map(generateTooltipField.bind(null, tile));

	if (isRtl()) {
		tooltipContent.reverse();
	}

	return tooltipContent.join(', ');
};

const showTooltip = (updateState, tileKey) => event => {
	LOGGING.debug(`showTooltip on ${tileKey}`);
	updateState({
		tooltip: tileKey
	});
};

const hideTooltip = (updateState, tileKey) => event => {
	LOGGING.debug(`hideTooltip from ${tileKey}`);
	updateState({
		tooltip: null
	});
};

const shouldShowTooltip = (tooltip, tileKey) => tooltip === tileKey;

/**
 * Generate JSX for single service tile.
 * Keep it function, used to pass parameters on 'this'.
 * @param state
 * @param dispatch
 * @param updateState
 * @param tile - data object of tile/service.
 *
 * @returns {*}
 */
const generateTileJsx = (state, dispatch, updateState, tile) => {
	const {tooltip} = state;
	const {
		servicePreviewData,
		servicePreviewId,
		data: {calculatedTileSize, groupField},
		popoverKey,
		popover,
		tilemapsConfig: {hoverTooltipFields},
		nocAppRef,
		timestampForTiles,
		tilemapsFormat = {}
	} = state.properties;


	const {businessServiceGroup = ''} = tilemapsFormat;
	const iconUrl = getValue(tile.iconUrl);
	const className = getDisplayValue(tile.className);
	const severity = getValue(tile.severity);
	const serviceId = getValue(tile.id);
	const name = getDisplayValue(tile.name);

	const tileId = `tile_${serviceId}`;
	const tilemapId = `tilemap_${groupField.name}_${groupField.value}`;
	const tileKey = _generateTileKey(tilemapId, tileId);

	const isServiceGroupMode = businessServiceGroup !== '';
	const isPreviewDisplayed = (
		servicePreviewData &&
		//only if in service groups, need to varify tilemapID to distinguish between differnt instances of service.
		(!isServiceGroupMode || servicePreviewData.tilemapId === tilemapId) &&
		servicePreviewData.sysId === serviceId
	);

	const ariaDescribedBy = isPreviewDisplayed ? ARIA_PREVIEW_POPOVER_ID : undefined;
	const labelId = `${tileKey}_label`;
	const tileSizeClass = calculatedTileSize ? `tile tile-${calculatedTileSize}` : 'tile';
	let cssClass = `severity-${severity} bg-color ${tileSizeClass}`;

	if (isPreviewDisplayed) {
		cssClass += ' selected';
	}

	//used to compute best fit for popover position
	const popoverTargetRef = createRef();

	const openPreviewHandler = createOpenPreviewHandler({
		serviceId,
		tilemapId,
		servicePreviewData,
		dispatch,
		focusTarget: focusTarget(tileKey),
		timestampForTiles
	});

	const previewPopover = isPreviewDisplayed ?
		generatePreviewPopover({
			dispatch,
			popover,
			servicePreviewData,
			servicePreviewId,
			popoverKey,
			popoverTargetRef,
			popoverContainerRef: nocAppRef
		}) :
		null;
	const tooltipText = generateTooltipContent(tile, hoverTooltipFields);
	const tooltipContent = shouldShowTooltip(tooltip, tileKey) ?
		tooltipText : null;
	const edsTooltip = tooltipContent ?
		<now-tooltip
			opened={true}
			target-ref={getTileElm(tileKey)}
			content={tooltipContent}/>
		: null;
	return (
		<li className={tileSizeClass}
		    ref={popoverTargetRef}>
			<button
				className={cssClass}
				key={tileKey}
				on-click={openPreviewHandler}
				id={tileKey}
				serviceId={serviceId}
				aria-labelledby={labelId}
				aria-describedby={ariaDescribedBy}
				hook-insert={addTileToMap(tileKey)}
				hook-update={addTileToMap(tileKey)} //required to update attached element on each render
				hook-destroy={removeTileFromMap(tileKey)}
				on-mouseenter={showTooltip(updateState, tileKey)}
				on-mouseleave={hideTooltip(updateState, tileKey)}
			>
				<img className='tile-icon now-p-block--xs now-p-inline--xs' src={iconUrl} alt=""/>
				<label id={labelId} className='tile-label'>
					{name || null}
				</label>
				<span className="sr-only">{tooltipText}</span>
			</button>

			{edsTooltip}
			{previewPopover}
		</li>
	);
};

const generateEllipsisTile = (state, dispatch, segment) => {
	const TOOLTIP_SHOW_MORE_ELEMENT_ID = 'TOOLTIP_SHOW_MORE_ELEMENT_ID';
	const {
		data: {groupField}
	} = state.properties;
	const diff = (segment.segmentTotalItemsCount - segment.segmentItemsCount);
	const diffText = `+${diff}`;

	const TOOLTIP_SHOW_MORE_ELEMENT = t('Show all services in \'{0}\'', groupField.displayValue);
	const tooltipShow = TOOLTIP_SHOW_MORE_ELEMENT;
	return (
		<li className='show-more-text-container'>

			<noc-element-with-tooltip tooltip={tooltipShow}>
				<button className='show-more-text'
				        aria-describedby={TOOLTIP_SHOW_MORE_ELEMENT_ID}
				        on-click={drillDownToGroup(dispatch, groupField.isServiceGroup, groupField)}>
					{diffText}
				</button>

				<label className='now-a11y-label' id={TOOLTIP_SHOW_MORE_ELEMENT_ID}>
					{tooltipShow}
				</label>
			</noc-element-with-tooltip>
		</li>
	);
};

const generateIndicatorJsx = (currentIndex, segment, state) => {
	const {
		tilemapsFormat: {sortByField},
		choicesFieldsModel
	} = state.properties;

	const {label, iconUrl} = choicesFieldsModel[sortByField][segment.segmentType];

	if (!label || !iconUrl) {
		LOGGING.error(`generateIndicatorJsx -> cannot find [${segment.segmentType}]`, {extra: {state, segment}});
	}

	const indicatorClassTarget = `-${_kebabCase(sortByField)}`;
	const key = `indicator_${currentIndex}`;
	const className = `segment-indicator -${indicatorClassTarget}`;

	return (
		<noc-element-with-tooltip
			tooltip={label}>
			<div className={className}>
				<img
					className='segment-indicator-icon'
					alt={label}
					src={iconUrl}
					key={key}/>
			</div>
		</noc-element-with-tooltip>
	);
};

const generateSegmentJsx = (state, dispatch, updateState, segment, currentIndex) => {
	const {
		choicesFieldsModel,
		data: {calculatedTileSize},
		tilemapsFormat: {sortByField}
	} = state.properties;


	const segmentJsx = segment.items.map(generateTileJsx.bind(null, state, dispatch, updateState));

	if (segment.isExpendable) {
		const ellipsisTile = generateEllipsisTile(state, dispatch, segment);
		segmentJsx.push(ellipsisTile);
	}

	const isGroupSegmented = choicesFieldsModel.hasOwnProperty(sortByField);
	const hasCategoryIndicator = isGroupSegmented ? 'indicator' : '';

	const categoryIndicator = isGroupSegmented ? generateIndicatorJsx(currentIndex, segment, state) : null;
	const key = `segment_key_${currentIndex}`;
	const className = `noc-tilemap-segment-container size-${calculatedTileSize} ${hasCategoryIndicator}`;

	return (
		<div className={className}>
			{categoryIndicator}
			<ol className='segment severity-colors'>
				{segmentJsx}
			</ol>
		</div>
	);
};

const drillDownToGroup = (dispatch, isServiceGroup, groupField) => () => {
	if (isServiceGroup) {
		const id = groupField.id;
		if (!id) {
			return;
		}

		const newServiceGroupObj = {item: {id}}; //The structure of the item is equal to the one send from the tree
		dispatch(serviceGroups.ACTION_TYPES.SERVICE_GROUP_SELECT_DIFFERENT_GROUP, newServiceGroupObj);

		//Only for the ORPHANS we want to continue and drilldown to this group by
		//dispatch the selectedGroup
		if (getDisplayValue(groupField) !== ORPHANS) {
			return;
		}
	}
	const newDrillDownToGroup = {name: getDisplayValue(groupField)};
	dispatch(nocTilemapSelectGroupEffect.ACTION_TYPES.TILEMAP_SELECT_REGULAR_GROUP, newDrillDownToGroup);
};

const noServicesToDisplayJsx = () => {
	return (
		<span className='tilemap-no-services-to-display'>No services to display</span>
	);
};

const isExpandable = (segments) => {
	const res = segments.find(segment => {
		return segment.isExpendable === true;
	});

	return !_isUndefined(res);
};

const createGroupTitleJsx = (state, dispatch, label, labelId) => {
	const {tilemapsFormat: {businessServiceGroup}, data, selectedGroup} = state.properties;
	const isServiceGroupOrphansChildren = businessServiceGroup && !data.groupField.id;//Check if this is the Orphans
                                                                                      // group
	const isSelectedGroup = (selectedGroup && selectedGroup === data.label);
	const {groupField: {isServiceGroup, displayValue}} = data;

	const conditionForNotTextLink =
		(isServiceGroupOrphansChildren || isSelectedGroup) ||
		(data.segments.length === 0) ||
		(!isServiceGroup && !isExpandable(data.segments)) ||
		(isServiceGroup && displayValue === ORPHANS && !isExpandable(data.segments));

	return conditionForNotTextLink ?
		<span className='tilemap-title-link  now-m-block-end--sm'>{label}</span> :
		<now-text-link variant='primary'
		               className='tilemap-title-link now-m-block-end--sm'
		               label={label}
		               id={labelId}
		               href='javascript:void(0)'
		               on-click={drillDownToGroup(dispatch, businessServiceGroup, data.groupField)}/>;
};

export const view = (state, {dispatch, updateState}) => {

	const {
		choicesFieldsModel,
		tilemapsFormat: {sortByField},
		data: {segments, segmentedBy, totalServicesCount, groupField}
	} = state.properties;

	if (!choicesFieldsModel || segmentedBy !== sortByField) {
		return null;
	}

	const segmentsJsx = !_isEmpty(segments) ? segments.map(generateSegmentJsx.bind(null, state, dispatch, updateState))
		: noServicesToDisplayJsx();
	const labelId = `tilemap_section_label_${_snakeCase(groupField.displayValue)}`;
	const label = `${groupField.displayValue} (${totalServicesCount})`;
	const labelJsx = createGroupTitleJsx(state, dispatch, label, labelId);
	const tilemapId = `tilemap_${groupField.name}_${groupField.value}`;

	return (
		<section className='noc-tilemap' key={tilemapId} tabindex='0' aria-labelledby={labelId}>
			{labelJsx}
			{segmentsJsx}
		</section>
	);
};
