import SPECTRUM_CONST from './snNocSpectrumConsts';
import settingsModalEffect from './settingsModalEffect';
import {i18n} from '@devsnc/library-opws-mapping-shared';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-modal';
import '@servicenow/now-dropdown';
import '@servicenow/now-heading';



export const getSettingsModal = args => {
	const {
		selectedItem,
		dispatch
	} = args;

	const hiddenStyle = {
		display: 'none'
	};

	const tileSizeLabelledBy = `${SPECTRUM_CONST.SECTION_ID_TILE_SIZE} ${SPECTRUM_CONST.SECTION_ID_TILE_SIZE_HIDDEN_LABEL}`;

	const tileSizeDropdownJsx = (
		<Fragment>
			<label id={SPECTRUM_CONST.SECTION_ID_TILE_SIZE_HIDDEN_LABEL} style={hiddenStyle}>
				{i18n.NOC_UPDATES_USER_VIEW}
			</label>

			<now-dropdown 
				className="now-align -inline-end"
				tooltipContent={i18n.NOC_NO_TILE_SIZE_TOOLTIP}
				selectedItems={selectedItem}
				component-id={settingsModalEffect.COMPONENT_IDS.TILE_SIZE_DROPDOWN_ID}
				key={settingsModalEffect.COMPONENT_IDS.TILE_SIZE_DROPDOWN_ID}
				aria-labelledby={tileSizeLabelledBy}
				items={SPECTRUM_CONST.tileSizeDropdownItems}
				variant='secondary'
				type='sm'/>
		</Fragment>
	);

	const labelValueItems = [
		{
			sectionId: SPECTRUM_CONST.SECTION_ID_TILE_SIZE,
			itemId: SPECTRUM_CONST.ITEM_ID_TILE_SIZE,
			sectionLabel: SPECTRUM_CONST.LABEL_SECTION_TILE_SIZE,
			label: SPECTRUM_CONST.LABEL_TILE_SIZE,
			value: {
				type: 'jsx', 
				value: tileSizeDropdownJsx
			}
		}
	];

	const generateSectionsJsx = item => (
		<section key={item.sectionId} className='settings-section'>
			<now-heading 
				hasNoMargin
				variant='title-primary' 
				level='2'
				label={item.sectionLabel}
				className='section-label' 
				id={item.sectionId}/>
        
			<now-label-value-tabbed
				className='section-item'
				aria-describedBy={item.sectionId}
				size='lg' 
				items={[item]}/>
		</section>
	);

	const sectionsJsx = labelValueItems.map(generateSectionsJsx);
    
	const settingsModalContent = (
		<form className='settings-form'>
			{sectionsJsx}
		</form>
	);

	return(
		<now-modal
			component-id={settingsModalEffect.COMPONENT_IDS.SETTINGS_MODAL_ID}
			opened
			append-to-payload={{dispatches: settingsModalEffect.ACTION_TYPES.SETTINGS_MODAL_STATE_CHANGED}}
			size='sm'
			header-label={SPECTRUM_CONST.LABEL_SETTINGS}>
			<Fragment slot='content'>
				{settingsModalContent}
			</Fragment>
		</now-modal>
	);
};