import {
    createNocElement, 
    createActionConfig, 
    snOpwsRelatedItemsListActionTypes, 
    snOpwsAlertCardActionTypes
} from '@devsnc/library-opws-mapping-shared';

import styles from './snNocAlertsPanel.scss';
import view from './snNocAlertsPanelView';
import effects from './snNocAlertsPanelEffect';
import snNocOpenAlertEffect from './snNocOpenAlertEffect';


createNocElement('sn-noc-alerts-panel', {
    view,
    styles,

    initialState: {
        isLoading: false,
        totalRecords: 0
    },

    properties: {
        alerts: {
            default: null
        },
        headerTitle: {
            default: ''
        },
        selectedAlert: {
            default: null
        },
        alertPreferences: {
            default: null
        }
    },

    actionHandlers: {
        [snOpwsRelatedItemsListActionTypes.SORT_SELECTED]:
            createActionConfig(effects.effectHandlers.alertsSortingChanged),

       [snOpwsRelatedItemsListActionTypes.REFRESH_REQUESTED]:
            createActionConfig(effects.effectHandlers.refreshAlerts),

        [snOpwsAlertCardActionTypes.ALERT_CARD_SELECTED]:
            createActionConfig(effects.effectHandlers.selectAlert),

        [effects.ACTION_TYPES.NOC_GET_ALERTS_REQUESTED]:
            createActionConfig(effects.effectHandlers.getAlerts),

        [effects.ACTION_TYPES.NOC_GET_ALERTS_REQUEST_SUCCEEDED]:
            createActionConfig(effects.effectHandlers.saveAlerts),

        [effects.ACTION_TYPES.NOC_GET_ALERTS_REQUEST_FAILED]:
            createActionConfig(effects.effectHandlers.getAlertsError),

        [snNocOpenAlertEffect.ACTION_TYPES.OPEN_ALERT_FORM_INVOKED]:
            createActionConfig(snNocOpenAlertEffect.effectHandlers.openAlertForm)




    }
});