import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, LOGGING, checkForError, deepFreeze, formatAlertsResponse} from '@devsnc/library-opws-mapping-shared';
import effectImpactedServices from '../noc-app/effectImpactedServices';
import effectAlertSelected from '../noc-app/effectAlertSelected';

const ACTION_TYPES = {
    NOC_GET_ALERTS_REQUESTED: 'NOC_ALERTS_REQUESTED',
    NOC_GET_ALERTS_REQUEST_STARTED: 'NOC_GET_ALERTS_REQUEST_STARTED',
    NOC_GET_ALERTS_REQUEST_SUCCEEDED: 'NOC_GET_ALERTS_REQUEST_SUCCEEDED',
    NOC_GET_ALERTS_REQUEST_FAILED: 'NOC_GET_ALERTS_REQUEST_FAILED'
};

const saveAlerts = coeffects => {
    LOGGING.info('saveAlerts', {caller: 'snNocAlertsPanelEffect#saveAlerts'});
    const {
        dispatch,
        updateState,
        updateProperties,
        action: {payload}
    } = coeffects;

    const {
        alerts = [],
        totalRecords,
        lastUpdated
    } = payload;

    const errors = checkForError({payload, dispatch, caller: 'snNocAlertsPanelEffect#saveAlerts'});

    const formattedAlerts = (errors) ? null : deepFreeze(formatAlertsResponse(alerts));

    updateState({
        isLoading: false,
        totalRecords
    });

    updateProperties({
        alerts: formattedAlerts
    });
};


const getAlerts = createHttpEffect('/api/sn_nocpit/nocpit/AllAlertsForBs', {
    method: 'POST',
    startActionType: ACTION_TYPES.NOC_GET_ALERTS_REQUEST_STARTED,
    successActionType: ACTION_TYPES.NOC_GET_ALERTS_REQUEST_SUCCEEDED,
    errorActionType: ACTION_TYPES.NOC_GET_ALERTS_REQUEST_FAILED
});

const getAlertsError = coeffects => {
    const {
        updateState,
        dispatch,
        action: {payload}
    } = coeffects;

    updateState({isLoading: false});

    dispatch(CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED, payload);
};

const alertsSortingChanged = coeffects => {
    const {
        updateState,
        dispatch,
        properties: {alertPreferences},
        action: {payload: {selectedSortOption: defaultAlertsSorting}}
    } = coeffects;

    dispatch(CONSTANTS.ACTION_TYPES.ALERT_PREFERENCES_SAVE_REQUESTED, {alertPreferences: {...alertPreferences, defaultAlertsSorting}});
};

const refreshAlerts = coeffects => {
    const {
        dispatch,
        action,
        updateState
    } = coeffects;

    LOGGING.info('refreshAlerts', {caller: 'snNocAlertsPanelEffect'});
    const {selectedSortOption, selectedType: {sortOptions}} = action.payload;
    const sortedBy = sortOptions.filter(s => s.id === selectedSortOption);
    const {order, field: sortingColumnName} = sortedBy[0];


    const data = {
        isOrderASC: order === 'asc' ? true : false,
        sortingColumnName,
        firstRow: '',
        timestamp: ''

    };

    updateState({isLoading: true});

    dispatch(ACTION_TYPES.NOC_GET_ALERTS_REQUESTED, {data});
};

const selectAlert = ({dispatch, action}) => {
    LOGGING.info('selectAlert', {caller: 'snNocAlertsPanelEffect'});
    const {isSelected, item} = action.payload;

    if (isSelected) {
        const {id: alertId, groupId, ciId} = item;
        const queryParams = {
            alertId,
            groupId: (groupId) ? groupId : ' ',
            ciId
        };

        // updating state to set id of selected alert.
        dispatch(effectAlertSelected.ACTION_TYPES.NOC_SELECT_ALERT_CARD, {selectedAlert: item});
        // Bring the sys ids of the services that are impacted by the alert
        dispatch(effectImpactedServices.ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST, queryParams);
    } else {
        dispatch(effectImpactedServices.ACTION_TYPES.CLEAR_IMPACTED_SERVICES_REQUEST);
    }
};

export const effectHandlers = {
    alertsSortingChanged,
    refreshAlerts,
    getAlerts,
    saveAlerts,
    getAlertsError,
    selectAlert
};

export default {
    ACTION_TYPES,
    effectHandlers
};
