import { view } from './snNocViewsView';
import { createNocElement, createActionConfig, CONSTANTS } from '@devsnc/library-opws-mapping-shared';
import styles from './snNocViews.scss';
import snNocViewsEffects from './snNocViewsEffects';
import USER_VIEW_CONSTANTS from './userViewsConstants';

createNocElement('sn-noc-views', {
	view,
	styles,

	properties: {
		views: {
			default: null
		},
		noChangesOnCurrentView: {
			default: true
		},
		currentView: {
			default: null
		}
	},

	actionHandlers: {
		[USER_VIEW_CONSTANTS.ACTION_TYPES.VIEW_NAME_SELECTED]:
			createActionConfig(snNocViewsEffects.effectHandlers.userViewSelectedEffect),
		[USER_VIEW_CONSTANTS.ACTION_TYPES.VIEW_ITEM_CLICKED]:
			createActionConfig(snNocViewsEffects.effectHandlers.userViewActionsEffect),
		[USER_VIEW_CONSTANTS.ACTION_TYPES.SAVE_BUTTON_CLICKED]:
			createActionConfig(snNocViewsEffects.effectHandlers.userSaveActionEffect),
		[USER_VIEW_CONSTANTS.ACTION_TYPES.VIEW_SAVE_AS_CLICKED]:
			createActionConfig(snNocViewsEffects.effectHandlers.userSaveAsActionEffect)
	}
});