/**
 * GQL Query to get current User View for nocpit.
 * Used on page load.
 */
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {transformToModel} from './transforms';

import {get as _get} from 'lodash';
import {CONSTANTS, checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from './constants';

const getVariables = (workspace, userId, viewName = CONSTANTS.DEFAULTS.DEFAULT_USER_VIEW_NAME) => {
	let q = `user_id=${userId}^workspace=${workspace}^view_name=${viewName}`;

	return {
		queryConditions: q
	};
};

const requestDefaultUserViewHandler = coeffects => {
	const {state, dispatch, action: {payload: {userId, viewName}}} = coeffects;

	// const {workspace} = state.properties;
	//temporary removing workspace for WS integration
	const workspace = 'nocpit';

	if (!workspace) {
		return false;
	}

	const variables = getVariables(workspace, userId, viewName);

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST, variables);
};

const RESPONSE_RESULT_PATH = 'data.GlideRecord_Query.noc_user_view._results[0]';

const defaultUserViewFetched = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_noc_user_view, {
	startActionType: VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST_STARTED,
	successActionType: VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST_SUCCEEDED,
	errorActionType: VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const handleDefaultUserViewResponse = coeffects => {
	LOGGING.info('Received handleDefaultUserViewResponse', {caller: 'revertUserViewEffect'});

	const {
		dispatch,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleDefaultUserViewResponse'});
	if (errors) {
		return;
	}

	const nocViewsRecord = _get(payload, RESPONSE_RESULT_PATH);
	const selectableProps = transformToModel(nocViewsRecord);

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_DEFAULT_USER_VIEW_REQUEST, selectableProps);
};

const effectHandlers = {
	requestDefaultUserViewHandler,
	defaultUserViewFetched,
	handleDefaultUserViewResponse
};

export default {
	effectHandlers,
	getVariables
};
