import {snOpwsRelatedItemsListItemTypes} from '@devsnc/library-opws-mapping-shared';

export default state => {
	const {
		isLoading,
		totalRecords,
		properties: {
			alerts,
			headerTitle,
			selectedAlert,
			alertPreferences: {
				defaultAlertsSorting
			}
		}
	} = state;

	const selectedAlertId = selectedAlert ? selectedAlert.id : '';
	return (
		<sn-opws-related-items-list
			manage-selected-item-id
			selected-sort-option={defaultAlertsSorting}
			selected-item-id={selectedAlertId}
			is-loading={isLoading}
			items={alerts ? alerts : []}
			last-updated={Date.now()}
			total-records={totalRecords}
			item-types={snOpwsRelatedItemsListItemTypes}
			header-title={headerTitle}
		/>
	);
};