import { nocSeverityDataBehavior } from '../noc-severity-data-behavior';
import { nocInitialDataBehavior } from '../noc-initial-data-behavior';
import { userViewBehaviors } from '../noc-user-view-behaviors';
import { 
	CONSTANTS, 
	createNocElement, 
	nocErrorBehavior, 
	createActionConfig, 
	behaviorNocNotification,
	effectToggleSidePanel
} from '@devsnc/library-opws-mapping-shared';
import { nocChangesNotificatorBehavior } from '../noc-changes-notificator-behavior';
import { nocGetServiceGroupsBehavior } from '../noc-service-groups-tree-behaviors/nocGetServiceGroupsBehavior';
import effectImpactedServices from './effectImpactedServices';
import effectAlertSelected from './effectAlertSelected';
import nocVisibilityChangeEffect from './nocVisibilityChangeEffect';
import { view } from './view';
import styles from './nocApp.scss';


createNocElement('noc-app', {
	view,
	onBootstrap(host, dispatch) {
		dispatch(CONSTANTS.ACTION_TYPES.NOC_COMPONENT_CONNECTED, {}, { stopPropagation: true });
	},
	styles,
	intialState: {
		userViewResource: {
			isViewModified: false
		}
	},
	properties: {
		//selectable properties

		/**
		 * name of workspace URL parameter
		 * String
		 */
		workspace: {
			default: CONSTANTS.DEFAULTS.WORKSPACE, //TODO set public prop from URL
			selectable: true
		},

		/**
		 * Array of services returned by services API, before any filtering
		 * [EMServiceDTO]
		 */
		population: {
			default: null,
			selectable: true
		},
		/**
		 * Object container for all service groups data structure models
		 */
		serviceGroups: {
			default: null,
			selectable: true
		},
		/**
		 * Array of displayed services, filtered by enabled severities.
		 * [EMServiceDTO]
		 */
		enabledPopulation: {
			default: null,
			selectable: true
		},

		/** mapping of severity objects with the following fields:
		 * {
		 * value: {value, label, iconUrl, color, isEnabled, populationSize, sequence}
		 * }
		 * 'value' is a numerical (1,2,3,4,5)
		 */
		severities: {
			default: null,
			selectable: true
		},

		//timestamp for tilemap refresh synchronization (eliminate repeated refesh on same timestamp value)
		timestampForTiles: {
			default: '-1',
			selectable: true
		},

		/**
		 * Formatted data from noc_config
		 * {isEM: boolean}
		 */
		nocConfig: {
			default: null,
			selectable: true
		},

		/**
		 * Formatted data on current user
		 * {userId, isA11y}
		 */
		userMeta: {
			default: null,
			selectable: true
		},

		/**
		 * Part of noc_user_view with the uber filter query
		 * {filter: String}
		 */
		uberFilter: {
			default: null,
			selectable: true
		},

		/**
		 * Part of noc_user_view with severities state.
		 * {enabledSeverities: {1: true, 3: true, 5: true...}}
		 */
		severityFilter: {
			default: null,
			selectable: true
		},

		/**
		 * Part of noc_user_view with user preferences that affect alerts representation
		 * {isAlertsCorrelatedView: Boolean, defaultAlertsSorting: String}
		 */
		alertPreferences: {
			default: null,
			selectable: true
		},

		/**
		 *
		 */
		views: {
			default: {
				viewItems: [],
				itemsMap: null,
				selectedView: null,
				defaultViewSysId: null
			},
			selectable: true
		},


		/**
		 * Part of noc_user_view with the honeycomb state
		 * {
		 *   groupByField: FieldName
		 *	 groupByOrder: "asc|desc"
		 *	 sortByField: FieldName
		 *	 sortByOrder: "asc|desc"
		 * }
		 */
		tilemapsFormat: {
			default: null,
			selectable: true
		},

		/**
		 * Side panel (right) which is used for showing alerts and other data
		 */
		sidePanel: {
			default: false,
			selectable: true
		},

		alerts: {
			default: null,
			selectable: true
		},

		/**
		 *     Array of services ids that are impacted by the alert
		 */
		impactedServicesAlertIds: {
			default: [],
			selectable: true
		},

		/**
		 * True if we are in the middle of getting impacted services
		 */
		isImpactedServicesLoading: {
			default: false,
			selectable: true
		},

		/**
		 * The selected alert, or null if not selected.
		 */
		selectedAlert: {
			default: null,
			selectable: true
		},

		/**
		 * This property shows if changes are performed on a view (used for "save" button disable/enable)
		 */
		noChangesOnCurrentView: {
			default: true,
			selectable: true
		}

	},
	actionHandlers: {
		[effectImpactedServices.ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST]:
			createActionConfig(effectImpactedServices.effectHandlers.getImpactedServicesEffectHandler),

		[effectImpactedServices.ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST_STARTED]:
			createActionConfig(effectImpactedServices.effectHandlers.handleImpactedServicesRequestStarted),

		[effectImpactedServices.ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST_SUCCEEDED]:
			createActionConfig(effectImpactedServices.effectHandlers.handleGetImpactedServicesResponse),

		[effectImpactedServices.ACTION_TYPES.CLEAR_IMPACTED_SERVICES_REQUEST]:
			createActionConfig(effectImpactedServices.effectHandlers.clearImpactedServicesHandler),

		[effectAlertSelected.ACTION_TYPES.NOC_SELECT_ALERT_CARD]:
			createActionConfig(effectAlertSelected.effectHandlers.nocSelectedAlertEffect),

		[effectToggleSidePanel.ACTION_TYPES.TOGGLE_SIDE_PANEL]:
			createActionConfig(effectToggleSidePanel.effectHandlers.toggleSidePanel),

		[nocVisibilityChangeEffect.ACTION_TYPES.NOC_VISIBILITY_CHANGED]:
			createActionConfig(nocVisibilityChangeEffect.effectHandlers.handleVisibilityChangeEffect)

	},
	behaviors: [
		nocInitialDataBehavior.behavior,
		userViewBehaviors.behaviors.getUserViewBehavior,
		userViewBehaviors.behaviors.saveUserViewBehavior,
		userViewBehaviors.behaviors.revertUserViewBehavior,
		nocSeverityDataBehavior.behavior,
		nocChangesNotificatorBehavior.behavior,
		nocGetServiceGroupsBehavior,
		nocErrorBehavior,
		behaviorNocNotification
	]
});
