import {isFunction as _isFunction} from 'lodash';

const ACTION_TYPES = {
    /**
     * To be dispatched by opener component that renders noc-popover, then popover is added on DOM (before or after).
     * payload={focusTarget: effect(), tileId: String}
     * @param focusTarget {Function} function that will be called when popover closes, to regain focus on target element.
     * @param tileId {String} will be passed to `focusTarget` function. This removes need to create new `focusTarget` function every time.
     */
    NOC_POPOVER_OPEN: 'NOC_POPOVER_OPEN',

	//payload={positionStyle={object for inline style}}
	NOC_POPOVER_FIT: 'NOC_POPOVER_FIT',

	//to be dispatched by opener component that renders noc-popover, after popover has been removed from DOM.
	NOC_POPOVER_CLOSED: 'NOC_POPOVER_CLOSED'
};

/**
 * This effect only stores the handler that will be used to restore original focus when popover is closed.
 */
const nocPopoverOpenEffect = coeffects => {
	const {
		updateProperties,
		action: {payload: {focusTarget}}
	} = coeffects;

	updateProperties({popover: {focusTarget}});
};

/**
 * This effect sets positionStyle for popover content, after it was already computed.
 * Computation done asynchronously, in `hook-insert` after content element was rendered.
 */
const nocPopoverFitEffect = coeffects => {
	//id sent on each fit, because initially on open id is still not ready.
	const {
		properties,
		updateProperties,
		action: {
			payload: {positionStyle, id}
		}
	} = coeffects;

	updateProperties({
		popover: {
			...properties.popover,
			id,
			positionStyle
		}
	});
};

/**
 * Default behavior on popover close: restore focus on target element.
 * using handler function that was provided in POPOVER_OPEN
 */
const nocPopoverCloseEffect = coeffects => {
	const {
		updateProperties,
		properties: {popover = {}}
	} = coeffects;

	const {focusTarget} = popover;

	if (_isFunction(focusTarget)) {
		focusTarget();
	}

	updateProperties({popover});
};

const effectHandlers = {
	nocPopoverOpenEffect,
	nocPopoverCloseEffect,
	nocPopoverFitEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};