import {checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {get as _get} from 'lodash';
import {transformRecordToMutationVars} from './transforms';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import VIEW_CONSTANTS from './constants';

const specificUserViewFetched = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_noc_user_view, {
	startActionType: VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_REQUEST_STARTED,
	successActionType: VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_REQUEST_SUCCEEDED,
	errorActionType: VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const comparisonUserViewFetched = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_noc_user_view_records_for_comparison, {
	startActionType: VIEW_CONSTANTS.ACTION_TYPES.COMPARISON_USER_VIEW_REQUEST_STARTED,
	successActionType: VIEW_CONSTANTS.ACTION_TYPES.COMPARISON_USER_VIEW_REQUEST_SUCCEEDED,
	errorActionType: VIEW_CONSTANTS.ACTION_TYPES.COMPARISON_USER_VIEW_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const requestSpecificUserViewHandler = coeffects => {
	LOGGING.info('Received requestSpecificUserViewHandler', {caller: 'getSpecificUserViewEffect'});

	const {
		properties,
		action: {payload},
		dispatch
	} = coeffects;

	if (checkForError({payload, dispatch, caller: 'requestSpecificUserViewHandler'})) {
		return;
	}

	// get the view name and user id from payload
	const {viewName, userId} = payload;
	const {nocConfig} = properties;
	// const {workspace} = properties;
	//temporary removing workspace for WS integration
	const workspace = 'nocpit';

	if (!workspace || !nocConfig) {
		return;
	}

	const variables = getVariables(workspace, viewName, userId);

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_REQUEST, variables);
};

const RESPONSE_RESULT_PATH = 'data.GlideRecord_Query.noc_user_view._results[0]';
const handleSpecificUserViewResponse = coeffects => {
	LOGGING.info('Received handleSpecificUserViewResponse', {caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'});

	const {
		properties,
		action: {payload},
		dispatch,
		updateProperties
	} = coeffects;

	if (checkForError({payload, dispatch, caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'})) {
		return;
	}

	const {
		// workspace.
		views,
		userMeta: {userId}
	} = properties;
	const {defaultViewSysId: sysId} = views;
	const workspace = 'nocpit';

	const nocViewsRecord = _get(payload, RESPONSE_RESULT_PATH);
	const originViewId = nocViewsRecord.sys_id.value;

	//
	// Update the application values with ones from view
	//
	LOGGING.info('Updating state with specific user view values', {caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'});
	dispatch(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_USER_VIEW_ON_STATE, nocViewsRecord);

	//
	// Write to the default user view
	//
	LOGGING.info(`Saving selected user view to user's default`, {caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'});
	const variables = transformRecordToMutationVars(
		workspace,
		VIEW_CONSTANTS.DEFAULTS.CURRENT_USER_VIEW_NAME,
		userId, //current user id, not from record!
		sysId,
		originViewId,
		nocViewsRecord
	);

	const actionType = sysId ? VIEW_CONSTANTS.ACTION_TYPES.UPDATE_USER_VIEW_REQUEST : VIEW_CONSTANTS.ACTION_TYPES.INSERT_USER_VIEW_REQUEST;
	dispatch(actionType, variables);

	//
	// Refresh the user views list and select the selected one
	//
	if (!views.itemsMap[originViewId]) {
		LOGGING.info('Dispatching load user view names', {
			extra: variables,
			caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'
		});
		dispatch(VIEW_CONSTANTS.ACTION_TYPES.LOAD_USER_VIEW_NAMES);
	} else {
		LOGGING.info(`Setting view from '${views.selectedView}' to '${originViewId}'`,
			{caller: 'getSpecificUserViewEffect.handleSpecificUserViewResponse'});
		const selectedView = [originViewId];
		updateProperties({views: {...views, selectedView}});
	}
};

const updateSpecificUserViewFailed = coeffects => {
	LOGGING.error('Received updateSpecificUserViewFailed', {caller: 'getSpecificUserViewEffect.updateSpecificUserViewFailed'});
};


const effectHandlers = {
	requestSpecificUserViewHandler,
	specificUserViewFetched,
	handleSpecificUserViewResponse,
	updateSpecificUserViewFailed,
	comparisonUserViewFetched
};

const getVariables = (workspace, viewName, userId) => {
	let q = `user_id=${userId}^workspace=${workspace}^view_name=${viewName}`;

	return {
		queryConditions: q
	};
};

export default {
	effectHandlers,
	getVariables
};
