import {checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
    NOC_SELECT_ALERT_CARD: 'NOC_SELECT_ALERT_CARD',
};

let delayTimer;
const nocSelectedAlertEffect = coeffects => {
    LOGGING.info('nocSelectedAlertEffect', {caller: 'nocSelectedAlertEffect'});
    clearTimeout(delayTimer);
    const {
        dispatch,
        action: {payload},
        updateProperties
    } = coeffects;

    const {selectedAlert} = payload;
    const errors = checkForError({payload, dispatch, caller: 'nocSelectedAlertEffect'});
    if (errors)
        return;
    delayTimer = setTimeout(function () {
        updateProperties({selectedAlert});
    }, 300);


};
const effectHandlers = {
    nocSelectedAlertEffect
};

export default {
    ACTION_TYPES,
    effectHandlers
};