import {CONSTANTS, createActionConfig} from '@devsnc/library-opws-mapping-shared';
import serviceGroups from './nocServiceGroupsEffect';

export const nocGetServiceGroupsBehavior = {
	name: 'nocGetServiceGroupsTreeBehavior',
	actionHandlers: {
		/**
         * On Population changed, build the model (tree, breadcrumb, layout buckets)
         */
		[CONSTANTS.ACTION_TYPES.NOC_POPULATION_SET]:
                  createActionConfig(serviceGroups.effectHandlers.getServiceGroupsTreeHandler),

		[serviceGroups.ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST]:
                  createActionConfig(serviceGroups.effectHandlers.getServiceGroupsTreeRequest),

		[serviceGroups.ACTION_TYPES.GET_SERVICE_GROUP_TREE_REQUEST_SUCCEEDED]:
                  createActionConfig(serviceGroups.effectHandlers.handleGetServiceGroupsTreeResponse),

		[serviceGroups.ACTION_TYPES.SERVICE_GROUP_TREE_RESOURCE_STATE_UPDATED]:
                  createActionConfig(serviceGroups.effectHandlers.createServiceGroupsTreeModel),

		/**
         * On Tilesmap Format changed (We need this in order to create again the
         * serviceGroup model to reflect the changes made in tilemapFormatConf
         */
		 [CONSTANTS.ACTION_TYPES.TILEMAPS_FORMAT_SET]:
                   createActionConfig(serviceGroups.effectHandlers.createServiceGroupsTreeModel),

        [CONSTANTS.ACTION_TYPES.NOC_POPULATION_GROUPS_SET]:
            createActionConfig(serviceGroups.effectHandlers.createServiceGroupsTreeModel),

		/**
         * On Service Group Tree item clicked/changed
         */
		["NOW_CONTENT_TREE#ACTIONABLE_ITEM_CLICKED"]:
                  createActionConfig(serviceGroups.effectHandlers.updateUserSelectedServiceGroup),

		/**
         * On Breadcrumb Service Group item clicked/changed
         */
		[serviceGroups.ACTION_TYPES.SERVICE_GROUP_SELECT_DIFFERENT_GROUP]:
                  createActionConfig(serviceGroups.effectHandlers.updateUserSelectedServiceGroup)
	}
};