import {CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty} from 'lodash';

const searchNode = (node, arr, selectedNode) => {
	if (!node || _isEmpty(node))
		return false;

	const {data, data: {id}, children} = node;
	const {serviceGroupSysId} = selectedNode;
	arr.push(data);

	if (id === serviceGroupSysId) {
		selectedNode.ref = node; //Set the ref for future usage (e.g finding the subTree beneath it)
		return true;
	}

	for (const child of children) {
		const {data: {type}} = child;
		if (type !== 'GROUP')
			continue;

		const isOneOfTheChildren = searchNode(child, arr, selectedNode);
		if (isOneOfTheChildren)
			return true;
	}

	arr.pop();
	return false;
};

export const constructBreadcrumb = function (tree, serviceGroupSysId) {
	if (!tree || !serviceGroupSysId)
		return null;

	const items = [];
	const selectedNode = {serviceGroupSysId, ref:null};
	const isExists = searchNode(tree, items, selectedNode);
	if (!isExists) {
		LOGGING.warn(`NOC: constructBreadcrumb didn't find the Service Group[${serviceGroupSysId}] within the tree`, tree);
		return null;
	}

	return {
		items,
		selectedNodeRef : selectedNode.ref
	};
};