import '@servicenow/now-heading';
import '@servicenow/now-alert';
import '../noc-severity-indicators-bar';
import '../noc-breadcrumb';
import '@devsnc/library-opws-mapping-shared/src/components/sn-noc-notifications';
import SPECTRUM_CONST from './snNocSpectrumConsts';
import { CONSTANTS, LOGGING } from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';
import { getButtonsJsx } from './snNocSpectrumButtonsJsx';
import { getSettingsModal } from './settingsModalJsx';
import { getUserViewSaveAsModal } from '../sn-noc-views/userViewSaveAsModalJsx';
import { t } from 'sn-translate';
import { isEmpty as _isEmpty , get as _get} from 'lodash';
import { isViewExists } from '../noc-user-view-behaviors/userViewsUtils';


const VIEW_NAME_REGEX = /^(.*?)(\-)*(\d*)$/;

const getInitialViewName = (viewName, viewsList) => {
	const TRIES_LIMIT = 100;
	for (var suffix = 1; suffix < TRIES_LIMIT; suffix++) {
		var name;
		const numberSuffix = VIEW_NAME_REGEX.exec(viewName);
		if (!_isEmpty(numberSuffix[2])) {
			name = numberSuffix[1] + '-' + (+numberSuffix[3] + suffix);
		} else {
			name = numberSuffix[0] + '-' + suffix;
		}
		if (!isViewExists(name, viewsList)) {
			return name;
		}
	}
	return '';
}

const generateSettingsModal = (tilemapsFormat, dispatch) => {
	const selectedItem = [tilemapsFormat.tileSize];
	return getSettingsModal({ selectedItem, dispatch });
};

const generateUserViewSaveAsModal = (userViewSaveAsModal, dispatch, viewNameSuggestion) => {
	const { isValid, errors } = userViewSaveAsModal;
	LOGGING.info(`Generate user view isValid? ${isValid}`);
	return getUserViewSaveAsModal({ dispatch, isValid, errors, viewNameSuggestion });
};

/**
 * Fix default user view name to capital case (Default)
 * @param viewName
 * @param userId
 * @returns {string}
 */
const fixUserViewName = (viewName,  userId) =>
	(userId === null && viewName === VIEW_CONSTANTS.DEFAULTS.DEFAULT_USER_VIEW_NAME) ?
		viewName[0].toLocaleUpperCase() + viewName.slice(1) : viewName;

export const view = (state, dispatch) => {
	const { population } = state.properties;
	const serviceText = t('{0} services', population.length);
	const {
		tilemapsFormat = {},
		userViewId,
		filter,
		settingsModal = {},
		userViewSaveAsModal = {},
		notifications = [],
		viewsList,
		noChangesOnCurrentView,
		selectedView
	} = state.properties;
	const currentView = viewsList[selectedView];

	const currentViewName = _get(currentView, 'view_name.value', '');
	const currentViewUserId = _get(currentView, 'user_id.value', null);

	const settingsModalJsx = settingsModal.isOpen ? generateSettingsModal(tilemapsFormat, dispatch) : null;
	var userViewSaveAsModalJsx = null;
	if (userViewSaveAsModal.isOpen) {
		const viewNameSuggestion = getInitialViewName(currentViewName, viewsList);
		userViewSaveAsModalJsx = generateUserViewSaveAsModal(userViewSaveAsModal, dispatch, viewNameSuggestion);
	}

	const buttonsJsx = getButtonsJsx({ userViewId, filter, dispatch, noChangesOnCurrentView, currentView });

	const severities = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/severities`;
	const severityFilter = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/severityFilter`;

	const displayUserViewName = fixUserViewName(currentViewName, currentViewUserId);

	return (
		<div className='noc-severity-bar' role="banner">
			<span className='full-title-width'>
				<now-heading
					has-no-margin
					variant='header-primary'
					label={displayUserViewName}
					className='view-name-title -right-Border' />
				<now-heading
					has-no-margin
					className='service-count'
					variant='header-secondary'
					label={serviceText} />

				{buttonsJsx}
				{settingsModalJsx}
				{userViewSaveAsModalJsx}
			</span>


			<span className="severity-breakdown-container">
				<label className='severity-breakdown'>{SPECTRUM_CONST.SEVERITY_BREAKDOWN}</label>
				<noc-severity-indicators-bar
					key='noc_severity_indicators_bar__key'
					component-id='noc-severity-indicators-indicators'
					severities={severities}
					population={population}
					severity-filter={severityFilter}
					className="severity-indicators-bar" />
			</span>

			<sn-noc-notifications
				component-id={CONSTANTS.DEFAULTS.NOC_APP_NOTIFICATIONS_COMPONENT_ID}
				className='notifications'
				notifications={notifications} />

		</div>
	);
};
