import {isObject as _isObject} from 'lodash';

export const transformState = state => {
	const {
		severities, 
		enabledPopulation = {length: 0}
	} = state.properties;

	if (!_isObject(severities))
		return state;

	const stateView = {
		...state,
		populationSize: enabledPopulation.length
	};
	return stateView;
};