import {LOGGING, checkForError, getValue} from '@devsnc/library-opws-mapping-shared';


const getMedian = numbers => {
    const sorted = numbers.slice().sort((a, b) => a - b);
    const middle = Math.floor(sorted.length / 2);

    if (sorted.length % 2 === 0)
        return Math.floor((sorted[middle - 1] + sorted[middle]) / 2);

    const median = Math.floor(sorted[middle]);
    return median;
};

const calculateMedian = (groups, maxAvailableSegments, start, end) => {
    const arr = [];

    //Prepare the input array for the median calculation
    //The array should contains all group's services count within the same targeted row
    for (let i = start; i < groups.length && i < end; i++) {
        const serviceNumber = groups[i].population.length;
        arr.push((serviceNumber <= maxAvailableSegments) ? maxAvailableSegments : serviceNumber);
    }

    const median = getMedian(arr);
    return median;
};

const calculateMaxServicesAllowed = (config, groups, segmentsChoicesCount, start, end) => {
    //Calculate the median services per a specific row of groups
    const median = calculateMedian(groups, segmentsChoicesCount, start, end);
    const maxServicesInGroup = (median > config.maxItems) ? config.maxItems : median;
    LOGGING.info(`calculateMaxServicesAllowed`, {
        caller: 'Dynamic Layout',
        extra: {row: (start / config.columns), config, median, maxServicesInGroup}
    });

    return maxServicesInGroup;
};

const fixSegmentsSize = (segments, selectedGroup, maxServicesInGroup, segmentsChoicesCount, canSegmentsDividedEqually) => {
    //Determine how many service should resides within each segment
    const segmentSize = (canSegmentsDividedEqually) ? segmentsChoicesCount : segments.length;
    const itemsPerSegment = Math.floor(maxServicesInGroup / segmentSize);

    segments.forEach(s => {
        s.segmentTotalItemsCount = s.items.length;
        s.items.length = (selectedGroup) ? s.segmentTotalItemsCount : itemsPerSegment;
        s.segmentItemsCount = (selectedGroup) ? s.segmentTotalItemsCount : itemsPerSegment;
        s.isExpendable = (selectedGroup) ? false : (s.segmentTotalItemsCount > itemsPerSegment);
    });
};


const createSegments = (sortByField, groupData) => {
    let lastSegmentType = null;
    const segments = groupData.population.reduce((segments, service) => {
        let lastSegment = segments[segments.length - 1];
        const segmentType = getValue(service[sortByField]) || 'empty_segment';

        if (!lastSegmentType || lastSegmentType !== segmentType) {
            lastSegmentType = segmentType;
            //Start a new Segment for the category
            segments.push({
                segmentType,
                items: [service],
            });
        } else {
            //If we are still in the same segment category just distribute it into the right row
            lastSegment.items.push(service);
        }
        return segments;
    }, []);

    return segments;
};

const createGroup = (groupData, groupIndex, sortByField, selectedGroup, maxServicesInGroup, segmentsChoicesCount, canSegmentsDividedEqually) => {
    const {groupField, population} = groupData;
    const segments = createSegments(sortByField, groupData);
    fixSegmentsSize(segments, selectedGroup, maxServicesInGroup, segmentsChoicesCount, canSegmentsDividedEqually);

    const group = {
        segmentedBy: sortByField, //We need this to avoid runtime race conditions between the tilemapformat update and this model
        groupField,
        segments,
        totalServicesCount: population.length,
        groupIndex
    };
    group.itemsCount = group.segments.map(s => s.items.length).reduce((a, b) => a + b , 0);
    return group;
};


const createRow = (sortByField, canSegmentsDividedEqually, selectedGroup, choicesFieldsModel, groups, config, start, end) => {
    //OOTB we have severity & business_critically as segmented
    const isGroupSegmented = choicesFieldsModel.hasOwnProperty(sortByField);

    //We can calculate the number of existing segments according to the number of options they have within choicesFieldsModel
    const segmentsChoicesCount = isGroupSegmented ? Object.keys(choicesFieldsModel[sortByField]).length : 1;

    //The amount of services to be displayed in one group complying median calculation and MAX,MIN limitations
    const maxServicesInGroup = calculateMaxServicesAllowed(config, groups, segmentsChoicesCount, start, end);

    const row = {
        rowItemsCount: 0,
        rowTotalItemsCount: 0,
        items: []
    };

    for (let i = start; i < groups.length && i < end; i++) {
        const group = createGroup(groups[i], i, sortByField, selectedGroup, maxServicesInGroup, segmentsChoicesCount, canSegmentsDividedEqually);
        row.items.push(group);
        row.rowItemsCount += group.itemsCount;
        row.rowTotalItemsCount += groups[i].population.length;
    }
    return row;
};


/**
 * Generate a grid grid which represent the layout of the tilesmap groups
 * @param tilemapsFormat
 * @param canSegmentsDividedEqually
 * @param selectedGroup
 * @param choicesFieldsModel
 * @param groups - the raw list of groups
 * @param windowSize
 * @param config - the best suitable configuration settings for the current user viewport
 * @returns {Object} - The grid data contains the groups
 * example:
 * Grid = {
 *  columnMaxWidth: 420,
 *  gridServicesCount: 0,
 *  gridTotalServicesCount:0,
 *  items:[{
 *           //Group metadata...
 *           segmentedBy: "busines_criticality",
 *           calculatedTileSize: small | large
 *           segments:[{
 *              //Segment metadata
 *              isExpendable : true,
 *              rows: [{
 *                  //Row metadata
 *                  items:[//Services]
 *              }]
 *           }]
 *        }],
 * }
 */
const createGrid = (tilemapsFormat, canSegmentsDividedEqually, selectedGroup, choicesFieldsModel, groups, windowSize, config) => {
    const {tileSize, sortByField} = tilemapsFormat;
    //Generate Grid's data segments etc.
    //by iterating the raw list of groups in steps(the number of columns)
    const grid = {
        columnMaxWidth: Math.floor(windowSize.width / config.columns),
        gridServicesCount: 0,
        gridTotalServicesCount: 0,
        rows: []
    };

    //Generate Grid's data model,
    //by iterating the raw list of groups in steps(the number of columns)
    for (let i = 0; i < groups.length; i += config.columns) {
        const row = createRow(sortByField, canSegmentsDividedEqually, selectedGroup, choicesFieldsModel, groups, config, i, (i + config.columns));
        grid.rows.push(row);
        grid.gridServicesCount += row.rowItemsCount;
        grid.gridTotalServicesCount += row.rowTotalItemsCount;
    }

    //Could be one of small/large/auto
    const autoTileSizeDetection = (grid.gridServicesCount < config.breakpoint) ? 'large' : 'small';
    const calculatedTileSize = (tileSize === 'auto') ? autoTileSizeDetection : tileSize;
    grid.rows.forEach(row => row.items.forEach(group => group.calculatedTileSize = calculatedTileSize));

    LOGGING.info("createGrid calculatedTileSize", {
        caller: 'Dynamic Layout',
        extra: {
            grid,
            tileSize,
            calculatedTileSize
        }
    });

    return grid;
};


export const transformState = state => {
    const {
        properties: {
            windowSize,
            tilemapsFormat,
            groups,
            choicesFieldsModel,
            selectedGroup,
            dynamicLayoutConfig,
            canSegmentsDividedEqually
        },
    } = state;

    if (!windowSize || !groups || !choicesFieldsModel || !dynamicLayoutConfig)
        return state;


    //Searching in the configuration table the first config that fits the current user viewport
    let config = dynamicLayoutConfig.find(c => c.width >= windowSize.width && c.height >= windowSize.height);
    if (!config) {
        LOGGING.error(`Can't find Dynamic Layout Configuration that comply resolution[${windowSize.width} ,${windowSize.height}]`);
        return state;
    }

    let filteredGroups;

    if (selectedGroup) {
        //In case of selectedGroup or when NO_GROUPING mode ->
        // then it means we need to eliminate all the limitations:
        config = {
            ...config,
            maxItems: Number.MAX_SAFE_INTEGER,
            columns: 1,
        };
        filteredGroups = groups.filter(g => g.groupField.displayValue === selectedGroup)
    } else {
        //Show default layout
        filteredGroups = groups;
    }

    const grid = createGrid(tilemapsFormat, canSegmentsDividedEqually, selectedGroup, choicesFieldsModel, filteredGroups, windowSize, config);
    return {...state, grid};
};
