import { CONSTANTS, createNocElement, createActionConfig } from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';
import SPECTRUM_CONST from './snNocSpectrumConsts';
import styles from './snNocSpectrumContainer.scss';
import { view } from './snNocSpectrumContainerView';
import snNocSpectrumContainerEffect from './snNocSpectrumContainerEffect';
import settingsModalEffect from './settingsModalEffect';
import userViewSaveAsModalEffect from '../sn-noc-views/userViewSaveAsModalEffect';
import { isViewExists } from '../noc-user-view-behaviors/userViewsUtils';
import { t } from 'sn-translate';


createNocElement('sn-noc-spectrum-container', {
	view,
	styles,
	onBootstrap(host, dispatch) {
		dispatch(CONSTANTS.ACTION_TYPES.NOC_COMPONENT_CONNECTED, {}, { stopPropagation: true });
	},
	properties: {
		population: {
			default: []
		},
		severities: {
			default: null
		},
		tilemapsFormat: {
			default: null
		},
		userViewId: {
			default: null
		},
		filter: {
			default: null
		},

		settingsModal: {
			default: {
				isOpen: false
			}
		},

		userViewSaveAsModal: {
			default: {
				isOpen: false,
				errors: null,
				isValid: true
			}
		},

		notifications: {
			default: []
		},



		viewsList: {
			default: null
		},

		selectedView: {
			default: null
		},

		noChangesOnCurrentView:{
			default: true
		}
	},

	actionHandlers: {
		[snNocSpectrumContainerEffect.ACTION_TYPES.MODAL_STATE_CHANGED]:
			createActionConfig(snNocSpectrumContainerEffect.effectHandlers.toggleModalEffect),

		[settingsModalEffect.ACTION_TYPES.SETTINGS_MODAL_STATE_CHANGED]:
			createActionConfig(settingsModalEffect.effectHandlers.toggleSettingsModalEffect),

		[settingsModalEffect.ACTION_TYPES.DROPDOWN_STATE_CHANGED]:
			createActionConfig(settingsModalEffect.effectHandlers.dropdownValueChangedEffect),

		[VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_MODAL_STATE_CHANGED]:
			createActionConfig(userViewSaveAsModalEffect.effectHandlers.toggleUserViewSaveAsModalEffect),

		[VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_NAME_CHANGED]:
			createActionConfig(userViewSaveAsModalEffect.effectHandlers.userViewNameChangedEffect),

		[VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_REQUESTED]:
			createActionConfig(userViewSaveAsModalEffect.effectHandlers.saveUserViewSaveAsModalEffect),

		['NOW_INPUT#INVALID_SET']:
			(coeffects) => {
				const { updateProperties, properties: { userViewSaveAsModal }, action: { payload: { value ,fieldValue} } } = coeffects;
				const isValid = (fieldValue.length>40)?false:!value;
				const errors = !isValid ? { status: 'critical', content: t('View name must be between 1-40 characters.'), icon: 'exclamation-circle-outline' } : null;
				updateProperties({ userViewSaveAsModal: { ...userViewSaveAsModal, isValid, errors } });
			},
		['NOW_INPUT#VALUE_SET']:
			(coeffect) => {
				const { action: { payload: { value } }, updateProperties, properties: { userViewSaveAsModal, viewsList } } = coeffect;
				const isValid = !isViewExists(value, viewsList);
				const errors = !isValid ? { status: 'critical', content: t('Name is already in use. Choose a different view name.'), icon: 'exclamation-circle-outline' } : null;
				updateProperties({ userViewSaveAsModal: { ...userViewSaveAsModal, isValid, errors } });
			}

	}
});