import {createHttpEffect} from '@servicenow/ui-effect-http';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {CONSTANTS, checkForError, onNextFrame, createNocUpdateStateEffect} from '@devsnc/library-opws-mapping-shared';

import {isUndefined as _isUndefined} from 'lodash';

const isDefined = value => (value !== null && !_isUndefined(value));

const ACTION_TYPES = {
	SERVICE_PREVIEW_REQUESTED: CONSTANTS.ACTION_TYPES.SERVICE_PREVIEW_REQUESTED,
	REFRESH_SERVICE_PREVIEW_REQUESTED: CONSTANTS.ACTION_TYPES.REFRESH_SERVICE_PREVIEW_REQUESTED,
	SERVICE_PREVIEW_REQUEST: 'SERVICE_PREVIEW_REQUEST',
	SERVICE_PREVIEW_REQUEST_STARTED: 'SERVICE_PREVIEW_REQUEST_STARTED',
	SERVICE_PREVIEW_REQUEST_SUCCEEDED: 'SERVICE_PREVIEW_REQUEST_SUCCEEDED',
	SERVICE_PREVIEW_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,//'SERVICE_PREVIEW_REQUEST_FAILED'
};

const refreshServicePreviewRequested = coeffects => {
	const {action, state, dispatch} = coeffects;

	const {
		serviceId, 
		tilemapId, 
		succeeded,
		timestamp: displayedPreviewTimestamp
	} = (state.servicePreviewResource || {});

	if (!succeeded || !serviceId) {
		LOGGING.log('refreshServicePreviewRequested: no data about currently displaying service preview. Skipping refresh.');
		return;
	}

	const {timestamp: requestedTimestamp} = action.payload;

	if (!requestedTimestamp) {
		LOGGING.log('refreshServicePreviewRequested: Expected "timestamp" in payload, got ', requestedTimestamp);
		return;
	}

	if (requestedTimestamp === displayedPreviewTimestamp) {
		LOGGING.log('refreshServicePreviewRequested: displayed preview timestamp is the same as requested. Skipping refresh ', requestedTimestamp);
		return;
	}

	dispatch(ACTION_TYPES.SERVICE_PREVIEW_REQUESTED, {serviceId, tilemapId, timestamp: requestedTimestamp});
};

const servicePreviewRequestedHandler = async ({action, properties, dispatch, updateState}) => {
	const {workspace} = properties;

	const {
		serviceId,
		tilemapId,
		timestamp} = action.payload;

	LOGGING.info(`Called to open service preview for service ${serviceId} / ${tilemapId} (${timestamp})`, {caller: 'getServicePreviewEffect.servicePreviewRequestedHandler'});

    //temporary removing workspace for WS integration

	if (!serviceId || !workspace || !timestamp) {
		LOGGING.warn(['servicePreviewRequestedHandler missing parameters: serviceId: ', serviceId,
			' workspace: ', workspace,
			' timestamp: ', timestamp]);
		return;
	}

	updateState({
		servicePreviewResource: {
			serviceId,
			tilemapId,
			timestamp
		}
	});

	await onNextFrame();

	dispatch(ACTION_TYPES.SERVICE_PREVIEW_REQUEST, {serviceId, workspace, timestamp});
};

const servicePreviewHandler = createHttpEffect('/api/sn_nocpit/nocpit/EMServicePreview/:serviceId', {
	method: 'GET',
	pathParams: ['serviceId'],
	queryParams: ['workspace', 'timestamp'],
	startActionType: ACTION_TYPES.SERVICE_PREVIEW_REQUEST_STARTED,
	successActionType: ACTION_TYPES.SERVICE_PREVIEW_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.SERVICE_PREVIEW_REQUEST_FAILED
});

/**
 *
 * Notes: empty display string can be an acceptable value
 * @param field
 * @returns {string|*}
 */
const getDisplayName = field => {
	// empty string can be an acceptable value
	return isDefined(field.display_name) ? field.display_name : field.name;
};

/**
 *
 * Notes: empty display string can be an acceptable value
 * @param field
 * @returns {string|*}
 */
const getDisplayValue = field => {
	// empty string can be an acceptable value
	return isDefined(field.display_value) ? field.display_value : field.value;
};

const transformOverviewFields = field => {
	
	const {name, value} = field;
	return {
		name,
		value,
		displayName: getDisplayName(field),
		displayValue: getDisplayValue(field)
	}
};

const transformResponse = response => {
	const overviewFields = response.overviewFields.map(transformOverviewFields);

	return {
		...response,
		overviewFields
	};
};

const servicePreviewResponseHandler = async coeffects => {
	LOGGING.info('servicePreviewResponseHandler');
	const {
		state,
		dispatch,
		updateState, 
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'servicePreviewResponseHandler'});
	if (errors)
		return;
	
	const {
		serviceId,
		tilemapId,
		timestamp
	} = (state.servicePreviewResource || {});

	const receivedServiceId = payload.sysId || '';
	
	if (serviceId !== receivedServiceId) {
		LOGGING.info(`servicePreviewResponseHandler: waiting for data on service ${serviceId}, 
		got service ${receivedServiceId}. 
		Skipping the data.`);

		return;
	}
	
	const servicePreviewData = transformResponse(payload || {});

	updateState({
		servicePreviewResource: {
			succeeded: true,
			serviceId,
			tilemapId,
			timestamp
		}
	});

	updateProperties({
		servicePreviewData: { //selectable
			tilemapId,
			...servicePreviewData 
		}
	});

	await onNextFrame();

	dispatch(CONSTANTS.ACTION_TYPES.NOC_SERVICE_PREVIEW_SET, {});
};

const effectHandlers = {
	servicePreviewRequestedHandler,
	refreshServicePreviewRequested,
	servicePreviewHandler,
	servicePreviewResponseHandler
};

export default {
	ACTION_TYPES,
	effectHandlers
};