import {createHttpEffect} from '@servicenow/ui-effect-http';
import {
	CONSTANTS,
	LOGGING,
	createNocUpdateStateEffect,
	formatComplexFieldsToCamelCase,
	onNextFrame,
	checkForError
} from '@devsnc/library-opws-mapping-shared';

import {
	assign as _assign, 
	isArray as _isArray, 
	isUndefined as _isUndefined, 
	mapValues as _mapValues
} from 'lodash';

const ACTION_TYPES = {
	NOC_POPULATION_REFRESH_REQUESTED: CONSTANTS.ACTION_TYPES.NOC_POPULATION_REFRESH_REQUESTED,
	BASE_POPULATION_REQUEST: 'BASE_POPULATION_REQUEST', //TODO implement REST endpoint
	EM_POPULATION_REQUEST: 'EM_POPULATION_REQUEST',
	POPULATION_REQUEST_STARTED: 'POPULATION_REQUEST_STARTED',
	EM_POPULATION_REQUEST_SUCCEEDED: 'EM_POPULATION_REQUEST_SUCCEEDED',
	BASE_POPULATION_REQUEST_SUCCEEDED: 'BASE_POPULATION_REQUEST_SUCCEEDED',
	POPULATION_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const getPopulationParams = (filter, workspace, currTimestamp) => {
	const query = filter ? String(filter) : 'sys_idISNOTEMPTY';
	
	let timestamp = _isUndefined(currTimestamp) ? -1 : currTimestamp;
	
	return {
		query,
		workspace,
		timestamp
	};
};

/**
 * This handler called to trigger getPopulation.
 * The effect decides which REST API to apply (EM or BASE population).
 * If nocConfig or uberFilter are not ready, then effect does nothing.
 */
const getPopulationHandler = createNocUpdateStateEffect((state, action, getAfterStateUpdate) => {
	LOGGING.log(`getPopulationHandler called by action ${action.type}`);
	
	const {
		nocConfig = null,
		uberFilter = null,
		workspace,
		timestampForTiles
	} = state.properties;

	// When this flag is true, the population will be updated regardless the timestamp
	const {forcePopulationUpdate = false} = action.payload;

	//wait for required properties.
	//wait until changeNotificator has initialized the timestamp.
	if (timestampForTiles === '-1' || !nocConfig || !uberFilter)
		return state;
		
	//Dealing with duplicate calls from same cycle of changes. Not likely to happen.
	if (!forcePopulationUpdate && state.lastUpdatedPopulationTimestamp === timestampForTiles)
		return state;
	
	//ready to fetch population, let's do it!
	const {filter = ''} = uberFilter;
	const queryParams = getPopulationParams(filter, workspace, timestampForTiles);
	const {
		isEm = false
	} = nocConfig;
	
	LOGGING.log(`getPopulationHandler ready to fetch population. 
		isEm: ${isEm}, 
		timestamp: ${timestampForTiles}`);
	
	const populationRequestAction = isEm ?
		ACTION_TYPES.EM_POPULATION_REQUEST :
		ACTION_TYPES.BASE_POPULATION_REQUEST; //TODO implement REST endpoint for BASE_

	const newState = {
		...state,
		lastUpdatedPopulationTimestamp: timestampForTiles
	};

	getAfterStateUpdate().then(dispatch =>
		dispatch(populationRequestAction, queryParams));

	return newState;
});

const getEmPopulationEffectHandler = createHttpEffect('/api/sn_nocpit/nocpit/EMServices', {
	method: 'GET',
	queryParams: ['query', 'workspace', 'timestamp'],
	startActionType: ACTION_TYPES.POPULATION_REQUEST_STARTED,
	successActionType: ACTION_TYPES.EM_POPULATION_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.POPULATION_REQUEST_FAILED
});

const formatPopulationResult = population => {
	if (!_isArray(population))
		return [];

	//instead immutable approach, transforming population items to prevent double size memory consumption.
    const populationMapper = population.reduce((mapper, item, idx) => {
        //flattening TileDTO to access dynamicFields transparently
        _assign(item, item.dynamicFieldsPlain);
        delete item.dynamicFieldsPlain;

        _assign(item, item.dynamicFieldsComplex);
        delete item.dynamicFieldsComplex;

        item = _mapValues(item, formatComplexFieldsToCamelCase);

		mapper[item.id] = item;
		population[idx] = item;
        return mapper;
	}, {});
	
	return {population, populationMapper};
};

const handleEmPopulationResponse = async coeffects => {
	LOGGING.log('handleEmPopulationResponse');

	const {
		dispatch,
		updateState,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleEmPopulationResponse'});
	if (errors)
		return;

	const {population, populationMapper} = formatPopulationResult(payload || []);

	updateState({populationMapper});
	updateProperties({population});

	await onNextFrame();
	dispatch(CONSTANTS.ACTION_TYPES.NOC_POPULATION_SET, {});
	dispatch(CONSTANTS.ACTION_TYPES.NOC_POPULATION_SET_PHASE_2, {});
};

//NOT IN USE. Waiting for EM agnostic REST API implementation
const handleBasePopulationResponse = coeffects => {
	LOGGING.log('handleBasePopulationResponse');

	const {
		dispatch,
		updateState,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleBasePopulationResponse'});
	if (errors)
		return;

	const {population, populationMapper} = formatPopulationResult(payload || []);

	updateState({populationMapper});

	updateProperties({
		population,
		enabledPopulation: population
	});
};

const effectHandlers = {
	getPopulationHandler,
	getEmPopulationEffectHandler,
	//TODO getBasePopulationHandler
	handleEmPopulationResponse,
	handleBasePopulationResponse
};

export default {
	ACTION_TYPES,
	effectHandlers
};
