import {dismissPill, view} from './snNocPillsBarView';
import styles from './snNocPillsBar.scss';
import {CONSTANTS, createNocElement} from '@devsnc/library-opws-mapping-shared';
createNocElement('sn-noc-pills-bar', {
	view,

	styles,
  
    actionHandlers: {
        'NOW_PILL#DISMISSED': coeffects => {
            dismissPill(coeffects);
        }
    },
	properties: {
        selectedAlert: {
			default: null
        },
        severities: {
            default: null
        }
	}
});