import {LOGGING, checkForError} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
    TILEMAP_SELECT_REGULAR_GROUP: 'TILEMAP_SELECT_REGULAR_GROUP'
};

/**
 * This method affects on the drilled down group within the regular services
 * @type {{effect: nocUpdateStateEffect, args: *[]}}
 */
const selectSpecificGroup = coeffects => {
	const {
		dispatch,
		properties,
		updateState,
		action: {payload, type}
	} = coeffects;

	LOGGING.log(`nocTilemapSelectGroupEffect->selectSpecificGroup called by action ${type}`);

	const errors = checkForError({payload, dispatch, caller: 'selectSpecificGroup'});
	if (errors)
		return;

	const {tilemapsFormat: {businessServiceGroup}} = properties;
	const {name : selectedGroup} = payload;
	updateState({selectedGroup});
};


const effectHandlers = {
	selectSpecificGroup
};
export default {
	ACTION_TYPES,
	effectHandlers
};
