import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CHANGE_EVENT_CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {keys as _keys, omit as _omit} from 'lodash';

const {GLOBAL_ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;

/**
 * TODO: refactor, rewrite the flow.
 * `prop` and `previousChanges` store component state in global variables and use it asynchronously.
 * In addition, function `doCall` reuses same `dispatch` for multiple async updates of state.
 * This is not promised to work by design in Seismic.
 * Danger of state overwriting in future versions of Seismic.
 */

let previousChanges = {'changes': {},'consumers':{}};
let prop = {'changes': {},'consumers':{}};
//keeping id of current `setTimeout`,
//to protect against duplicate `setTimeout` invocations for `isChanged` request.
let timeoutId = null;

const ACTIONS_TYPES = {
	NOC_ON_READ_CHANGES:          'NOC_ON_READ_CHANGES',
	NOC_ON_START_CHANGES:         'NOC_ON_START_CHANGES',
	NOC_ON_READ_CHANGES_QUERY:    'NOC_ON_READ_CHANGES_QUERY',
	NOC_ON_READ_CHANGES_SUCCESS:  'NOC_ON_READ_CHANGES_SUCCESS',
	NOC_ON_READ_CHANGES_ERROR:    'NOC_ON_READ_CHANGES_ERROR',
	NOC_DO_INIT:                  'NOC_DO_INIT',
	NOC_ON_START_CHANGE_TIMER:    'NOC_ON_START_CHANGE_TIMER',
	NOC_ON_STOP_CHANGE_TIMER:     'NOC_ON_STOP_CHANGE_TIMER'
};

const initPreviousChanges = (state,payload) => {
	const consumers = {
		...previousChanges.consumers,
		...payload.types
	};
	
	const pChanges = {
		...state,
		consumers,
		id: payload.id, 
		changes: {
			...previousChanges.changes,
			...payload.types
		}
	};
	previousChanges = pChanges;
};

const initChangeManager = (state,payload) => {
	const newState = {
		...state,
		consumers: {
			...prop.consumers
		},
		id: payload.id,
		changes: {
			...prop.changes
		}
	};

	if (!payload.changeAction || payload.changeAction  === 'add') {
		newState.consumers = {...newState.consumers, ...payload.types};
		newState.changes = {...newState.changes, ...payload.types};
	} else if (payload.changeAction === 'remove') {
		newState.consumers = _omit(newState.consumers, _keys(payload.types));
		newState.changes = _omit(newState.changes, _keys(payload.types));
	}

	return newState;
};

const getChangeRequestParams = (coeffects, payload) => {
	const {id, consumers} = payload.propChanges;
	const params = {types: consumers};
	const changeTypes = JSON.stringify(params);
	return {
		id,
		changeTypes
	};
};

const requestChangesCall = dispatch => () => {
	dispatch(ACTIONS_TYPES.NOC_ON_START_CHANGES, {propChanges: prop});
};

const stopCurrentTimeout = () => {
	if (timeoutId) {
		LOGGING.info('repeated change already running, resetting.', {caller: 'nocChangeEffect.stopCurrentTimeout'});
		clearTimeout(timeoutId);
		timeoutId = null;
	}
};

const REQUEST_START_CHANGE_TIMER = {
	effect(coeffects) {
		LOGGING.info('Called to start change timer', {caller: 'nocChangeEffect.REQUEST_START_CHANGE_TIMER'});

		const {dispatch} = coeffects;
		dispatch(CHANGE_EVENT_CONSTANTS.GLOBAL_ACTION_TYPES.NOC_INIT_CHANGE_MANAGER);
	}
};

const REQUEST_STOP_CHANGE_TIMER = {
	effect(coeffects) {
		LOGGING.info('Called to stop change timer', {caller: 'nocChangeEffect.REQUEST_STOP_CHANGE_TIMER'});

		const {
			state,
			action
		} = coeffects;

		const payload = {...action.payload, changeAction: 'remove'};

		stopCurrentTimeout();
		prop = initChangeManager(state, payload);
		initPreviousChanges(state, payload);
	}
};

const doCall = (dispatch) => {
	//extra safety to prevent any potential duplication of setTimeout
	stopCurrentTimeout();
	timeoutId = setTimeout(requestChangesCall(dispatch), prop.properties.nocConfig.nocRefreshRate);
};

const REQUEST_REPEATED_CHANGE_NOTIFICATION = {
	effect(coeffects) {
		const {
			dispatch, 
			state,
			action: {payload}
		} = coeffects;

		prop = initChangeManager(state, payload);
		initPreviousChanges(state, payload);

		stopCurrentTimeout();
		LOGGING.info('Initializing repeated change sequence.', {caller: 'nocChangeEffect.REQUEST_REPEATED_CHANGE_NOTIFICATION'});
		requestChangesCall(dispatch)();
	}   
};

/**
 * This handler called to trigger getChange.
 * The effect decides which REST API to apply.
 */
const REQUEST_CHANGE_NOTIFICATION =  {
	effect(coeffects) {
		const {payload} = coeffects.action;
		const {dispatch, state, action} = coeffects;

		timeoutId = null;
		const queryParams = getChangeRequestParams(coeffects, payload);
		LOGGING.info(`getChangeEffect called by action ${action.type}`, {caller: 'nocChangeEffect.REQUEST_CHANGE_NOTIFICATION', extra: queryParams.changeTypes});
		dispatch(ACTIONS_TYPES.NOC_ON_READ_CHANGES, queryParams);
	}
};

const CHANGE_RESPONSE_REQUEST_SENT = {
	effect(coeffects) {
		const {dispatch, state, action} = coeffects;

		LOGGING.info('Change response request sent', {caller: 'nocChangeEffect.CHANGE_RESPONSE_REQUEST_SENT', extra: action.type});
	 }
};

/**
 * Sets the property return by thge result.
 * @param {} coeffects
 * @param {*} changes 
 * @param {*} properties 
 */
const setChangeProperties = (coeffects) => {
	let  notificationMap  = [];
	const {state, action} = coeffects;
	const {properties}    = state;
	const {types}         = action.payload;
	if (typeof types == 'undefined') {
		return notificationMap;
	}
	const json = JSON.parse(types);
	let key = '';
	for (key in json) {
		if (previousChanges.changes[key] !== json[key]) {
			notificationMap.push({'name': key, 'value': json[key], 'prevValue': previousChanges.changes[key]});
			previousChanges.changes[key] = json[key];
		}
	}
	return notificationMap;
};

const notifyChanges = (coeffects, notificationList) => {
	const {dispatch} = coeffects;
   
	notificationList.forEach((value, key) => {
		LOGGING.info(`nocChangeEffect/notifyChanges: 
		name:       ${value.name} 
		value:      ${value.value}
		PrevValue:  ${value.prevValue}`);
		
		const changedItem = {
			type: value.name,
			value: value.value, 
			previous: value.prevValue
		};
		
		dispatch(GLOBAL_ACTION_TYPES.NOC_EVENT_HASH_CHANGED, changedItem);
	});
};

const CHANGE_RESPONSE_SUCCESS = coeffects => {
	const {
		dispatch
	} = coeffects;

	LOGGING.info('isChange success', {caller: 'nocChangeEffect.CHANGE_RESPONSE_SUCCESS'});
	doCall(dispatch); //schedule next call for changes
	const notificationList = setChangeProperties(coeffects);
	notifyChanges(coeffects, notificationList);
};

const CHANGE_RESPONSE_ERROR = coeffects => {
	const {
		dispatch
	} = coeffects;

	LOGGING.warn('isChange failure', {caller: 'nocChangeEffect.CHANGE_RESPONSE_ERROR'});
	doCall(dispatch); //schedule next call for changes
};

const REQUEST_CHANGE_NOTIFICATION_EFFECT_HANDLER = createHttpEffect('/api/now/it_service/isChanged', {
	method: 'GET',
	queryParams: ['id', 'changeTypes'],
	startActionType:    ACTIONS_TYPES.NOC_ON_READ_CHANGES_QUERY,
	successActionType:  ACTIONS_TYPES.NOC_ON_READ_CHANGES_SUCCESS,
	errorActionType:    ACTIONS_TYPES.NOC_ON_READ_CHANGES_ERROR
});

const effectHandlers = {
	REQUEST_STOP_CHANGE_TIMER,
	REQUEST_START_CHANGE_TIMER,
	REQUEST_REPEATED_CHANGE_NOTIFICATION,
	REQUEST_CHANGE_NOTIFICATION,
	REQUEST_CHANGE_NOTIFICATION_EFFECT_HANDLER,
	CHANGE_RESPONSE_REQUEST_SENT,
	CHANGE_RESPONSE_SUCCESS,
	CHANGE_RESPONSE_ERROR
};

export default {
	ACTIONS_TYPES,
	effectHandlers
}