import {LOGGING, CHANGE_EVENT_CONSTANTS, CONSTANTS} from '@devsnc/library-opws-mapping-shared';

const initChangeHandler = {
	effect(coeffects) {
		const {dispatch} = coeffects;
		const {PAYLOADS, GLOBAL_ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;

		LOGGING.info('dispatch ACTION_TYPES.NOC_ON_START_REPEATED_CHANGES', {caller: 'initChangeHandler', extra: PAYLOADS});
		dispatch(GLOBAL_ACTION_TYPES.NOC_ON_START_REPEATED_CHANGES, {...PAYLOADS.POPULATION_CHANGES, id: CONSTANTS.GLOBALS.ALL_SYS_ID});
	}
};

const effectHandlers = {
	initChangeHandler
};

export default {
	effectHandlers
};