import {invokeOnNextFrame} from '@devsnc/library-opws-mapping-shared';

/**
 * Once the popover opens and is visible, trigger a focus on it.
 */
const onPopoverAttach = vNode => invokeOnNextFrame(() => {
	const {elm: slot} = vNode;

	const slottedElm = (slot.assignedNodes() || []) [0];
	
	if (slottedElm)
		slottedElm.focus();
});

export const view = (state, dispatch) => {
	const {
		positionStyle = {}
	} = state.properties;

	return (
	   <div className='noc-popover'
			id='noc-popover'
			role='region'
			aria-live='polite'
			style={positionStyle}>
			
		   <slot hook-insert={onPopoverAttach}/>
	   </div>
	);
};