import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	MODAL_STATE_CHANGED: 'NOW_MODAL#OPENED_SET' //modal opened/closed
};

/**
 * When the settings modal opens or closes, this effect clears any notifications that stored on the modal level on
 * state.
 */
const toggleModalEffect = coeffects => {
	const {
		dispatch,
		action: {payload: {dispatches}}
	} = coeffects;

	if (!dispatches) {
		LOGGING.error(`Requested to toggle modal but no 'dispatches' value provided!`, {caller: 'snNocSpectrumContainerEffect.toggleModalEffect'});
		return;
	}

	LOGGING.info(`Toggle modal - dispatch [${dispatches}]`, {caller: 'snNocSpectrumContainerEffect.toggleModalEffect'});

	dispatch(dispatches, coeffects);
};


const effectHandlers = {
	toggleModalEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
}