import {getOrdinal} from '@devsnc/library-opws-mapping-shared';

/**
 *
 * @param field
 * @param ascending
 * @returns {function(*, *): number}
 */
export const comparatorByField = (field, ascending) => {
	if (ascending) {
		return (a, b) => ordinalComparator(a[field], b[field]);
	} else {
		return (a, b) => ordinalComparator(b[field], a[field]);
	}
};

/**
 *
 * @param a
 * @param b
 * @returns {number}
 * @constructor
 */
const ordinalComparator = (a, b) => {
	const ordA = getOrdinal(a);
	const ordB = getOrdinal(b);

	if (ordA > ordB) {
		return 1;
	} else if (ordB > ordA) {
		return -1;
	}

	return 0;
};
