import {searchFilterPopulation} from './transformers/populationFilter';
import {groupByPopulation} from './transformers/groupByPopulation';
import {sortByPopulation} from './transformers/sortByPopulation';
import {sortGroupsPopulation} from './transformers/sortGroupsPopulation';
import {impactedByAlertPopulation} from './transformers/impactedByAlertPopulation';
import {CONSTANTS, PERFORMANCE} from '@devsnc/library-opws-mapping-shared';
import {values as _values} from 'lodash';

/**
 *
 * The expected structure of the grouped population, e.g.:
 * [
 *      {
 *          "groupField": {
 *              "displayValue": "Info",
 *              "value": "5",
 *              "name": "severity"
 *      },
 *          "population": [
 *              {...}
 *          ]
 *      }
 * ]
 *
 * @param groupedPopulation
 * @param state
 * @returns {{[p: string]: *}}
 */
const transformGroupedPopulation = (groupedPopulation, state) => {
    let newGroupedPopulation = groupedPopulation;
    const {tilemapsFormat: {businessServiceGroup}} = state.properties;

    const transformersSet = (businessServiceGroup) ? serviceGroupTransformers: regularTransformers;
    transformersSet.forEach(fn => {
        newGroupedPopulation = fn(newGroupedPopulation, state);
    });

    const newState = {
        ...state,
        groupedPopulation: _values(newGroupedPopulation)
    };
    return newState;
};

/**
 *
 * @param state
 * @returns {*}
 */
export const transformState = (state) => {
    const {tilemapsFormat: {businessServiceGroup}, serviceGroups} = state.properties;
    let groupedPopulation;

    if (businessServiceGroup) {
        if (!serviceGroups || !serviceGroups.buckets)
            return state;

        groupedPopulation = serviceGroups.buckets;
    } else {
        groupedPopulation = state.properties.population;
    }


    // do nothing when no population.
    // empty population is a valid response.
    if (!groupedPopulation) {
        return state;
    }

    return PERFORMANCE.measureCall(transformGroupedPopulation, [groupedPopulation, state]);
};

/**
 * array of functions to pipeline by order
 * @type {*[]}
 */
const regularTransformers = [
    impactedByAlertPopulation, // filter out any service which is not in the list of services that are impacted by the alert
    searchFilterPopulation, //filter according to the search term
    groupByPopulation, // group according to the 'Group By' field
    sortByPopulation, // sort each group according to the 'Sort By' field
    sortGroupsPopulation // sort groups according to the 'groupByOrder' field
];




/**
 * array of functions to pipeline by order for Service Group Mode
 * @type {*[]}
 */
const serviceGroupTransformers = [
    impactedByAlertPopulation, // filter out any service which is not in the list of services that are impacted by the alert
    searchFilterPopulation, //filter according to the search term
    sortByPopulation, // sort each group according to the 'Sort By' field
    sortGroupsPopulation // sort groups according to the 'groupByOrder' field
];




