import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
    NOC_SEVERITY_INDICATOR_CLICKED: 'NOC_SEVERITY_INDICATOR#CLICKED' //When severity indicator clicked
};

const filterSeverities = coeffects => {
    const {action, dispatch, properties} = coeffects;
    const {severityClicked} = action.payload;
    const {enabledSeverities}  = properties.severityFilter || {};

    enabledSeverities[severityClicked] = !enabledSeverities[severityClicked];
    dispatch(CONSTANTS.ACTION_TYPES.SEVERITY_FILTER_SAVE_REQUESTED, {
        severityFilter: {enabledSeverities}
    });
};

export const effectHandlers = {
    filterSeverities,
};

export default {
    ACTION_TYPES,
    effectHandlers
};