import { get as _get, isEqual as _isEqual } from 'lodash';
import { transformToModel } from './transforms';
import { CONSTANTS, onNextFrame, checkForError, LOGGING } from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from './constants';

/**
 *
 * @param dispatch
 * @param selectableProps
 */
const updateUserViewMetaData = async (dispatch, selectableProps) => {
	await onNextFrame();

	if (selectableProps.uberFilter) {
		dispatch(CONSTANTS.ACTION_TYPES.UBER_FILTER_SET, { forcePopulationUpdate: true });
	}

	if (selectableProps.severityFilter) {
		dispatch(CONSTANTS.ACTION_TYPES.SEVERITY_FILTER_SET, {});
	}

	if (selectableProps.tilemapsFormat) {
		dispatch(CONSTANTS.ACTION_TYPES.TILEMAPS_FORMAT_SET, {});
	}

	if (selectableProps.alertPreferences) {
		dispatch(CONSTANTS.ACTION_TYPES.ALERT_PREFERENCES_SET, {});
	}
};

/**
 *
 * @param dispatch
 * @param nocViewsRecord
 */
// TODO merge with snNocViewsEffects.userViewSelectedEffect
const handleUserViewSwitch = (dispatch, nocViewsRecord) => {
	const {
		view_name: { value: viewName },
		user_id: { value: userId }
	} = nocViewsRecord;

	LOGGING.info(`Switching to new view '${viewName}' of user '${userId}'`, { caller: 'handleUserViewSwitch' });

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_SELECTED, { viewName, userId });
};

/**
 * HOC function to reuse code for userViewResponseInterceptor.
 * Created interceptor must be used with updateStateEffect.
 * Same effect used in GlideRecord_Query response,
 * and in insert and update GlideRecord_Mutation responses.
 * The only difference is the response format.
 * @param resultPath the path where the result is stored on the action.
 */
export const handleUserViewResponseEffect = resultPath => {
	return coeffects => {
		const caller = 'handleUserViewResponseEffect';
		LOGGING.info(`Received response [${resultPath}]`, { caller });

		const {
			dispatch,
			state,
			updateState,
			updateProperties,
			action,
			properties: { views }
		} = coeffects;

		const { payload } = action;

		const errors = checkForError({ payload, dispatch, caller });
		if (errors) {
			return;
		}

		const nocViewsRecord = _get(action, resultPath);
		const selectableProps = transformToModel(nocViewsRecord);

		const recordSysId = _get(nocViewsRecord, 'sys_id.value') || '';
		const originViewId = _get(nocViewsRecord, 'origin_view_id.value') || _get(state, 'userViewResource.originViewId') || recordSysId;
		const defaultViewSysId = _get(state, 'userViewResource.sys_id') || recordSysId;

		updateProperties(selectableProps);

		updateState({
			userViewResource: {
				sys_id: defaultViewSysId,
				originViewId
			}
		});


		//disable the save button there are no changes
		dispatch(VIEW_CONSTANTS.ACTION_TYPES.COMPARISON_SAVE_BUTTON_STATE, { current: originViewId, latest: defaultViewSysId });


		const viewIsCurrent = recordSysId === defaultViewSysId;
		const isNewView = views.itemsMap !== null && !views.itemsMap[recordSysId];
		const isInsertNew = resultPath === VIEW_CONSTANTS.INSERT_NOC_USER_VIEW_PATH;

		// view is not the default (current) and not found in views (items) map and as a result of an insert response (resultPath)
		if (!viewIsCurrent && isNewView && isInsertNew) {
			handleUserViewSwitch(dispatch, nocViewsRecord);
		} else {
			return updateUserViewMetaData(dispatch, selectableProps);
		}
	};
};
