import SPECTRUM_CONST from './snNocSpectrumConsts';
import settingsModalEffect from './settingsModalEffect';
import '../noc-global-filter';
import '../sn-noc-views';
import '@servicenow/now-tooltip';
import {CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';


const CONFIG_ARIA = {
	'aria-label': SPECTRUM_CONST.LABEL_SETTINGS
};

export const getButtonsJsx = args => {
	const {
		userViewId,
		filter,
		dispatch,
		noChangesOnCurrentView,
		currentView
	} = args;

	const openSettingsModal = dispatch => () => {
		LOGGING.info(`Open 'Settings' modal`, {caller: 'snNocSpectrumButtonsJsx.getButtonsJsx.openSettingsModal'});
		dispatch(settingsModalEffect.ACTION_TYPES.SETTINGS_MODAL_STATE_CHANGED, {value: true});
	};

	const views = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/views`;

	return (
		<div className='buttons'>
			<now-button-iconic
				tooltip-content={SPECTRUM_CONST.LABEL_SETTINGS}
				config-aria={CONFIG_ARIA}
				className='toolbar-button'
				icon='gear-outline'
				size='md'
				on-click={openSettingsModal(dispatch)}
			/>
			<noc-global-filter filter={filter} user-view-id={userViewId}/>
			<sn-noc-views views={views} user-view-id={userViewId} no-changes-on-current-view={noChangesOnCurrentView} current-view={currentView}/>
		</div>
	);
};