import {isEmpty as _isEmpty, mapKeys as _mapKeys} from 'lodash';

const impactedServicesFilter = function(service) {
	const {impactedServicesIdsMap} = this;
	const {id} = service;
	return Boolean(impactedServicesIdsMap[id]);
};

const getValueAsKey = (value, key) => value;

export const impactedByAlertPopulation = (population, state) => {
	const {tilemapsFormat: {businessServiceGroup}, impactedServicesAlertIds, selectedAlert} = state.properties;

	if (!population || (selectedAlert === null && _isEmpty(impactedServicesAlertIds))) {
		return population;
	}

	// Filter out any service which is not in the impacted services list
	const impactedServicesIdsMap = _mapKeys(impactedServicesAlertIds, getValueAsKey);
	let filteredPopulation;
	if (businessServiceGroup) {
		filteredPopulation = population.map(group => {
			const servicesFiltered = group.population.filter(impactedServicesFilter, {impactedServicesIdsMap});
			return {
				...group,
				population: servicesFiltered
			};
		});
	} else {
		filteredPopulation = population.filter(impactedServicesFilter, {impactedServicesIdsMap});
	}
	return filteredPopulation;
};