import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, LOGGING, checkForError} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	REMOVE_FROM_FAVORITES_REQUEST: 'REMOVE_FROM_FAVORITES_REQUEST',
	REMOVE_FROM_FAVORITES_REQUEST_STARTED: 'REMOVE_FROM_FAVORITES_REQUEST_STARTED',
	REMOVE_FROM_FAVORITES_REQUEST_SUCCEEDED: 'REMOVE_FROM_FAVORITES_REQUEST_SUCCEEDED',
	REMOVE_FROM_FAVORITES_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};




const removeFromFavoritesRequest = createHttpEffect('/api/sn_nocpit/nocpit/RemoveFromFavorites', {
	method: 'PUT',
	queryParams: ['workspace', 'ciId'],
	startActionType: ACTION_TYPES.REMOVE_FROM_FAVORITES_REQUEST_STARTED,
	successActionType: ACTION_TYPES.REMOVE_FROM_FAVORITES_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.REMOVE_FROM_FAVORITES_REQUEST_FAILED
});



const removeFromFavoritesResponse = coeffects => {
	LOGGING.log('removeFromFavoritesResponse');

	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'removeFromFavoritesResponse'});
	if (errors)
		return;

	const {favorite} = payload.result;

	updateProperties({
		path: 'servicePreviewData.favorite',
		value: favorite,
		operation: 'set'
	});
};


const effectHandlers = {
	removeFromFavoritesRequest,
	removeFromFavoritesResponse
};


export default {
	ACTION_TYPES,
	effectHandlers
};
