import '@servicenow/now-text-link';
import '@servicenow/now-icon';
import '@servicenow/now-label-value';
import nocBreadcrumbEffect from './nocBreadcrumbEffect';

const changeServiceGroup = (dispatch, item) => () => {
     dispatch(nocBreadcrumbEffect.ACTION_TYPES.NOC_BREADCRUMB_ITEM_CLICKED, item);
};

const generateBreadcrumb = (bucket, item, currentIndex) => {
    const {itemsCount, elements, dispatch} = bucket;

    if (currentIndex < itemsCount - 1) {
        elements.push(<now-text-link bare
                                     variant='primary'
                                     className='breadcrumb-link'
                                     label={item.name}
                                     href='javascript:void(0)'
                                     on-click={changeServiceGroup(dispatch, item)}/>);
        elements.push(<now-icon icon='chevron-right-outline'
                                className='breadcrumb-separator'
                                size='md'/>);
    } else {
        elements.push(<span aria-current="true">{item.name}</span>);
    }

    return bucket;
};


export const view = (state, dispatch) => {
    const {items} = state.properties.data;
    if (!items)
        return null;

    const breadcrumb = items.reduce(generateBreadcrumb, {
        itemsCount: items.length,
        elements: [],
        dispatch
    });
    return (
        <nav aria-label="Breadcrumb" className='breadcrumb'>
			{breadcrumb.elements}
		</nav>
    );
};
