import '@servicenow/now-button';
import '@servicenow/now-modal';
import '@servicenow/now-badge';

import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {t} from 'sn-translate';
import {get as _get} from 'lodash';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';

const {PROPERTIES_SET} = CONSTANTS.GENERAL;
const MODAL_HEADER = t('Filter services');
const TOOLTIP_FILTER = t('Filter services');

const openGlobalFilter = (state, dispatch) => () => {
	const val = state.properties.isModalOpen;
	dispatch(PROPERTIES_SET, {isModalOpen: !val});
};

const onRedirect = (elm, dispatch) => evt => {
	const url = _get(elm, 'contentWindow.location.href', '') || '';
	if (url.includes('redirected=true')) {
		dispatch(PROPERTIES_SET, {isModalOpen: false});
		dispatch(VIEW_CONSTANTS.ACTION_TYPES.CURR_GLOBAL_FILTER_CHANGED, {});
	} else if (url.includes('action=cancel')) {
		dispatch(PROPERTIES_SET, {isModalOpen: false});
	}
};

const onFrameInsert = dispatch => ({elm}) => {
	if (!elm) {
		return;
	}

	elm.addEventListener('load', onRedirect(elm, dispatch));
};

const createFilterModal = (userViewId, dispatch) => {
	const url = `/noc_user_view.do?sysparm_view=global_filter_view&sysparm_titleless=true&sysparm_skipmsgs=true&sys_id=${userViewId}`;
	const iframe = <iframe
		key='filter_modal_iframe'
		hook-insert={onFrameInsert(dispatch)}
		width='100%' frameBorder='0' height='400px'
		src={url}>
	</iframe>;

	return <now-modal
		opened
		size='lg'
		content-full-width
		header-label={MODAL_HEADER}
		content={iframe}/>;
};

export const view = (state, dispatch) => {
	const {isModalOpen, userViewId, filterSize} = state.properties;
	if (!userViewId) {
		return null;
	}

	const modal = isModalOpen ? createFilterModal(userViewId, dispatch) : null;
	const FILTER_BADGE_MESSAGE = filterSize === 0 ? TOOLTIP_FILTER : t('Show filters ({0} conditions)', filterSize);
	const filterBadge = filterSize > 0 ?
		<now-badge className='filter-badge'
		           round
		           size='sm'
		           status='info'
		           value={filterSize}
		           aria-label={FILTER_BADGE_MESSAGE}
		           variant='secondary'
		           on-click={openGlobalFilter(state, dispatch)}/>
		: null;

	const configAria = {
		'aria-label': FILTER_BADGE_MESSAGE
	};
	return (
		<Fragment>
			<now-button-iconic
				variant='secondary'
				size='md'
				config-aria={configAria}
				icon='filter-outline'
				on-click={openGlobalFilter(state, dispatch)}
				tooltip-content={FILTER_BADGE_MESSAGE}/>
			{filterBadge}
			{modal}
		</Fragment>
	);
};
