import '@servicenow/now-pill';
import {CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty, reduce as _reduce} from 'lodash';
import {t} from 'sn-translate';
import {SECTIONS} from "../noc-user-view-behaviors/transforms";

const SEVERITIES_LABEL = t('Severities');

const PILL_TYPES = {
    ALERT: 'alert',
    SEVERITIES: 'severities'
};

const createPillJsx = (type, data, label , className) => {
    return <now-pill
        append-to-payload={{type, data}}
        can-dismiss icon="list-fill"
        label={label}
        className={className}
        manage-selected size="sm"/>;
};

const pillAlertJsx = alert => {
    if (!alert)
        return null;

    return createPillJsx(PILL_TYPES.ALERT, alert, alert.number, 'pill');
};

const pillSeveritiesJsx = severities => {
    if (!severities)
        return null;

    const reduced = _reduce(severities, (result, val) => {
        const {isEnabled, label} = val;
        if (isEnabled) {
            result.pillLabel += `${label}, `;
            result.trueCounter++
        } else {
            result.falseCounter++
        }
        return result;
    }, {
        trueCounter: 0,
        falseCounter: 0,
        pillLabel: `${SEVERITIES_LABEL}: `
    });

    //true if at least one severity is enabled but not if all are
    const showPill = reduced.trueCounter > 0 && reduced.falseCounter > 0
    if (showPill) {
        reduced.pillLabel = reduced.pillLabel.slice(0, -2);
        return createPillJsx(PILL_TYPES.SEVERITIES, severities, reduced.pillLabel, 'circle-exclamation-outline');
    }
    else {
        return null;
    }

};

export const view = state => {
    const {selectedAlert, severities} = state.properties;

    const pillAlert = pillAlertJsx(selectedAlert);
    const pillSeverities = pillSeveritiesJsx(severities);

    return (pillAlert || pillSeverities ?
        <div className='sn-pills-bar now-p-block-start--lg now-p-inline--xl'>
          {pillAlert}
          {pillSeverities}
        </div> : null);
};

export const dismissPill = coeffects => {
    const {dispatch, action: {payload}} = coeffects;
    if (_isEmpty(payload)) {
        LOGGING.warn('Dismissed a pill without payload!');
        return;
    }

    switch (payload.type) {
        case PILL_TYPES.ALERT:
            dispatch(CONSTANTS.ACTION_TYPES.CLEAR_IMPACTED_SERVICES_REQUEST);
            break;
        case PILL_TYPES.SEVERITIES:
            const enabledSeverities = _reduce(payload.data, (result, val) => {
                result[val.value]= true;
                return result;
            }, {});
            dispatch(CONSTANTS.ACTION_TYPES.SEVERITY_FILTER_SAVE_REQUESTED, {
                severityFilter: {enabledSeverities}
            });
            break;
        default:
            LOGGING.warn(['Dismissed a pill with an unknown type=', payload.type]);
            return;
    }
};