import {t} from 'sn-translate';
import '@servicenow/now-modal';
import '@servicenow/now-heading';
import '../noc-service-groups-tree';
import {nocModalActionEffect} from '@devsnc/library-opws-mapping-shared';
import {isUndefined as _isUndefined} from 'lodash';

const {ACTION_TYPES: modalActionTypes} = nocModalActionEffect;

const footerAction = JSON.stringify([
	{
		variant: 'primary',
		label: t('Close'),
		isModalOpen: false,
		clickActionType: modalActionTypes.NOC_ACTION_CLICK
	}
]);

//Captions
const MODAL_HEADER = t('Service groups');

export const view = (state) => {
	const {
		populationSize,
		properties: {serviceGroups}
	} = state;

	if (_isUndefined(populationSize)) {
		return null;
	}

	const tree = <noc-service-groups-tree serviceGroups={serviceGroups}/>;

	return <now-modal
		opened
		size='lg'
		header-label={MODAL_HEADER}
		footer-actions={footerAction}>
		{tree}
	</now-modal>;
};


