import {values as _values, slice as _slice} from 'lodash';
import {getValue, getOrdinal, CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {comparatorByField} from '../comparators/ordinalComparator';

/**
 *
 * @param groupedPopulation
 * @param state
 * @returns {*}
 */
export const sortGroupsPopulation = (groupedPopulation, state) => {
    const {properties: {tilemapsFormat}} = state;
    const {businessServiceGroup: isServiceGroup} = tilemapsFormat;
    const isAscending =  tilemapsFormat.groupByOrder == CONSTANTS.GLOBALS.ASC;

    groupedPopulation = runSortGroupsStrategy(groupedPopulation, tilemapsFormat.groupByField, isAscending, isServiceGroup);

    return groupedPopulation;
};



// TODO there are constants under noc-tilemap-toolbar/src/constants.js which should be shared
/**
 * Return the groups array sorted by groupByOrder
 *
 * @param groupedPopulation
 * @param groupByField
 * @param isAscending
 * @returns {*}
 */
const runSortGroupsStrategy = (groupedPopulation, groupByField, isAscending, isServiceGroup) => {
    if (!groupByField) {
        return groupedPopulation;
    }

    let sortedGroupedPopulation = _values(groupedPopulation);
    if (sortedGroupedPopulation.length <= 1) {
        return groupedPopulation;
    }

    const hasOther = isServiceGroup || (sortedGroupedPopulation[0].groupField.value === '');
    const theOther = hasOther ? sortedGroupedPopulation[0] : null;
    const population = hasOther ? sortedGroupedPopulation.slice(1) : sortedGroupedPopulation;

    sortedGroupedPopulation = population.sort(comparatorByField('groupField', isAscending));

    if (hasOther) {
        sortedGroupedPopulation.push(theOther); //should be always on the end
    }

    return sortedGroupedPopulation;
};
