import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {CONSTANTS, LOGGING, checkForError, onNextFrame} from '@devsnc/library-opws-mapping-shared';
import {get as _get} from 'lodash';

const ACTION_TYPES = {
	USER_META_REQUEST: 'USER_META_REQUEST',
	USER_META_REQUEST_STARTED: 'USER_META_REQUEST_STARTED',
	USER_META_REQUEST_SUCCEEDED: 'USER_META_REQUEST_SUCCEEDED',
	USER_META_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
query getUserMeta{
  GlideDomain_Query {
    user {
      sys_id
      preferences(name: ["glide.ui.accessibility"]) {
        name
        value
      }
    }
  }
}
`;

const userMetaFetched = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.USER_META_REQUEST_STARTED,
	successActionType: ACTION_TYPES.USER_META_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.USER_META_REQUEST_FAILED,
});

const transform = result => {
	const userId = result.sys_id;
	const isA11y = true;
	return {
		userId,
		isA11y
	}
};

const handleUserMetaResponse = async coeffects => {
	LOGGING.log('handleUserMetaResponse');

	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleUserMetaResponse'});
	if (errors) 
		return;

	const record = _get(payload, 'data.GlideDomain_Query.user');
	const userMeta = transform(record);

	if (!userMeta)
		return;

	updateProperties({userMeta});
	
	await onNextFrame();
	dispatch(CONSTANTS.ACTION_TYPES.USER_META_SET, {});
};

const effectHandlers = {
	userMetaFetched,
	handleUserMetaResponse
};

export default {
	ACTION_TYPES,
	effectHandlers
};
