import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from './constants';


const GQL_MUTATION_INSERT = `
	mutation ($workspace: String!,
	$view_name: String!, 
	$user_id: String!, 
	$filter: String!,
	$tile_size: String!,
	$sort_by_field: String!,
	$sort_by_order: String!,
	$group_by_field: String!, 
	$group_by_order: String!,
	$business_service_group: String!, 
	$enabled_severities: String!,
	$alerts_panel_default_sorting: String!,
	$alerts_correlated_view: String!,
	$origin_view_id: String){
		GlideRecord_Mutation {
			insert_noc_user_view 
				(workspace: $workspace,
				view_name: $view_name, 
				user_id: $user_id, 
				filter: $filter,
				sort_by_field: $sort_by_field, 
				sort_by_order: $sort_by_order,
				group_by_field: $group_by_field,
				group_by_order: $group_by_order,
				business_service_group: $business_service_group, 
				enabled_severities: $enabled_severities,
				tile_size: $tile_size,
				alerts_panel_default_sorting: $alerts_panel_default_sorting,
				alerts_correlated_view: $alerts_correlated_view,
				origin_view_id: $origin_view_id) {
					sys_id {
						value
					}
					view_name {
						value
					}
					user_id {
						value
					}
					filter {
						value
					}
					sort_by_field {
						value
					}
					sort_by_order {
						value
					}
					group_by_field {
						value
					}
					group_by_order {
						value
					}
					business_service_group {
						value
					}
					enabled_severities {
						value
					}
					tile_size {
						value
					}
					alerts_panel_default_sorting {
						value
					}
					alerts_correlated_view {
						value
					}
					origin_view_id {
						value
					}
				}
			}
		}
`;

