import {view} from './view';
import styles from './nocGlobalFilter.scss';
import {createActionConfig, createNocElement, nocModalActionBehavior} from '@devsnc/library-opws-mapping-shared';
import {ACTION_TYPES, effectHandlers} from './effectGetFilterCountHandler';
import {effectFilterPropertyOnChange} from './effectFilterPropertyChanged';
//import {effectFilterPropertyChanged} from './effectFilterPropertyChanged';
//import {actionTypes} from '@seismic/core';
//const {COMPONENT_PROPERTY_CHANGED} = actionTypes;

createNocElement('noc-global-filter', {
	view,
	styles,

	properties: {
		isModalOpen: {
			default: false
		},

		userViewId: {
			default: null
		},

		filter: {
			// TODO wait for fix: https://code.devsnc.com/dev/sn-seismic/issues/375
			// `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/uberFilter`,
			default: null,
			// TODO @deprecated remove onChange and keep the actionHandler
			onChange: effectFilterPropertyOnChange
		},

		filterSize: {
			default: 0
		}
	},

	// TODO waiting for a fix in seismic (actionHandler not called on selectables)
	//actionHandlers: {
	//	[COMPONENT_PROPERTY_CHANGED]: createActionConfig(effectFilterPropertyChanged)
	//},

	actionHandlers: {
		[ACTION_TYPES.QUERY_PARSE_REQUEST_REQUESTED]:
			createActionConfig(effectHandlers.getQueryParseEffectRequest),
		[ACTION_TYPES.QUERY_PARSE_REQUEST_SUCCEEDED]:
			createActionConfig(effectHandlers.handleQueryParseResponse)
	},

	behaviors: [
		nocModalActionBehavior
	]
});