import {t} from 'sn-translate';
import CONSTANTS from './constants';
import {get as _get} from 'lodash';

const serviceGroupOption = {
	value: 'service_group',
	label: t('Service group...'),
	preventSelection: true,
	action: CONSTANTS.ACTION_TYPES.OPEN_GROUP_TREE_MODAL
};

const addFieldOption = (fields, option) => {
	return [
		...fields,
		option
	];
};

const updateGroupByFields = (groupByFields, selectedGroup) => {
	let newGroupByFields = addFieldOption(groupByFields, serviceGroupOption);

	if (!selectedGroup) {
		return newGroupByFields;
	} else {
		const selectedGroupOption = {
			label: selectedGroup,
			value: selectedGroup,
			preventSelection: true
		};

		newGroupByFields = addFieldOption(newGroupByFields, selectedGroupOption);

		return newGroupByFields;
	}
};

const getServiceGroupName = (serviceSysId, serviceGroups) => {
	if (serviceSysId && serviceGroups && serviceGroups.breadcrumb) { //FIXME Arik: No need for serviceGroups.breadcrumb check
		const items = _get(serviceGroups, 'breadcrumb.items', []);
		const item = items[items.length - 1];

		return item.name;
	}

	return null;
};

export default state => {
	const {groupByFields, tilemapsFormat: {groupByField, businessServiceGroup}, serviceGroups} = state.properties;

	const newGroupByField = getServiceGroupName(businessServiceGroup, serviceGroups);
	const updatedGroupByFields = updateGroupByFields(groupByFields, newGroupByField);

	const newState = {
		...state,
		properties: {
			...state.properties,
			groupByFields: updatedGroupByFields,
			tilemapsFormat: {
				...state.properties.tilemapsFormat,
				groupByField: newGroupByField || groupByField
			}
		}
	};

	return newState;
};