/**
 * GQL Query to get current User View for nocpit.
 * Used on page load.
 */
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {get as _get} from 'lodash';
import {checkForError, LOGGING} from '@devsnc/library-opws-mapping-shared';
import {handleUserViewResponseEffect} from './handleUserViewResponseEffect';
import VIEW_CONSTANTS from './constants';
import {transformRecordToMutationVars} from './transforms';
import {transformToModel} from '../noc-user-view-behaviors/transforms';

const requestCurrentUserViewHandler = coeffects => {
    LOGGING.info('Received requestCurrentUserViewHandler', {caller: 'getCurrentUserViewEffect'});

    const {
        properties,
        action: {payload},
        dispatch
    } = coeffects;

    if (checkForError({payload, dispatch, caller: 'requestCurrentUserViewHandler'}))
        return;

    const viewName = VIEW_CONSTANTS.DEFAULTS.CURRENT_USER_VIEW_NAME;

    const userId = _get(properties, 'userMeta.userId', null);
    const {nocConfig} = properties;
    // const {workspace} = properties;
    //temporary removing workspace for WS integration
    const workspace = 'nocpit';

    if (!userId || !workspace || !nocConfig)
        return;

    const variables = getVariables(workspace, viewName, userId);
    dispatch(VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST, variables);
};

const requestCurrentGlobalFilterHandler = coeffects => {
    LOGGING.info('Received requestCurrentGlobalFilterHandler', {caller: 'getCurrentUserViewEffect'});
    let {
        state, 
        action: {payload}, 
        dispatch
    } = coeffects;

    if (checkForError({payload, dispatch, caller: 'requestCurrentGlobalFilterHandler'}))
        return;

    const sysId = _get(state, 'userViewResource.sys_id', null);
    dispatch(VIEW_CONSTANTS.ACTION_TYPES.CURR_GLOBAL_FILTER_REQUEST, {sys_id: sysId});
};

const RESPONSE_RESULT_PATH = 'data.GlideRecord_Query.noc_user_view._results[0]';
const RESPONSE_RESULT_ALTERNATIVE_PATH = 'data.GlideRecord_Query.noc_user_view._results[1]'; //For use for SN Hop users

const currGlobalFilterFetched = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_current_global_filter, {
    startActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_STARTED,
    successActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_SUCCEEDED,
    errorActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_FAILED,
    variableList: ['sys_id']
});

const currUserViewFetched  = createGraphQLEffect(VIEW_CONSTANTS.VIEW_GQL_QUERIES.get_noc_user_view, {
    startActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_STARTED,
    successActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_SUCCEEDED,
    errorActionType: VIEW_CONSTANTS.ACTION_TYPES.CURR_USER_VIEW_REQUEST_FAILED,
    variableList: ['queryConditions']
});

const handleCurrentUserViewResponse = coeffects => {
    LOGGING.info('Received handleCurrentUserViewResponse', {caller: 'getCurrentUserViewEffect'});

    const {
        properties,
        action: {payload}, 
        dispatch,
        updateState
    } = coeffects;

    if (checkForError({payload, dispatch, caller: 'handleCurrentUserViewResponse'}))
        return;

    const {
        // workspace, 
        userMeta: {userId}
    } = properties;

    const workspace = 'nocpit';

    const nocViewsFirstRecord = _get(payload, RESPONSE_RESULT_PATH);
    const nocViewsSecondRecord = _get(payload, RESPONSE_RESULT_ALTERNATIVE_PATH);
    const firstRecordUserId = _get(nocViewsFirstRecord, 'user_id.value');
    const secondRecordUserId = _get(nocViewsSecondRecord, 'user_id.value');

    const existingUserId = firstRecordUserId || secondRecordUserId || false;

    if (existingUserId) {
        LOGGING.info('User has own record, skipping INSERT and triggering handleUserViewResponseEffect.', {caller: 'handleCurrentUserViewResponse'});

        const updatePayload = (firstRecordUserId) ? nocViewsFirstRecord : nocViewsSecondRecord;
        dispatch(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_USER_VIEW_ON_STATE,  updatePayload);
        updateState({originView: transformToModel(updatePayload)});
    } else {
        //New User
        const originViewId = _get(nocViewsFirstRecord, 'sys_id.value');
        const variables = transformRecordToMutationVars(
            workspace,
            VIEW_CONSTANTS.DEFAULTS.CURRENT_USER_VIEW_NAME,
            userId,
            null,
            originViewId,
            nocViewsFirstRecord
        );

        dispatch(VIEW_CONSTANTS.ACTION_TYPES.INSERT_USER_VIEW_REQUEST, variables);
        updateState({originView: transformToModel(variables)});
    }

    LOGGING.info('Dispatching load user view names', {caller: 'getCurrentUserViewEffect.handleCurrentUserViewResponse'});
    dispatch(VIEW_CONSTANTS.ACTION_TYPES.LOAD_USER_VIEW_NAMES);
};

//this effect is called when current UserViews load into model, and it already has unique record in DB.
const updateCurrentUserViewOnStateHandler = handleUserViewResponseEffect('payload');

const updateCurrentUserViewFailed = coeffects => {
    LOGGING.warn('Received updateCurrentUserViewFailed', {caller: 'getCurrentUserViewEffect'});
};

const effectHandlers = {
    requestCurrentGlobalFilterHandler,
    requestCurrentUserViewHandler,
    currUserViewFetched,
    currGlobalFilterFetched,
	handleCurrentUserViewResponse,
	updateCurrentUserViewOnStateHandler,
    updateCurrentUserViewFailed
};

const getVariables = (workspace, viewName, userId) => {
    let q = `user_id=${userId}^ORuser_idISEMPTY^workspace=${workspace}`;

    if (viewName)
        q += `^view_name=${viewName}`;

    return {
        queryConditions: q
    };
};

export default {
    effectHandlers,
    getVariables
};
