import serviceGroups from '../noc-service-groups-tree-behaviors/nocServiceGroupsEffect';
import nocTilemapSelectGroupEffect from '../noc-tilemap-data-behavior/nocTilemapSelectGroupEffect';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
    NOC_BREADCRUMB_ITEM_CLICKED: 'NOC_BREADCRUMB_ITEM_CLICKED'
};

const onBreadcrumbClickEffect = {
    effect(coeffects) {
        const {dispatch, state, action} = coeffects;
        LOGGING.log(`nocBreadcrumbEffect->onBreadcrumbClickEffect called by action ${action.type}`);
        const {payload: {id=''}} =action;

        const newServiceGroupId = {item: {id}} //The structure of the item is equal to the one send from the tree
        dispatch(serviceGroups.ACTION_TYPES.SERVICE_GROUP_SELECT_DIFFERENT_GROUP, newServiceGroupId);
        dispatch(nocTilemapSelectGroupEffect.ACTION_TYPES.TILEMAP_SELECT_REGULAR_GROUP, {name: ''}); //Reset the drill down
    }
};

const effectHandlers = {
    onBreadcrumbClickEffect
};
export default {
    ACTION_TYPES,
    effectHandlers
};
