
const ACTION_TYPES = {
	NOC_POPOVER_ESC_KEY_PRESSED: 'NOC_POPOVER_ESC_KEY_PRESSED'
};

const nocPopoverOnEscKey = coeffects => {
    const {state, dispatch} = coeffects;
	const {onCloseAction} = state.properties;
	
	if (onCloseAction)
		dispatch(onCloseAction, {});
};

const effectHandlers = {
	nocPopoverOnEscKey
};

export default {
	ACTION_TYPES,
	effectHandlers
};