import { LOGGING } from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';
import { get as _get } from 'lodash';


/**
 * When the user view modal opens or closes, this effect clears any notifications that stored on the modal level on
 * state.
 */
const toggleUserViewSaveAsModalEffect = coeffects => {
	const {
		action: { payload = {} },
		updateProperties,
		properties
	} = coeffects;
	const { value: isOpen = false } = payload;
	LOGGING.info(`Received a call to ${isOpen ? 'open' : 'close'} 'save as...' modal`, { caller: 'userViewSaveAsModalEffect.toggleUserViewSaveAsModalEffect' });
	updateProperties({
		userViewSaveAsModal: {
			isValid: true,
			errors: null,
			notification: null,
			isOpen
		}
	});
};

const saveUserViewSaveAsModalEffect = coeffects => {
	LOGGING.info(`User clicked on 'Save' in the 'Save as...' modal`, { caller: 'userViewSaveAsModalEffect.saveUserViewSaveAsModalEffect' });
	const {
		action: { payload: { action: { getViewNameFromInput } } },
		dispatch
	} = coeffects;

	// close the modal
	dispatch(VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_MODAL_STATE_CHANGED, { value: false });
	// dispatch save view
	dispatch(VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_REQUEST_SAVE, { viewName: getViewNameFromInput() });
};

const userViewNameChangedEffect = coeffects => {
	const {
		action,
		properties,
		updateProperties
	} = coeffects;

	const { value: viewName } = action.payload;

	updateProperties({
		userViewSaveAsModal: {
			...properties.userViewSaveAsModal,
			viewName
		}
	});
};

const effectHandlers = {
	toggleUserViewSaveAsModalEffect,
	saveUserViewSaveAsModalEffect,
	userViewNameChangedEffect
};

const COMPONENT_IDS = {
	USER_VIEW_SAVE_AS_MODAL_ID: 'USER_VIEW_SAVE_AS_MODAL_ID'
};

export default {
	effectHandlers,
	COMPONENT_IDS
};
