/**
 * Effect handlers to bring global nocpit configuration.
 */
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import getPopulationEffect from './getPopulationEffect';
import {get as _get} from 'lodash';
import {LOGGING, CHANGE_EVENT_CONSTANTS, CONSTANTS, checkForError, onNextFrame} from '@devsnc/library-opws-mapping-shared';

const {GLOBAL_ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;

const ACTION_TYPES = {
	NOC_CONFIG_REQUEST: 'NOC_CONFIG_REQUEST',
	NOC_CONFIG_REQUEST_STARTED: 'NOC_CONFIG_REQUEST_STARTED',
	NOC_CONFIG_REQUEST_SUCCEEDED: 'NOC_CONFIG_REQUEST_SUCCEEDED',
	NOC_CONFIG_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
	query getNocConfig($queryConditions:String!) {
	  GlideRecord_Query{
			noc_conf (queryConditions:$queryConditions) {
				_results {
					event_mgmt_mode {
						value
					}
					noc_refresh_rate {
						value
					}
					debug_level {
						value
					}
					debug_options {
						value
					}
				}
			}
		}
	}
`;

const nocConfigFetched = createGraphQLEffect(GQL_QUERY, {
	startActionType: ACTION_TYPES.NOC_CONFIG_REQUEST_STARTED,
	successActionType: ACTION_TYPES.NOC_CONFIG_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.NOC_CONFIG_REQUEST_FAILED,
	variableList: ['queryConditions']
});

const transform = result => {
	const isEm = _get(result, 'event_mgmt_mode.value', false);
    const nocRefreshRate = _get(result, 'noc_refresh_rate.value', 5 * 1000);
	return {
		isEm,
		nocRefreshRate
	};
};

const overrideLogging = (config) => {
	const debugLevel = _get(config, 'debug_level.value', null);
	if (debugLevel) {
		LOGGING.info(`Updating logging level to ${debugLevel}`);
		LOGGING.setLogLevel(debugLevel);
	}

	const debugOptions = _get(config, 'debug_options.value', null);
	if (debugOptions) {
		LOGGING.info(`Updating logging options to ${debugOptions}`);
		try {
			const optionsJson = JSON.parse(debugOptions);
			LOGGING.updateOptions(optionsJson);
		} catch(e) {
			LOGGING.warn(['Failed updating logging options', e]);
		}
	}
};

const handleNocConfigResponse = async coeffects => {
	LOGGING.log('handleNocConfigResponse');

	const {
		dispatch,
		updateProperties,
		action: {payload}
	} = coeffects;

	const errors = checkForError({payload, dispatch, caller: 'handleNocConfigResponse'});
	if (errors)
		return;

	const nocConfig = _get(payload, 'data.GlideRecord_Query.noc_conf._results[0]', null);

	if (!nocConfig)
		return;

	overrideLogging(nocConfig);

	const transformedNocConfig = transform(nocConfig);

	updateProperties({nocConfig: transformedNocConfig});

	await onNextFrame();
	dispatch(CONSTANTS.ACTION_TYPES.NOC_CONFIG_SET, {});
};

const onNocConfigSet = coeffects => {
	const {dispatch} = coeffects;
	
	dispatch(getPopulationEffect.ACTION_TYPES.NOC_POPULATION_REFRESH_REQUESTED, {});
	// STRY50601730 - Using timer effect to init change manager if visible, no need to call it again
	//dispatch(GLOBAL_ACTION_TYPES.NOC_INIT_CHANGE_MANAGER, {});
	dispatch(CONSTANTS.ACTION_TYPES.NOC_CONFIG_SET_PHASE_2, {});
};

const effectHandlers = {
	nocConfigFetched,
	handleNocConfigResponse,
	onNocConfigSet
};

const getVariables = workspace => ({
	queryConditions: `workspace=${workspace}`
});

export default {
	ACTION_TYPES,
	effectHandlers,
	getVariables
};
