import {createActionConfig, createNocElement} from '@devsnc/library-opws-mapping-shared';
import keyBindingBehavior from "@servicenow/behavior-key-binding";
import {nocPopoverBehavior} from '../noc-popover-behavior';
import nocPopoverOnEscEffect from './nocPopoverOnEscEffect';
import {view} from './view';
import styles from './nocPopover.scss';


/**
 * Escape key handler (a11y)
 * Closes the popup.
 * @param host
 * @param event
 * @param state
 * @param dispatch
 */
const onEscKeyPress = (host, event, dispatch) => {
	dispatch(nocPopoverOnEscEffect.ACTION_TYPES.NOC_POPOVER_ESC_KEY_PRESSED, {});
};

createNocElement('noc-popover', {
	view,

	styles,

	properties: {
		//ActionType to dispatch when popover closes.
		onCloseAction: {
			default: nocPopoverBehavior.ACTION_TYPES.NOC_POPOVER_CLOSED
		},

		positionStyle: {
			default: null
		}
	},

	actionHandlers: {
		[nocPopoverOnEscEffect.ACTION_TYPES.NOC_POPOVER_ESC_KEY_PRESSED]: 
			createActionConfig(nocPopoverOnEscEffect.effectHandlers.nocPopoverOnEscKey)
	},

	behaviors: [
		{
			behavior: keyBindingBehavior,
			options: {
					keyBindings: {
						onEscKeyPress: 'Escape',
					},
					handlers: {
						onEscKeyPress
					}
			}
		}
	]

});
