import {createHttpEffect} from '@servicenow/ui-effect-http';
import {CONSTANTS, LOGGING, checkForError} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty, xor as _xor, get as _get} from 'lodash';

const ACTION_TYPES = {
    GET_IMPACTED_SERVICES_REQUEST_STARTED: 'GET_IMPACTED_SERVICES_REQUEST_STARTED',
    GET_IMPACTED_SERVICES_REQUEST_SUCCEEDED: 'GET_IMPACTED_SERVICES_REQUEST_SUCCEEDED',
    GET_IMPACTED_SERVICES_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED,
    GET_IMPACTED_SERVICES_REQUEST: 'GET_IMPACTED_SERVICES_REQUEST',
    CLEAR_IMPACTED_SERVICES_REQUEST: CONSTANTS.ACTION_TYPES.CLEAR_IMPACTED_SERVICES_REQUEST
};

const getImpactedServicesEffectHandler = createHttpEffect('/api/sn_nocpit/nocpit/ImpactedServicesByAlert', {
    method: 'GET',
    queryParams: ['alertId', 'groupId', 'ciId'],
    startActionType: ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST_STARTED,
    successActionType: ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST_SUCCEEDED,
    errorActionType: ACTION_TYPES.GET_IMPACTED_SERVICES_REQUEST_FAILED
});

const handleImpactedServicesRequestStarted = coeffects => {
    const {
        updateProperties
    } = coeffects;

    LOGGING.info(`Requested to get impacted services for selected service`,
        {caller: 'effectImpactedServices.handleImpactedServicesRequestStarted'});

    updateProperties({isImpactedServicesLoading: true});
};

const handleGetImpactedServicesResponse = coeffects => {
    const caller = 'effectImpactedServices.handleGetImpactedServicesResponse';
    LOGGING.info(`handleGetImpactedServicesResponse`, {caller});

    const {
        properties,
        dispatch,
        updateProperties,
        action: {payload}
    } = coeffects;

    LOGGING.info(`Received get impacted services for selected service`, {caller});
    updateProperties({isImpactedServicesLoading: false});

    const errors = checkForError({payload, dispatch, caller});
    if (errors)
        return;

    const receivedIds = _get(payload, 'impactedServicesIds', []);
    const currentIds = properties.impactedServicesAlertIds || [];

    // check if the arrays contain different impacted services
    const hasChangesInIds = (_isEmpty(currentIds) && _isEmpty(receivedIds)) || !_isEmpty(_xor(currentIds, receivedIds));

    if (hasChangesInIds)
        updateProperties({impactedServicesAlertIds: receivedIds});
};


const clearImpactedServicesHandler = coeffects => {
    LOGGING.info('clearImpactedServicesHandler', {caller: 'clearImpactedServicesHandler'});

    const {
        dispatch,
        action: {payload},
        updateProperties
    } = coeffects;
    const errors = checkForError({payload, dispatch, caller: 'clearImpactedServicesHandler'});
    if (errors)
        return;
    updateProperties({selectedAlert: null, impactedServicesAlertIds: []});

};

const effectHandlers = {
    getImpactedServicesEffectHandler,
    handleImpactedServicesRequestStarted,
    handleGetImpactedServicesResponse,
    clearImpactedServicesHandler
};

export default {
    ACTION_TYPES,
    effectHandlers
};