import { LOGGING } from '@devsnc/library-opws-mapping-shared';
import { isViewExists } from './userViewsUtils';
import VIEW_CONSTANTS from './constants';

/**
 * A request to save the current view under other view name.
 * The request is tested here before sending to server.
 *
 * @param coeffects
 */
const requestedSaveAsViewEffect = coeffects => {
	const caller = 'snNocViewsEffects.requestedSaveAsViewEffect';
	const {
		properties: { views },
		action: { payload },
		dispatch
	} = coeffects;
	const { viewName } = payload;

	LOGGING.info(`Requested to save current view under the name '${viewName}'`, { caller });
	const { itemsMap } = views;
	const viewExist = isViewExists(viewName, itemsMap);
	if (viewExist) {
		LOGGING.warn(`A view with the name '${viewName}' already exists!`, { caller });
		return;
	}

	LOGGING.info(`Dispatch request to save view '${viewName}'`, { caller });

	const variables = {
		viewName
	};

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.SAVE_USER_VIEW_REQUEST, variables);
};

const effectHandlers = {
	requestedSaveAsViewEffect
};

export default {
	effectHandlers
};
