import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';
import {get as _get} from 'lodash';

/**
 * When user selects a different view from a list
 *
 * TODO
 *      1. Check if current view is not saved - should notify user? TBD
 *      2. Change the current view to the selected view - DONE
 *      3. Update the latest/default view to hold the selected view - DONE
 *      4. Update the latest/default view origin_view_id to the selected view's ID - DONE
 *
 * TODO fix behavior of isModified check and notification to user
 *	    situations where we should notify user about
 *	    1. If view has not been modified - ALLOW changing view
 *	    2. if view has been modified prior to the view change:
 * 	       a. Global view - Notify and wait for confirmation - ?
 *	       b. Private view - Notify and wait for confirmation - ?
 *
 * @param coeffects
 */
// TODO merge with handleUserViewResponseEffect.handleUserViewSwitch
const userViewSelectedEffect = ({action, properties, dispatch}) => {
	const {value} = action.payload;
	const {views: {itemsMap}} = properties;

	LOGGING.info(`Requested to change selected view`, {
		caller: 'snNocViewsEffects.userViewChangedEffect',
		extra: itemsMap[value],
		output: {objects: true}
	});

	const viewName = _get(itemsMap[value], 'view_name.value', null);
	const userId = _get(itemsMap[value], 'user_id.value', null);

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.SPECIFIC_USER_VIEW_SELECTED, {viewName, userId});
};

/**
 *
 * @param coeffects
 */
const userViewActionsEffect = ({action, properties, dispatch}) => {
	const caller = 'snNocViewsEffects.userViewActionsEffect';
	const {item} = action.payload;
	const {views: {itemsMap, selectedView}} = properties;

	LOGGING.info(`User selected item`, {
		output: {objects: true},
		extra: item,
		caller
	});

	if (!selectedView) {
		LOGGING.warn('No selected view found!', {caller});
		return;
	}

	const value = selectedView[0];
	const viewName = _get(itemsMap[value], 'view_name.value', null);
	const userId = _get(itemsMap[value], 'user_id.value', null);

	LOGGING.info(`Dispatching '${item.action}' for view '${viewName}' user '${userId}'`, {caller});
	dispatch(item.action, {viewName, userId});
};

/**
 * User clicked on the 'Save as...' item from the split button dropdown
 *
 * @param dispatch
 */
const userSaveAsActionEffect = ({dispatch}) => {
	LOGGING.info(`Open 'save as...' modal`, {caller: 'snNocViewsEffects.userSaveAsActionEffect'});
	dispatch(VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_MODAL_STATE_CHANGED, {value: true});
};

/**
 * User clicked on the 'Save' button in the split button.
 * The request is tested here before sending it to the server.
 *
 * TODO handle save
 *      1. Check if global view -
 *         a. Should we disallow saving? - DONE
 *         b. Should we popup 'Save as...' confirmation?
 *            i.  with global's view name as the default name / empty name?
 *            ii. if asked to save with a name that already exists, should we allow? another confirmation to overwrite?
 *
 * @param properties
 * @param dispatch
 */
const userSaveActionEffect = ({properties, dispatch}) => {
	const caller = 'snNocViewsEffects.userSaveActionEffect';
	LOGGING.info(`User clicked on SAVE`, {caller});

	const {views = {}} = properties;
	const view = views.itemsMap[views.selectedView];

	if (!view) {
		LOGGING.warn('Failed to get current view', {caller});
		return;
	}

	const userId = _get(view, 'user_id.value', null);
	if (!userId) {
		LOGGING.warn(`View is a global view. Use 'Save as...' to save under a different view name`, {caller});
		return;
	}

	//dispatch save action
	const sysId = _get(view, 'sys_id.value', null);
	if (!sysId) {
		LOGGING.warn(`View with the requested sysId '${sysId}' was not found!`, {caller});
		return;
	}

	const viewName = _get(view, 'view_name.value', null);
	if (!viewName) {
		LOGGING.warn(`No view name!`, {caller});
		return;
	}

	const variables = {
		viewName,
		sysId
	};

	LOGGING.info(`Dispatch request to save view [${viewName}]`, {extra: view, caller});

	dispatch(VIEW_CONSTANTS.ACTION_TYPES.SAVE_USER_VIEW_REQUEST, variables);
};


const effectHandlers = {
	userViewSelectedEffect,
	userViewActionsEffect,
	userSaveActionEffect,
	userSaveAsActionEffect
};

export default {
	effectHandlers
};