/**
 * Seismic component noc-field-select.
 *
 */
import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import {noop} from 'lodash';
import {NONE_VALUE, view} from './view.js';
import {nocDropdownBehavior} from './nocDropdownBehavior';
import {nocTilemapToolbarBehavior} from '../noc-tilemap-toolbar';

import styles from './nocFieldSelect.scss';

export {NONE_VALUE} from './view';

createNocElement('noc-field-select',  {
	view,
	styles,
	properties: {
		label: {
			default: null
		},
		value: {
			default: null
		},
		displayNone: {
			// FIXME there is a bug in seismic - doesn't accept boolean properties
			default: 'true'
		},
		displayNoneValue: {
			default: ''
		},
		onChange: {
			default: noop
		},
		choices: {
			default: null
		},
		type: {
			default: ''
		},
        disabled: {
            default: false
		},
		tooltipContent: {
			default: ''
		},
		manageSelectedItems: {
			default: false
		}
	},

	behaviors: [
		nocDropdownBehavior,
		nocTilemapToolbarBehavior
	]
});