import {snNocTilemapLayoutView} from './snNocTilemapLayoutView';
import styles from './snNocTilemapLayout.scss';
import {transformState} from './transformers/transformState';
import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import clearServicePreviewDataEffect from '../noc-service-preview-behaviors/clearServicePreviewDataEffect';

createNocElement('sn-noc-tilemap-layout', {
    view: snNocTilemapLayoutView,
    transformState,
    styles,
    properties: {
        nocAppRef: {
            default: null
        },

        tilemapsFormat: {
            default: null
        },

        choicesFieldsModel: {
            default: null
        },

        /**
         * The state table configuration of the dynamic layout
         */
        dynamicLayoutConfig: {
            default: null
        },

        /**
         * If true then the number of items per segments should be the same in all groups within the same row
         */
        canSegmentsDividedEqually: {
            default: false
        },

        /**
         * The container which holds the services divided into groups
         */
        groups: {
            default: null
        },
        /**
         * When regular group is drilled down.
         * Only for regular services (not working with in Service Groups mode)
         */
        selectedGroup: {
            default: null
        },

        windowSize: {
            default: null,
            onChange(value, prevValue, dispatch) {
                //close preview when window resizes
                dispatch(clearServicePreviewDataEffect.ACTION_TYPES.NOC_CLEAR_SERVICE_PREVIEW, {});
            }
        },

        servicePreviewResource: {
            default: null
        },

        popover: {
            default: null
        }
    }
});