import {get as _get} from 'lodash';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {nocPopoverBehavior} from '../noc-popover-behavior';
import {generatePreviewPopoverKey} from './generatePreviewId';

const ACTION_TYPES = {
	NOC_CLEAR_SERVICE_PREVIEW: 'NOC_CLEAR_SERVICE_PREVIEW'
};

const clearServicePreviewDataEffect = coeffects => {
	const {
		state: {servicePreviewResource},
		properties,
		updateProperties,
		dispatch
	} = coeffects;

	const popoverKey = generatePreviewPopoverKey(servicePreviewResource);
	const currId = _get(properties, 'popover.id', null);

	LOGGING.debug(`clearServicePreviewDataEffect: invoked popoverKey=${popoverKey} currId=${currId}`);

	updateProperties({servicePreviewData: null});

	if (popoverKey && popoverKey === currId) {
		LOGGING.debug(`clearServicePreviewDataEffect: clearing servicePreviewResource popoverKey=${popoverKey}`);

		dispatch(nocPopoverBehavior.ACTION_TYPES.NOC_POPOVER_CLOSED);
	}
};

const effectHandlers = {
	clearServicePreviewDataEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};