import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty} from 'lodash';
import '../noc-tilemap-container';

export const getNocTilemapContainerJsx = (state, nocAppRef, workspace, isEm) => {
    const {
        severities,
        tilemapsFormat,
        timestampForTiles,
    } = state.properties;

	const serviceGroups = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/serviceGroups`;
    const enabledPopulation = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/enabledPopulation`;
    const impactedServicesAlertIds = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/impactedServicesAlertIds`;
    const isImpactedServicesLoading = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/isImpactedServicesLoading`;
    const selectedAlert = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/selectedAlert`;

    //TODO: Arik: use the selectable (timestamp, severities...)
    const cond = (isEm && !_isEmpty(severities)) || !isEm;
    const tilemapContainer = cond ?
        <noc-tilemap-container
            key='noc_tilemap_container_key'
            nocAppRef={nocAppRef}
            population={enabledPopulation}
            workspace={workspace}
            timestamp={timestampForTiles}
            className='tilemap-container eds-inline -inline-start'
            serviceGroups={serviceGroups}
            severities={severities}
            impacted-services-alert-ids={impactedServicesAlertIds}
            is-impacted-services-loading={isImpactedServicesLoading}
            selected-alert={selectedAlert}
            tilemapsFormat={tilemapsFormat}
            component-id={CONSTANTS.DEFAULTS.NOC_TILEMAP_CONTAINER_COMPONENT_ID}/> : null;

    return  tilemapContainer;
}

