/**
 * Effect handler for NOCpit tile map configuration.
 * This handler will provide:
 * <lu>
 *     <li>sort by</li>
 *     <li>group by</li>
 *     <li>hover tooltip</li>
 * </lu>
 *
 * fields are from the `noc_tilemap_conf` table
 */

import {createHttpEffect} from '@servicenow/ui-effect-http';

import {camelCase as _camelCase} from 'lodash';
import {CONSTANTS, createNocUpdateStateEffect} from '@devsnc/library-opws-mapping-shared';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';


const {PROPERTIES_SET} = CONSTANTS.GENERAL;

const ACTION_TYPES = {
	NOC_TILE_MAP_CONFIG_REQUEST_INIT: 'NOC_TILE_MAP_CONFIG_REQUEST_INIT',
	NOC_TILE_MAP_CONFIG_REQUEST_STARTED: 'NOC_TILE_MAP_CONFIG_REQUEST_STARTED',
	NOC_TILE_MAP_CONFIG_REQUEST_SUCCEEDED: 'NOC_TILE_MAP_CONFIG_REQUEST_SUCCEEDED',
	NOC_TILE_MAP_CONFIG_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};


const handleNocTileMapConfigInit = createHttpEffect('/api/sn_nocpit/nocpit/GetTilemapGetChoices', {
	method: 'GET',
	queryParams: ['workspace'],
	startActionType: ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_STARTED,
	successActionType: ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_SUCCEEDED,
	errorActionType: ACTION_TYPES.NOC_TILE_MAP_CONFIG_REQUEST_FAILED,
});


const mapValuesFromArray = item => ({
	value: item.name, 
	label: item.displayName
});


const reduceChoiceFields = (total, value) => {
	var choices = value.choices.map(mapValuesFromArray);
	const reducedKey = _camelCase(value.field);

	return {
		...total,
		[reducedKey]: choices
	};
};

const transformPlainFields = (fieldsObj) => {
    let obj ={};
    for (var item in  fieldsObj){
        obj[_camelCase(item)] = fieldsObj[item];
    }
    return obj;
};

/**
 *
 * @param tileMapConfig
 */
const transformTileMapConfig = response => {
	const choices = response.choicesFields.reduce(reduceChoiceFields, {});
    const plain = transformPlainFields(response.plainFields);
	return {
        ...choices,
        ...plain
    };
};

const handleNocTileMapConfigSucceeded = createNocUpdateStateEffect((state, action, getAfterStateUpdate) => {
	LOGGING.log('handleNocTileMapConfigSucceeded');
	if (action.error) {
		return {
			...state,
			nocTileMapsConfigResource: {
				error: action.error
			}
		};
	}
	const result = action.payload;

	if (!result) {
		LOGGING.log('Failed fetching "tileMapConfig" record!');
		return state;
	}

	const tilemapsConfig =  transformTileMapConfig(result);

	var newState = {
		...state,
		properties: {
			...state.properties,
			tilemapsConfig
		}
	}

	getAfterStateUpdate().then(dispatch => 
		dispatch(CONSTANTS.ACTION_TYPES.NOC_TILE_MAP_CONFIG_SET, {caller: 'nocTileMapConfigEffect'}));
		
	return newState;
});

// TODO error handling
const handleNocTileMapConfigFailed = null;

/**
 * list of effects handled in this module
 *
 */
const effectHandlers = {
	handleNocTileMapConfigInit,
	handleNocTileMapConfigSucceeded,
	handleNocTileMapConfigFailed
};

const getVariables = workspace => {
	return {
		queryConditions: `workspace=${workspace}`
	};
};

export default {
	ACTION_TYPES,
	effectHandlers,
	getVariables
};
