import revertUserViewEffect from './revertUserViewEffect';
import userViewMutationEffect from './userViewMutationEffect';
import saveCurrentUserViewEffect from './saveCurrentUserViewEffect';
import {createActionConfig} from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from './constants';

export const revertUserViewBehavior = {
	name: 'revertUserViewBehavior',
	actionHandlers: {
		[VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW]:
				createActionConfig(revertUserViewEffect.effectHandlers.requestDefaultUserViewHandler),

		[VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST]:
			createActionConfig(revertUserViewEffect.effectHandlers.defaultUserViewFetched),

		[VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW_REQUEST_SUCCEEDED]:
			createActionConfig(revertUserViewEffect.effectHandlers.handleDefaultUserViewResponse),

		[VIEW_CONSTANTS.ACTION_TYPES.UPDATE_DEFAULT_USER_VIEW_REQUEST]:
		createActionConfig(saveCurrentUserViewEffect.effectHandlers.revertCurrentUserViewRequestedHandler),

		[VIEW_CONSTANTS.ACTION_TYPES.UPDATE_TO_DEFAULT_USER_VIEW_REQUEST_SUCCEEDED]:
			createActionConfig(userViewMutationEffect.effectHandlers.revertCurrentUserViewHandler)
	}
};
