import {generatePreviewId, generatePreviewPopoverKey} from '../noc-service-preview-behaviors/generatePreviewId';
import '../noc-tilemap';
import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';

/**
 * This method responsible for rendering the entire group on its content
 * @param cell - a specific group data
 * @returns {The rendered HTML of one tilemap component}
 */
const generateCellJsx = function (cell) {
	const {
		properties: {
			servicePreviewResource,
			tilemapsFormat,
			popover,
			selectedGroup,
			nocAppRef,
		}
	} = this;

	const servicePreviewId = generatePreviewId(servicePreviewResource);
	const popoverKey = generatePreviewPopoverKey(servicePreviewResource);
	const tilemapKey = `cell_index_${cell.groupIndex}`;

	const severities = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/severities`;
	const timestampForTiles = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/timestampForTiles`;
	const tilemapsConfig = `@${CONSTANTS.DEFAULTS.NOC_TILEMAP_CONTAINER_COMPONENT_ID}/tilemapsConfig`;
	const choicesFieldsModel = `@${CONSTANTS.DEFAULTS.NOC_TILEMAP_CONTAINER_COMPONENT_ID}/choicesFieldsModel`;
	const servicePreviewData = `@${CONSTANTS.DEFAULTS.NOC_TILEMAP_CONTAINER_COMPONENT_ID}/servicePreviewData`;
	
	return (
		<noc-tilemap
			className="layout-cell"
			key={tilemapKey}
			nocAppRef={nocAppRef}
			data={cell}
			tilemapsFormat={tilemapsFormat}
			servicePreviewData={servicePreviewData}
			servicePreviewId={servicePreviewId}
			popoverKey={popoverKey}
			popover={popover}
			selectedGroup={selectedGroup}
			severities={severities}
			timestamp-for-tiles={timestampForTiles}
			tilemaps-config={tilemapsConfig}
			choices-fields-model={choicesFieldsModel}
		/>
	);
};

const generateRowJsx = function (row) {
	const rowJsx = row.items.map(generateCellJsx, this);
	return (<div className="layout-row">{rowJsx}</div>);
};

export const snNocTilemapLayoutView = (state) => {
	const {
		grid,
		properties: {
			windowSize,
			dynamicLayoutConfig
		}
	} = state;

	if (!grid || !windowSize || !dynamicLayoutConfig) 
		return null;
	

	const gridJsx = grid.rows.map(generateRowJsx, state);
	return (
		<section className="layout" >
			{gridJsx}
		</section>
	);
};
