/**
 * This effect provides action name to update current user view.
 * It encapsulates logic to update or insert a record on noc_user_view table.
 */
import { get as _get, isEqual as _isEqual } from 'lodash';
import { getMutationVars } from './transforms';
import { LOGGING } from '@devsnc/library-opws-mapping-shared';
import VIEW_CONSTANTS from './constants';



const createSpecificUserViewSaveRequestedEffect = coeffects => {
	LOGGING.info(`Called`, { caller: 'saveCurrentUserViewEffect.createSpecificUserViewSaveRequestedEffect' });
	const {
		dispatch,
		action: { payload },
		properties
	} = coeffects;

	const workspace = 'nocpit';
	const { userMeta: { userId } } = properties;
	const { viewName, sysId } = payload;

	const variables = getMutationVars(
		workspace,
		viewName,
		userId,
		sysId,
		null,
		properties
	);
	const actionType = sysId ? VIEW_CONSTANTS.ACTION_TYPES.UPDATE_USER_VIEW_REQUEST : VIEW_CONSTANTS.ACTION_TYPES.INSERT_USER_VIEW_REQUEST;
	dispatch(actionType, variables);
};

const createCurrentUserViewSaveRequestedEffect = mutationActionType => coeffects => {
	LOGGING.info(`Called with ${mutationActionType}`, { caller: 'saveCurrentUserViewEffect.createCurrentUserViewSaveRequestedEffect' });

	const {
		dispatch,
		state,
		action,
		properties: {
			userMeta,
			views: { selectedView }
			// workspace //FIXME temporary removing workspace for WS integration
		}
	} = coeffects;

	//temporary removing workspace for WS integration
	const workspace = 'nocpit';

	const { userId } = userMeta;
	const userViewModel = action.payload;
	const sysId = _get(state, 'userViewResource.sys_id') || '-1';
	const originViewId = selectedView ? selectedView[0] : null; //_get(state, 'userViewResource.originViewId', null);

	//getMutationVars will return only variables for passed part in userViewModel.
	const variables = getMutationVars(workspace, VIEW_CONSTANTS.DEFAULTS.CURRENT_USER_VIEW_NAME, userId, sysId, originViewId, userViewModel);
	dispatch(mutationActionType, variables);
};

const dispatchComparisonForViews = coeffect => {
	const { dispatch, action: { payload: { current, latest } } } = coeffect;
	let q = `sys_id=${current}^ORsys_id=${latest}`;
	dispatch(VIEW_CONSTANTS.ACTION_TYPES.COMPARISON_USER_VIEW_REQUEST, { queryConditions: q });
}

const compareViews = coeffect => {
	const { action, updateProperties } = coeffect;
	const viewsArr = _get(action, 'payload.data.GlideRecord_Query.noc_user_view._results', null);
	if (viewsArr != null) {
		const [originView, latestView] = viewsArr;
		const keys = Object.keys(originView);
		const result = keys.find(key => latestView[key] && !_isEqual(originView[key], latestView[key]));
		updateProperties({ noChangesOnCurrentView: Boolean(!result) });
	}
}

const uberFilterSaveRequestedHandler =
	createCurrentUserViewSaveRequestedEffect(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_UBER_FILTER_REQUEST);

const severityFilterSaveRequestedHandler =
	createCurrentUserViewSaveRequestedEffect(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_SEVERITY_FILTER_REQUEST);

const tilemapsFormatSaveRequestedHandler =
	createCurrentUserViewSaveRequestedEffect(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_TILEMAPS_FORMAT_REQUEST);

const alertPreferencesSaveRequestedHandler =
	createCurrentUserViewSaveRequestedEffect(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_ALERT_PREFERENCES_REQUEST);

const revertCurrentUserViewRequestedHandler =
	createCurrentUserViewSaveRequestedEffect(VIEW_CONSTANTS.ACTION_TYPES.UPDATE_TO_DEFAULT_USER_VIEW_REQUEST_SUCCEEDED);

const effectHandlers = {
	createSpecificUserViewSaveRequestedEffect,
	uberFilterSaveRequestedHandler,
	severityFilterSaveRequestedHandler,
	tilemapsFormatSaveRequestedHandler,
	alertPreferencesSaveRequestedHandler,
	revertCurrentUserViewRequestedHandler,
	dispatchComparisonForViews,
	compareViews
};

export default {
	effectHandlers
};
