import VIEW_CONSTANTS from './constants';
import { get as _get } from 'lodash';

const PRIVATE = VIEW_CONSTANTS.VIEW_SECTIONS.PRIVATE;
const GLOBAL = VIEW_CONSTANTS.VIEW_SECTIONS.GLOBAL;


export const isViewExists = (viewName, itemsMap) => {
	viewName = viewName.trim();
	const found = Object.values(itemsMap).find(view => view.view_name.value.trim() === viewName);
	return Boolean(found);
};

/**
 * map all items to [{key: item}, ...]
 *
 * @param items
 * @returns {Array|*}
 */
const itemsToMap = items => {
	if (!items)
		return null;

	const itemsMap = items.reduce((result, item) => {
		result[item.sys_id.value] = item;
		return result;
	}, {});

	return itemsMap;
};

/**
 *
 * @param itemsMap
 * @param selected
 * @returns {[*, *]}
 */
const createViewItems = (items, selected = null) => {
	const sections = {
		[PRIVATE]: { label: PRIVATE, children: [] },
		[GLOBAL]: { label: GLOBAL, children: [] }
	};

	// FIXME temporary for now
	const viewChanged = false;

	items.map(item => {
		// ignore the item with the origin view id (its the 'latest' view)
		if (item.origin_view_id.value)
			return;

		// this view is marked as the latest and was changed
		const suffix = item.sys_id.value && viewChanged === selected ? '*' : '';

		const newItem = { id: item.sys_id.value, label: item.view_name.value + suffix };
		const section = item.user_id.value ? sections[PRIVATE] : sections[GLOBAL];

		section.children.push(newItem);
	});

	// TODO sort each section by view name
	const viewItems = [
		sections[GLOBAL],
		sections[PRIVATE]
	];

	return viewItems;
};

const getUserDefaultViewSysId = (items) => {
	const defaultView = items.find(item => {
		const originViewId = _get(item, 'origin_view_id.value', null);
		const viewName = _get(item, 'view_name.value', null);

		return originViewId && viewName === VIEW_CONSTANTS.DEFAULTS.DEFAULT_USER_VIEW_NAME;
	});

	return defaultView ? _get(defaultView, 'sys_id.value', null) : null;
};

export const isViewGlobal = (view) => {
	if (!view) {
		return false;
	}

	return _get(view, 'user_id.value', null) === null;
}

export const userViewsUtils = (items, origin) => {
	const itemsMap = itemsToMap(items);
	const viewItems = createViewItems(items, origin);
	const defaultViewSysId = getUserDefaultViewSysId(items);
	const selectedView = [origin];

	const newViews = {
		viewItems,
		itemsMap,
		selectedView,
		defaultViewSysId
	};

	return newViews;
};