import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import '../sn-noc-spectrum-container';

export const getNocSpectrumJsx = (state, isEm) => {
	const {
		population,
		uberFilter,
		severities,
		tilemapsFormat,
		serviceGroups,
		notifications
	} = state.properties;

	const {
		userViewResource: {
			sys_id = null
		} = {}
	} = state;
	const selectedView=`@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/views.selectedView[0]`;
	const viewsList=`@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/views.itemsMap`;
	const noChangesOnCurrentView = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/noChangesOnCurrentView`;

	const spectrumContainerJsx = (isEm && severities) ?
		<sn-noc-spectrum-container key='noc_spectrum_container_key'
		                           component-id={CONSTANTS.DEFAULTS.NOC_SPECTRUM_CONTAINER_COMPONENT_ID}
		                           population={population}
		                           filter={uberFilter}
		                           severities={severities}
		                           service-groups={serviceGroups}
		                           user-view-id={sys_id}
		                           tilemaps-format={tilemapsFormat}
								   notifications={notifications}
								   selected-view={selectedView}
								   views-list={viewsList}
								   no-changes-on-current-view={noChangesOnCurrentView}
								   /> : null;
	return spectrumContainerJsx;
};

