import {find as _find,
	isUndefined as _isUndefined} from 'lodash';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';

const selectionChangedEffect = {
	effect(coeffects) {
		const {
			dispatch,
			action: {payload},
			properties: {choices, onChange, type}
		} = coeffects;

		const {value} = payload;

		if (_isUndefined(value)) {
			LOGGING.error('eds-dropdown selection returned undefined');
			return;
		}

		if (choices && value) {
			const singleValue = value[0];
			const option = _find(choices, {value: singleValue});
			const {preventSelection = false} = option || false;

			if (option && option.action) {
				LOGGING.debug(`calling action ${option.action}`, {output: {objects: true}, extra: option});
				dispatch(option.action, {option});
			}

			LOGGING.debug(`option preventSelection=${preventSelection}`);
			if (!preventSelection) {
				dispatch(onChange, {type: type, value: singleValue});
			}
		}
	}
};

export const effectHandlers = {
	selectionChangedEffect
};

export default {
	effectHandlers
};