import "@servicenow/now-dropdown";
import {t} from 'sn-translate';

export const NONE_VALUE = 'none';

const createOption = (option) => {
    return {
        ...option,
        id: option.value,
        value: option.label,
        label: option.label
    };
};

const createOptions = (choices, displayNone = false,
                       displayNoneValue = t('--NONE--'),
                       selectedValue = null) => {
    let options = [];

    if (displayNone) {
        const opt = {'value': NONE_VALUE, 'label': displayNoneValue};
        options.push(createOption(opt));
    }

    //add 'choices'
    if (choices) {
        choices.forEach(choice => {
            options.push(createOption(choice, selectedValue));
        });
    }

    return options;
};

export const view = (state, dispatch) => {
    const {
        label, 
        choices, 
        displayNone, 
        displayNoneValue, 
        value, 
        disabled,
        tooltipContent,
        manageSelectedItems
    } = state.properties;

    const labelWithNoSpaces = label.replace(/\s+/g, '-').toLowerCase();
    const dropDownID = `noc_field_select_${labelWithNoSpaces}_${disabled}`;
    const hiddenLabelId = `${dropDownID}_hidden`;
    const options = createOptions(choices, displayNone, displayNoneValue, value);
    const selectedItem = [value];

    const labelledBy = `${dropDownID} ${hiddenLabelId}`;
    
    return (
        <form>
            <label 
            className='dropdown-label'
            id={dropDownID}>
                {label} 
            </label>

            <label id={hiddenLabelId} className='now-a11y-label'>
                {tooltipContent}
            </label>

            <now-dropdown disabled={disabled}
                          selected-items={selectedItem}
                          tooltip-content={tooltipContent}
                          key={dropDownID}
                          className='noc-field-dropdown'
                          items={options}
                          variant='tertiary'
                          size="md"
                          bare
                          manage-selected-items={manageSelectedItems}
                          aria-labelledby={labelledBy}
            />
        </form>
    );
};