import nocSeverityIndicatorBarEffect from '../noc-severity-indicators-bar/nocSeverityIndicatorBarEffect';

const ACTION_TYPES = {
};

const indicatorClicked = coeffects => {
	const {properties, action: {payload: {event}}, dispatch} = coeffects;

	const isClicked = (event.type === 'click');
	if (isClicked) {
		const {severity: {value}} = properties;
		dispatch(nocSeverityIndicatorBarEffect.ACTION_TYPES.NOC_SEVERITY_INDICATOR_CLICKED, {
			severityClicked: value
		});
	}
};

export const effectHandlers = {
	indicatorClicked,
};

export default {
	ACTION_TYPES,
	effectHandlers
};