import {CONSTANTS, LOGGING} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {
	SETTINGS_MODAL_STATE_CHANGED: 'SETTINGS_MODAL#OPENED_SET',
	DROPDOWN_STATE_CHANGED: 'NOW_DROPDOWN#SELECTED_ITEMS_SET' //dropdown selected value changed
};


/**
 * When the settings modal opens or closes, this effect clears any notifications that stored on the modal level on
 * state.
 */
const toggleSettingsModalEffect = coeffects => {
	const {
		action: {
			payload: {
				value: isOpen = false
			}
		},
		properties,
		updateProperties
	} = coeffects;

	updateProperties({
				settingsModal: {
					...properties.settingsModal,
					notification: null,
					isOpen
				}
			}
	);
};


const updateTileSize = (dispatch, tilemapsFormat, tileSize) => {
	if (!tileSize) {
		return;
	}

	const newTilemapsFormat = {
		...tilemapsFormat,
		tileSize
	};

	dispatch(CONSTANTS.ACTION_TYPES.TILEMAPS_FORMAT_SAVE_REQUESTED, {tilemapsFormat: newTilemapsFormat});
};

/**
 * When the dropdown for tile size changes value, this effect adds notification on settings modal.
 * @param {*} coeffects
 */
const dropdownValueChangedEffect = coeffects => {
	const {state, action, dispatch} = coeffects;
	const {value: selectedValue = []} = action.payload;
	const {path} = action.meta;
	if (path.includes(COMPONENT_IDS.TILE_SIZE_DROPDOWN_ID)) {
		const {
			tilemapsFormat
		} = state.properties;

		updateTileSize(dispatch, tilemapsFormat, selectedValue[0]);
	}

};

const effectHandlers = {
	toggleSettingsModalEffect,
	dropdownValueChangedEffect
};

const COMPONENT_IDS = {
	SETTINGS_MODAL_ID: 'SETTINGS_MODAL_ID',
	TILE_SIZE_DROPDOWN_ID: 'TILE_SIZE_DROPDOWN_ID'
};

export default {
	ACTION_TYPES,
	effectHandlers,
	COMPONENT_IDS
};