import {CONSTANTS, i18n} from '@devsnc/library-opws-mapping-shared';
import TOOLBAR_CONSTANTS from './constants';
import {t} from 'sn-translate';
import '../sn-noc-service-tree-container';
import '@servicenow/now-label-value';
import {NONE_VALUE} from '../noc-field-select';


export const view = (state, dispatch) => {
    const {
        groupByFields,
        sortByFields,
        tilemapsFormat,
        selectedGroup,
        isModalOpen
    } = state.properties;

    const {
        groupByField,
        sortByField,
        groupByOrder,
        businessServiceGroup
    } = tilemapsFormat;


    // we still did not receive the tilemaps format from server
    if (!tilemapsFormat)
        return null;


    const groupBy = {
        id: 'nocFieldSelectGroupBy',
        label: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.GROUP_BY.label,
        value: groupByField,
        displayNone: true,
        disabled: false,
        displayNoneValue: t('No grouping'),
        choices: groupByFields,
        type: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.GROUP_BY.type,
        dispatchMessage: TOOLBAR_CONSTANTS.ACTION_TYPES.TILEMAPS_DROPDOWN_CHANGED,
    };

    const sortBy = {
        id: 'nocFieldSelectSortBy',
        label: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.SORT_BY.label,
        value: sortByField,
        displayNone: false,
        disabled: false,
        choices: sortByFields,
        type: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.SORT_BY.type,
        dispatchMessage: TOOLBAR_CONSTANTS.ACTION_TYPES.TILEMAPS_DROPDOWN_CHANGED
    };

    const groupOrder = {
        id: 'nocFieldSelectGroupOrder',
        label: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.GROUP_ORDER.label,
        value: groupByOrder,
        displayNone: false,
        disabled: Boolean(!businessServiceGroup && (groupByField === NONE_VALUE) || selectedGroup),
        choices: TOOLBAR_CONSTANTS.ORDER_TYPES.groupByOrderOptions,
        type: TOOLBAR_CONSTANTS.DROPDOWN_TYPES.GROUP_ORDER.type,
        dispatchMessage: TOOLBAR_CONSTANTS.ACTION_TYPES.TILEMAPS_DROPDOWN_CHANGED
    };

    const createDropdown = ({option, hasBorder = false, manageSelectedItems = false}) => {

        const className = `select-field ${hasBorder ? '-right-Border' : ''}`;
        return (
            <noc-field-select
                tooltip-content={i18n.NOC_UPDATES_USER_VIEW}
                disabled={option.disabled}
                id={option.id}
                label={option.label}
                value={option.value}
                display-none={option.displayNone}
                display-none-value={option.displayNoneValue}
                choices={option.choices}
                onChange={option.dispatchMessage}
                type={option.type}
                manage-selected-items={manageSelectedItems}
                className={className}/>
        );
    };

    const serviceGroups = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/serviceGroups`;
    const severities = `@${CONSTANTS.DEFAULTS.NOC_APP_COMPONENT_ID}/severities`;
    const serviceGroupTree = isModalOpen ? <sn-noc-service-tree-container className='sn-noc-service-tree-container'
                                                                          isModalOpen={isModalOpen}
                                                                          service-groups={serviceGroups}
                                                                          severities={severities}
                                                                          key='sn-noc-service-tree-container__key'
                                                                          component-id='sn-noc-service-tree-container'
    /> : null;


    // TODO remove the filterByAlertBtn when we will switch to 'tabbed' side-panel
    return (
        <section className='noc-tilemap-toolbar'>
            <div role="group" className='noc-tilemap-select'>
                {createDropdown({option: groupBy, manageSelectedItems: true})}
                {createDropdown({option: groupOrder, hasBorder: true})}
                {createDropdown({option: sortBy})}
            </div>
            {serviceGroupTree}
        </section>
    );
};