import '@servicenow/now-modal';
import '@servicenow/now-heading';
import '@servicenow/now-input';
import userViewSaveAsModalEffect from './userViewSaveAsModalEffect';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import CONSTANTS from './userViewsConstants';
import {get as _get, debounce as _debounce} from 'lodash';
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';

const FOOTER_ACTIONS = {
	save_copy: {
		'label': CONSTANTS.LABELS.LABEL_SAVE_COPY,
		'variant': 'primary',
		'clickActionType': VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_REQUESTED
	},
	cancel: {
		'label': CONSTANTS.LABELS.LABEL_CANCEL,
		'variant': 'secondary',
		'clickActionType': VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_MODAL_STATE_CHANGED,
		'payload': { value: false }
	}
};


/**
 * Get the footer actions but apply the current view name to the Save button data
 * and disable it if required
 *
 * @param allowSave
 * @returns {{viewName: *, disabled: boolean}[]}
 */
const getFooterActions = (allowSave, inputRef) => {
	const footerActions = {
		...FOOTER_ACTIONS,
		save_copy: {
			...FOOTER_ACTIONS.save_copy,
			disabled: Boolean(!allowSave),
			getViewNameFromInput: () => {
				return inputRef.current.value;
			}

		}
	};
	return Object.values(footerActions);
};

const handleOninput = _debounce((e, dispatch) => {
	const value = _get(e, 'path[0].value', null);
	if (value) {
		dispatch('NOW_INPUT#VALUE_SET', {value});
	}
}, 200);

/**
 *
 * @param args
 * @returns {*}
 */
export const getUserViewSaveAsModal = args => {
	const {
		isValid,
		errors,
		viewNameSuggestion,
		dispatch
	} = args;

	const inputRef = createRef();
	const footerActions = getFooterActions(isValid, inputRef);
	return (
		<now-modal
			className='save-as-form'
			component-id={userViewSaveAsModalEffect.COMPONENT_IDS.USER_VIEW_SAVE_AS_MODAL_ID}
			opened
			append-to-payload={{ dispatches: VIEW_CONSTANTS.ACTION_TYPES.USER_VIEW_SAVE_AS_MODAL_STATE_CHANGED }}
			size='md'
			header-label={CONSTANTS.LABELS.HEADER_SAVE_AS}
			footer-actions={footerActions}>

			<now-heading
				hasNoMargin
				variant='title-secondary'
				level='1'
				label={CONSTANTS.LABELS.HEADER_DESCRIPTION}
				className='now-p-block-end--xl'
			/>

			<now-input
				autofocus
				required
				type='text'
				label={CONSTANTS.LABELS.TITLE_VIEW_NAME}
				placeholder={CONSTANTS.LABELS.VIEW_NAME_PLACEHOLDER}
				value={viewNameSuggestion}
				ref={inputRef}
				minlength={1}
				maxlength={40}
				className='full-width'
				manageInvalid
				invalid={!isValid}
				on-input={(e) => handleOninput(e, dispatch)}
				messages={errors ? [errors] : []}
			/>

		</now-modal>
	);
};