/**
 * Generates id for current preview data state.
 * Designed to be used as key to prevent jsx redraws on preview popover.
 * @param {*} servicePreviewResource 
 */
export const generatePreviewId = servicePreviewResource => {
	if (!servicePreviewResource)
		return 'none';

	const {
		serviceId, tilemapId, timestamp
	} = servicePreviewResource;

	return `preview_${tilemapId}_${serviceId}_${timestamp}`;
};

/**
 * Preview popover key doesn't have the timestamp.
 * It's a stable key that reduce renderings of <noc-preview-popover> element on DOM on each change in tiles data.
 * @param {*} servicePreviewResource 
 */
export const generatePreviewPopoverKey = servicePreviewResource => {
	if (!servicePreviewResource)
		return 'none';

	const {
		serviceId, tilemapId
	} = servicePreviewResource;

	return `preview_popover_${tilemapId}_${serviceId}`;
};