import nocPopoverOpenCloseEffect from './nocPopoverOpenCloseEffect';
import {createActionConfig} from '@devsnc/library-opws-mapping-shared';

export const nocPopoverBehavior = {
		name: 'nocPopoverBehavior',
		actionHandlers: {
				[nocPopoverOpenCloseEffect.ACTION_TYPES.NOC_POPOVER_OPEN]:
						createActionConfig(nocPopoverOpenCloseEffect.effectHandlers.nocPopoverOpenEffect),

				[nocPopoverOpenCloseEffect.ACTION_TYPES.NOC_POPOVER_FIT]:
						createActionConfig(nocPopoverOpenCloseEffect.effectHandlers.nocPopoverFitEffect),

				[nocPopoverOpenCloseEffect.ACTION_TYPES.NOC_POPOVER_CLOSED]:
						createActionConfig(nocPopoverOpenCloseEffect.effectHandlers.nocPopoverCloseEffect)
		}
}