import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {LOGGING, checkForError, getValue, onNextFrame, colorsAndIconsEffect} from '@devsnc/library-opws-mapping-shared';

import {
    get as _get,
    isArray as _isArray
} from 'lodash';

const ACTION_TYPES = {
    NOC_GET_COLORS_AND_ICONS: 'NOC_GET_COLORS_AND_ICONS',
    NOC_GET_CHOICE_FIELDS: 'NOC_GET_CHOICE_FIELDS',
    CATEGORIZED_FIELDS_EFFECT_STARTED: 'CATEGORIZED_FIELDS_EFFECT_STARTED',
    CATEGORIZED_FIELDS_EFFECT_SUCCEEDED: 'CATEGORIZED_FIELDS_EFFECT_SUCCEEDED',
    CATEGORIZED_FIELDS_EFFECT_FAILED: 'CATEGORIZED_FIELDS_EFFECT_FAILED',
    CATEGORIZED_FIELDS_EFFECT_SET_STATE: 'CATEGORIZED_FIELDS_EFFECT_SET_STATE',
    CATEGORIZED_FIELDS_EFFECT_CHOICES_MODEL_SET_STATE: 'CATEGORIZED_FIELDS_EFFECT_CHOICES_MODEL_SET_STATE'
};

const constructGQLQuery = field => `
    ${field}: GlideLayout_Query {
            choiceDataRetriever(tableName: $tableName, fieldName: $${field}) {
              choice {
                value
                label
              }
            }
     }
     `;

const constructGQLContent = categorizedByFields => {
    let queryContent = '';
    for (const field of categorizedByFields)
        queryContent += constructGQLQuery(field);

    return queryContent;
};

const constructGQLDeclaration = categorizedByFields => {
    let queryVariablesDeclaration = '';
    for (const field of categorizedByFields)
        queryVariablesDeclaration += `, $${field}: String!`;

    return queryVariablesDeclaration;
};


const constructGQL = categorizedFields => {
    const declaration = constructGQLDeclaration(categorizedFields);
    const content = constructGQLContent(categorizedFields);
    return `query getCategorizedChoices($tableName: String! ${declaration}) {
                ${content}
            }`;
};

const fetchFieldsChoicesEffect = (categorizedFields, coeffects) => {
    const gql = constructGQL(categorizedFields);
    const qglHandler = createGraphQLEffect(gql, {
        startActionType: ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_STARTED,
        successActionType: ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_SUCCEEDED,
        errorActionType: ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_FAILED,
        variableList: ['tableName'].concat(categorizedFields)
    });

    if (!qglHandler || !_isArray(qglHandler.args))
        throw new Error('createGraphQLEffect expected to return object with `args` array.');

    qglHandler.args.push(coeffects);
    return qglHandler;
};

const constructPayloadAction = (action, field) => {
    action[field] = field;
    return action;
};

const getChoices = {
    effect(coeffects) {
        const {action} = coeffects;
        LOGGING.log(`categorizedFieldsEffect-> getChoices called by action ${action.type}`);
    },
    interceptors: [{
        after(context) {
            const {coeffects, effects} = context;
            const {state} = coeffects;
            const {categorizedByFields = []} = state.properties.tilemapsConfig;
            const categorizedFields = categorizedByFields.map(getValue).filter(f => f !== 'severity'); //severity is redundant, existing property on state
            const {dispatch} = coeffects;

            //If no categorized fields -> just hop to the next step - setting the model
            if (categorizedFields.length === 0) {
                dispatch(ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_SUCCEEDED, {});
            }

            const payload = categorizedFields.reduce(constructPayloadAction, {tableName: 'cmdb_ci_service_auto'});
            const newCoeffects = {
                ...coeffects,
                action: {
                    ...coeffects.action,
                    payload
                }
            };

            const extendedEffects = [
                ...effects,
                fetchFieldsChoicesEffect(categorizedFields, newCoeffects)
            ];

            return {
                ...context,
                effects: extendedEffects,
                coeffects: newCoeffects
            };
        }
    }]
};

const transform = (options, choiceField) => options.reduce((obj, item, currentIndex) => {
    const key = (choiceField === 'severity') ? currentIndex + 1 : getValue(item);
    obj[key] = item;
    return obj;
}, {});

const relevantSeveritiesFilter = f => (f.value !== '' && f.value !== '0');

const setChoicesFieldResource = async coeffects => {
    const {
		dispatch,
		updateState,
		action: {payload, type}
	} = coeffects;

    LOGGING.log(`categorizedFieldsEffect:setChoicesFieldResource called by action ${type}`);
    const errors = checkForError({payload, dispatch, caller: 'categorizedFieldsEffect:setChoicesFieldResource'});

    if (errors)
        return;

    const {data = []} = payload;
    const choicesFieldResource = {};
    for (const choiceField in data) {
        const {choiceDataRetriever: {choice}} = data[choiceField];
        const options = (choiceField === 'severity') ? choice.filter(relevantSeveritiesFilter) : choice;
        choicesFieldResource[choiceField] = transform(options, choiceField);
    }

    updateState({choicesFieldResource});

    await onNextFrame();

    dispatch(ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_SET_STATE, {});
};

const valueNotSeverityFilter = v => v !== 'severity';

const getColorsAndIcons = coeffects => {
    const {action, dispatch, properties} = coeffects;
    LOGGING.log(`categorizedFieldsEffect-> getColorsAndIcons called by action ${action.type}`);

    const {categorizedByFields = []} = properties.tilemapsConfig;

    //severity is redundant, existing property on state
    const categorizedFields = categorizedByFields.map(getValue).filter(valueNotSeverityFilter); 
    
    dispatch(colorsAndIconsEffect.ACTION_TYPES.COLORS_REQUEST,
        colorsAndIconsEffect.getVariables(categorizedFields));
};

const fieldIsSeverityFilter = f => f.value === 'severity';

const mergeContentAndIcons = async coeffects => {
    const {
        dispatch,
        state,
        properties,
		updateProperties,
		action: {type}
    } = coeffects;
    
    LOGGING.log(`NOC: categorizedFieldsEffect-> mergeContentAndIcons called by action ${type}`);

    const {choicesFieldResource} = state;

    //If already did the merge or we don't have enough data for it-> do nothing
    if (properties.choicesFieldsModel || 
        !choicesFieldResource || 
        !state.colorsAndIconsResource || 
        !properties.severities)
        return;

    //Iterate over all fields (except severity)
    
    const choicesFieldsModel = {};
    for (const category in choicesFieldResource) {
        choicesFieldsModel[category] = {};
        for (const index in choicesFieldResource[category]) {
            const item = choicesFieldResource[category][index];
            const key = getValue(item);
            const style = state.colorsAndIconsResource.result[category][key];
            choicesFieldsModel[category][index] = {
                ...item,
                ...style
            };
        }
    }

    //Insert also the severities if requested
    const {categorizedByFields = []} = properties.tilemapsConfig;
    const isSeverityEnabled = categorizedByFields.filter(fieldIsSeverityFilter).length > 0;
    if (isSeverityEnabled)
        choicesFieldsModel['severity'] = {...properties.severities};

    updateProperties({choicesFieldsModel});

    await onNextFrame();

    dispatch(ACTION_TYPES.CATEGORIZED_FIELDS_EFFECT_CHOICES_MODEL_SET_STATE, {});
};

const onTilemapConfigSet = ({dispatch}) => {
    dispatch(ACTION_TYPES.NOC_GET_CHOICE_FIELDS, {});
    dispatch(ACTION_TYPES.NOC_GET_COLORS_AND_ICONS, {});
};

const effectHandlers = {
    onTilemapConfigSet,
    getChoices,
    getColorsAndIcons,
    setChoicesFieldResource,
    mergeContentAndIcons
};

export default {
    ACTION_TYPES,
    effectHandlers
};

