
import {LOGGING, CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {nocPopoverBehavior} from "../noc-popover-behavior";

/**
 * Handler to open service preview popover for given serviceId.
 * @returns {Function}
 */
export const createOpenPreviewHandler = args => () => {
	const {
		serviceId, 
		tilemapId,
		dispatch, 
		focusTarget,
		servicePreviewData,
		timestampForTiles: timestamp
	} = args;

	const {
		sysId: previewServiceId, 
		tilemapId: previewTilemapId
	} = servicePreviewData || {};

	//if same tile clicked after preview is already open, operation will be stopped here.
	if (previewServiceId === serviceId && previewTilemapId === tilemapId) {
		LOGGING.log(`openPreviewHandler requested preview that is already open, stopping: 
			serviceId: ${serviceId}
			tilemapId: ${tilemapId}`);
			
		return;
	}

	//bring preview data, this will render the popover
	dispatch(CONSTANTS.ACTION_TYPES.SERVICE_PREVIEW_REQUESTED, {serviceId, tilemapId, timestamp});
	//store state that will be needed when popover is closed
	dispatch(nocPopoverBehavior.ACTION_TYPES.NOC_POPOVER_OPEN, {focusTarget});
};