import {invokeOnNextFrame} from '@devsnc/library-opws-mapping-shared';
import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {getBestFitInfo} from "@servicenow/behavior-fit";
import {nocPopoverBehavior} from '../noc-popover-behavior';

const OFFSET_PX = 24;

const detectChangeInPositionStyle = (curr, next) => {
	return (curr.left !== next.left || curr.top !== next.top);
};

const fitPopover = args => {
	const {
		dispatch,
		id,
		popoverTargetRef,
		popoverContentRef,
		popoverContainerRef,
		currPositionStyle,
		isInsert
	} = args;

	const hookUpdate = (insertvNode, updatevNode) => invokeOnNextFrame(() => {
		const {elm} = (isInsert ? insertvNode : updatevNode);
		const contentComponent = popoverContentRef.current;
		const contentBox = contentComponent.getBoundingClientRect();
		
		const args = {
			target: popoverTargetRef.current,
			content: elm,
			contentArea: window,
			container: popoverContainerRef.current,
			offset: [OFFSET_PX, OFFSET_PX, 8, 8, 8, 8],
			position: [
				'center-end center-start',
				'center-start center-end',
				'top-end top-start',
				'top-start top-end', //FIXME this one has no offset by x.
				'bottom-start top-start',
				'top-end bottom-end'
			],
			constrain: {
				minHeight: parseInt(contentBox.height),
				minWidth: parseInt(contentBox.width)
			}
		};
		const fit = getBestFitInfo(args);
		const {style} = fit;

		const animation = {
			animationName: 'noc_popover_fade',
			animationDuration: '0.5s',
		};

		//https://code.devsnc.com/dev/emotive-design-system/issues/816
		//Ensuring offset of OFFSET_PX if fit didn't provide it.

		let topInt = parseInt(style.top) || 0;
		if (topInt + args.constrain.minHeight > args.container.clientHeight) {
			topInt = args.container.clientHeight - args.constrain.minHeight;
		}

		const left = `${Math.max(OFFSET_PX, parseInt(style.left) || 0)}px`;
		const top = `${Math.max(OFFSET_PX, topInt)}px`;

		const positionStyle = {
			opacity: '1',
			...animation, //TODO condition to put slide animation when popover was already open
			left,
			top
		};

		//if calculated fit style was changed since previous calculation, then store it on state
		if (detectChangeInPositionStyle(currPositionStyle, positionStyle))
			dispatch(nocPopoverBehavior.ACTION_TYPES.NOC_POPOVER_FIT, {positionStyle, id});

		else
			LOGGING.log('fitPopover repeated calculation. Skipping update state.');
	});

	return hookUpdate;
};


export const fitPopoverOnUpdate = fitPopover;
export const fitPopoverOnInsert = args => fitPopover({...args, isInsert: true});