import '@servicenow/now-dropdown';
import { LOGGING } from '@devsnc/library-opws-mapping-shared';
import CONSTANTS from './userViewsConstants';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import "@servicenow/now-button";
import VIEW_CONSTANTS from '../noc-user-view-behaviors/constants';
import {isViewGlobal} from '../noc-user-view-behaviors/userViewsUtils';
import {isEmpty as _isEmpty} from 'lodash';

const PRIVATE = VIEW_CONSTANTS.VIEW_SECTIONS.PRIVATE;

const CONFIG_SELECT_ARIA = {
	'aria-label': CONSTANTS.LABELS.CONFIG_SELECT_TOOLTIP
};

const CONFIG_SAVE_ARIA = {
	'aria-label': CONSTANTS.LABELS.CONFIG_SAVE_TOOLTIP
};

const MORE_ACTIONS_ARIA = {
	'aria-label': CONSTANTS.LABELS.MORE_ACTIONS_ARIA
};

export const view = (state) => {
	const {
		userViewSaveAsModal = {},
		views: {
			viewItems = [],
			selectedView,
		},
		noChangesOnCurrentView,
		currentView
	} = state.properties;

    const dropDownViewItems = viewItems.filter(item => {
        return (item.label !==  PRIVATE || (item.label === PRIVATE && !_isEmpty(item.children)))
    });

	const isGlobalView = isViewGlobal(currentView);

	const moreActionsItems = [{
		id: 'save_as',
		label: CONSTANTS.LABELS.LABEL_SAVE_COPY,
		action: CONSTANTS.ACTION_TYPES.VIEW_SAVE_AS_CLICKED
	},
	{
		id: 'revert',
		label: CONSTANTS.LABELS.LABEL_REVERT,
		action: VIEW_CONSTANTS.ACTION_TYPES.REVERT_CURRENT_USER_VIEW,
		disabled: noChangesOnCurrentView
	}
	];

	LOGGING.info(`Selected view ${selectedView}`, { caller: 'snNocViewsView.view' });
	const viewsList = <now-dropdown
		variant='secondary'
		size='md'
		manage-selected-items={true}
		items={dropDownViewItems}
		selected-items={selectedView}
		on-change={CONSTANTS.ACTION_TYPES.VIEW_NAME_SELECTED}
		config-aria={CONFIG_SELECT_ARIA}
		placeholder={CONSTANTS.LABELS.CONFIG_SELECT_PLACEHOLDER}
		tooltip-content={CONSTANTS.LABELS.CONFIG_SELECT_TOOLTIP}
	/>;

	const viewActions = <now-dropdown
		className='now-m-inline-start--sm'
		variant='secondary'
		size='md'
		icon='ellipsis-h-fill'
		items={moreActionsItems}
		config-aria={MORE_ACTIONS_ARIA}
		tooltip-content={CONSTANTS.LABELS.CONFIG_MORE_ACTIONS_TOOLTIP}
		hide-caret
		select='none'
	/>;

	const saveViewButton = <now-button
		className='now-m-inline-start--sm'
		variant='primary'
		label={CONSTANTS.LABELS.LABEL_SAVE}
		size='md'
		config-aria={CONFIG_SAVE_ARIA}
		tooltip-content={CONSTANTS.LABELS.CONFIG_SAVE_TOOLTIP}
		disabled={noChangesOnCurrentView || isGlobalView}
	/>

	return (
		<Fragment>
			{viewsList}
			{saveViewButton}
			{viewActions}
		</Fragment>
	);
};