import {getDisplayValue, getValue} from '@devsnc/library-opws-mapping-shared';

/**
 * Call filter on enabled population by the key term function searched in all related fields
 *
 * @param groupedPopulation
 * @param state
 */
export const searchFilterPopulation = (groupedPopulation, state) => {
	const {
		searchTerm,
		tilemapsFormat: {businessServiceGroup},
		tilemapsConfig: {searchFields}
	} = state.properties;

    if (!searchFields || searchFields.length === 0) {
        return groupedPopulation;
    }

    let requiredFields = searchFields.map(getValue);
    const fn = (businessServiceGroup)? serviceGroupsPopulationFilter : servicesPopulationFilter;
	return fn(groupedPopulation, searchTerm, requiredFields);
};

/**
 * Population filter.
 * The population could be filtered according to the group by fields and hover tooltip fields.
 */
const enabledPopulationFilter = function(it) {
	const {searchTerm, searchFields} = this;
	const searchTermLC = searchTerm.toLowerCase();

	// Go over all relevant fields and see if they contain the search term
	for (let i = 0; i < searchFields.length; i++) {
		const field = searchFields[i];
		const displayValue = getDisplayValue(it[field]).toLowerCase();

		if (displayValue.includes(searchTermLC)) {
			return true;
		}
	}
	return false;
};

/**
 *
 * @param population
 * @param searchTerm - the term that the user wants to search
 * @param searchFields - the fields that the search term is searched in
 * @returns {{filteredPopulation: *}}
 */
const servicesPopulationFilter = (population, searchTerm, searchFields) => {
	if (!searchTerm) {
		return population;
	}

	const filteredPopulation = population.filter(enabledPopulationFilter, {searchTerm, searchFields});
	return filteredPopulation;
};


/**
 *
 * @param population
 * @param searchTerm - the term that the user wants to search
 * @param searchFields - the fields that the search term is searched in
 * @returns {{filteredPopulation: *}}
 */
export const serviceGroupsPopulationFilter = (population, searchTerm, searchFields) => {
    if (!searchTerm)
        return population;

    const filteredPopulation = population.map(group=>{
        const servicesFiltered = group.population.filter(enabledPopulationFilter, {searchTerm, searchFields});
        return {
            ...group,
            population: servicesFiltered
        };
    });
    return filteredPopulation;
};
