import {createNocElement} from '@devsnc/library-opws-mapping-shared';
import {nocSeverityIndicatorView} from './nocSeverityIndicatorView';
import styles from './nocSeverityIndicator.scss';
import effectNocSeverityIndicator from './effectNocSeverityIndicator';


/**
 * Seismic component noc-severity-indicator.
 * Implements single toggle for severity.
 * Defines toggle color and label.
 * Toggle label also has severity counter out of current population.
 */
createNocElement('noc-severity-indicator', {
    view: nocSeverityIndicatorView,
    styles,
    properties: {
        severity: {
            default: {}
        },
        count: {
            default: 0
        },
        percentage: {
            default: 0
        }
    },
    eventHandlers: [{
        // DEF0070357 - for some reason, clicking on Enter/Space causes two events to fire, MouseEvent and KeyUp
        //              because we count on the current state (stateful), calling this effect twice causes the bug
        events: ['click'],//, 'keyup'
        effect: effectNocSeverityIndicator.effectHandlers.indicatorClicked
    }]
});
