import nocConfigEffect from './nocConfigEffect';
import userMetaEffect from './userMetaEffect';
import getPopulationEffect from './getPopulationEffect';
import initChangesManagerEffect from './initChangesManagerEffect';
import {LOGGING, CHANGE_EVENT_CONSTANTS, CONSTANTS, createActionConfig} from '@devsnc/library-opws-mapping-shared';

const {GLOBAL_ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;

const initFlowHandler = {
	effect(coeffects) {
		const {state, dispatch} = coeffects;
		// const {workspace} = state.properties;
		//temporary removing workspace for WS integration
        const workspace = 'nocpit';
		const {ACTION_TYPES} = CHANGE_EVENT_CONSTANTS;
		const {PAYLOADS} = CHANGE_EVENT_CONSTANTS;

		LOGGING.log('dispatch NOC_CONFIG_REQUEST');
		dispatch(nocConfigEffect.ACTION_TYPES.NOC_CONFIG_REQUEST,
			nocConfigEffect.getVariables(workspace));

		LOGGING.log('dispatch USER_META_REQUEST');
		dispatch(userMetaEffect.ACTION_TYPES.USER_META_REQUEST, {});
	}
};

export const nocInitialDataBehavior = {
	name: 'nocInitialDataBehavior',
	actionHandlers: {
		[CONSTANTS.ACTION_TYPES.NOC_COMPONENT_CONNECTED]:
			createActionConfig(initFlowHandler),

		[nocConfigEffect.ACTION_TYPES.NOC_CONFIG_REQUEST]:
			createActionConfig(nocConfigEffect.effectHandlers.nocConfigFetched),

		[userMetaEffect.ACTION_TYPES.USER_META_REQUEST]:
			createActionConfig(userMetaEffect.effectHandlers.userMetaFetched),

		[userMetaEffect.ACTION_TYPES.USER_META_REQUEST_SUCCEEDED]:
			createActionConfig(userMetaEffect.effectHandlers.handleUserMetaResponse),

		// [userMetaEffect.ACTION_TYPES.USER_META_REQUEST_FAILED]: //TODO reuse error handler]: 
		// 	createActionConfig(),

		[nocConfigEffect.ACTION_TYPES.NOC_CONFIG_REQUEST_SUCCEEDED]:
			createActionConfig(nocConfigEffect.effectHandlers.handleNocConfigResponse),

		// [nocConfigEffect.ACTION_TYPES.NOC_CONFIG_REQUEST_FAILED]: //TODO reuse error handler]: 
		// 	createActionConfig(),

		[CONSTANTS.ACTION_TYPES.NOC_CONFIG_SET]:
			createActionConfig(nocConfigEffect.effectHandlers.onNocConfigSet),

		[GLOBAL_ACTION_TYPES.NOC_INIT_CHANGE_MANAGER]:
			createActionConfig(initChangesManagerEffect.effectHandlers.initChangeHandler),

		[CONSTANTS.ACTION_TYPES.UBER_FILTER_SET]:
			createActionConfig(getPopulationEffect.effectHandlers.getPopulationHandler),

		[getPopulationEffect.ACTION_TYPES.NOC_POPULATION_REFRESH_REQUESTED]:
			createActionConfig(getPopulationEffect.effectHandlers.getPopulationHandler),

		[getPopulationEffect.ACTION_TYPES.EM_POPULATION_REQUEST]:
			createActionConfig(getPopulationEffect.effectHandlers.getEmPopulationEffectHandler),
	
		// [getPopulationEffect.ACTION_TYPES.BASE_POPULATION_REQUEST), //TODO getBasePopulationRequestHandler]: 
		// 	createActionConfig(),

		[getPopulationEffect.ACTION_TYPES.EM_POPULATION_REQUEST_SUCCEEDED]:
			createActionConfig(getPopulationEffect.effectHandlers.handleEmPopulationResponse),

		[getPopulationEffect.ACTION_TYPES.BASE_POPULATION_REQUEST_SUCCEEDED]:
			createActionConfig(getPopulationEffect.effectHandlers.handleBasePopulationResponse),

		 //[getPopulationEffect.ACTION_TYPES.POPULATION_REQUEST_FAILED]: //TODO reuse error handle]:
		 //	createActionConfig()
	}
};
