import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import {t} from 'sn-translate';

export const ORPHANS =  t('Other services');

const isGroup = nodeType =>{
   return  nodeType === 'GROUP';
}

const createGroupBucket = (displayValue, id) => ({
    groupField: {displayValue, value: displayValue, name: 'serviceGroup', id, isServiceGroup:true},
    population: []
});

const addBusinessServicesChildren = (setContainer, node) => {
    const {children} = node;
    for (const child of children) {
        const {data: {id, type}} = child;
        if (!isGroup(type)) {
            setContainer.add(id);
            continue;
        }
        addBusinessServicesChildren(setContainer, child);
    }
};

/**
 *  Distribute all Business Services into temporary containers, which represents only
 *The first level children of the selected node + a special system Children container -> to hold
 *All Business Services which resides directly under the selected node.
 *We use Set in order to ensure existing of only one instance of service per container.
 * @param selectedNodeRef - The parent node (the one that was selected in the Service Tree)
 * @returns {*}
 */
const splitIntoContainers = selectedNodeRef => {
    const {data: {id}} = selectedNodeRef;

    const containers = selectedNodeRef.children.reduce((result, child) => {
        const {data: {id, name, type}} = child;
        if (!isGroup(type)) {
            result[ORPHANS].services.add(id);
        } else {
            //In case of a GROUP
            if (!result[name])
                result[name] = {
                    id,
                    services: new Set()
                };

            addBusinessServicesChildren(result[name].services, child);
        }
        return result;
    }, {
        [ORPHANS]:{
            id,
            services: new Set()
        }
    });

    return containers;
};

export const constructServiceGroupsBuckets = function (populationMapper, selectedNodeRef) {
    if (!populationMapper || !selectedNodeRef || !selectedNodeRef.children) {
        LOGGING.warn('NOC: constructServiceGroupsBuckets didn\'t get the mandatory arguments', populationMapper, selectedNodeRef);
        return null;
    }
    const containers = splitIntoContainers(selectedNodeRef);
    const buckets = Object.keys(containers).map(key =>{
        const {id, services} = containers[key];
        const bucket = createGroupBucket(key, id);

        services.forEach(id => {
            const populationMapperItem = populationMapper[id];
            if (populationMapperItem.isEnabled)
                bucket.population.push(populationMapperItem);
        });

        return bucket;
    });

    return buckets;
};