import {createNocUpdateStateEffect, CONSTANTS} from '@devsnc/library-opws-mapping-shared';

const ACTION_TYPES = {

};

//mapping from change type to Action for refresh request of certain part of application. 
const refreshRequestByChangeType = {
	DASHBOARD_CHANGE: CONSTANTS.ACTION_TYPES.NOC_POPULATION_REFRESH_REQUESTED,
	ALERT: CONSTANTS.ACTION_TYPES.NOC_ALERTS_REFRESH_REQUESTED
};

const nocOnChangeEffect = createNocUpdateStateEffect((state, action, getAfterStateUpdate) => {
	const {payload} = action;
	const arrivedTimestamp = payload.value;
	const currTimestamp = state.properties.timestampForTiles;
	const refreshRequestAction = refreshRequestByChangeType[payload.type];

	//DEF0037195: Services in 'Service Group' doesn't refresh as needed
	//DEF0037045: Alert panel is not refreshing
	if (!refreshRequestAction)
		return state;
		

	//If new timestamp arrived, then updating it in property 'timestampForTiles'
	const newState = (currTimestamp === arrivedTimestamp) ? state : {
		...state,
		properties: {
			...state.properties,
			//update timestampForTiles on DASHBOARD_CHANGE
			//this timestamp used to eliminate duplicate refresh on population
			timestampForTiles: arrivedTimestamp
		}
	};
	
	getAfterStateUpdate().then(dispatch => {
		dispatch(refreshRequestAction, {timestamp: arrivedTimestamp});
	});
	
	return newState;
});

const effectHandlers = {
	nocOnChangeEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};