import {CONSTANTS, nocModalActionEffect} from '@devsnc/library-opws-mapping-shared';
import TILEMAP_CONSTANTS from './constants';
import {isObject as _isObject} from 'lodash';
import nocTilemapSelectGroupEffect from '../noc-tilemap-data-behavior/nocTilemapSelectGroupEffect';

const {ACTION_TYPES: modalActionTypes} = nocModalActionEffect;

const nocSetAndSaveTileFormat = {
	effect(coeffects) {
		const {payload} = coeffects.action;
		const {dispatch, state} = coeffects;
		const {tilemapsFormat} = state.properties;

		if (!_isObject(tilemapsFormat)) {
			return;
		}

		const newTilemapsFormat = {
			...tilemapsFormat,
			[payload.type]: payload.value
		};

		// if groupBy, selection should clear businessServiceGroup and selectedGroup, if exists
		if (payload.type === TILEMAP_CONSTANTS.DROPDOWN_TYPES.GROUP_BY.type) {
			newTilemapsFormat.businessServiceGroup = '';
			dispatch(nocTilemapSelectGroupEffect.ACTION_TYPES.TILEMAP_SELECT_REGULAR_GROUP, {selectedGroup: ''});
		}

		// and update groupBy field on user_view (properties)
		dispatch(CONSTANTS.ACTION_TYPES.TILEMAPS_FORMAT_SAVE_REQUESTED, {tilemapsFormat: newTilemapsFormat });
	}
};

const openAlertsPanelEffect = {
	effect(coeffects) {
		const {dispatch} = coeffects;
		dispatch(CONSTANTS.ACTION_TYPES.TOGGLE_SIDE_PANEL, {open: true});
		dispatch(CONSTANTS.ACTION_TYPES.NOC_GET_ALERTS_INIT);
	}
};

const openGroupTreeEffect = {
	effect(coeffects) {
		const {dispatch} = coeffects;

		dispatch(modalActionTypes.NOC_ACTION_CLICK, {isModalOpen: true});
	}
};

export const effectHandlers = {
	nocSetAndSaveTileFormat,
	openAlertsPanelEffect,
	openGroupTreeEffect
};

export default {
	effectHandlers
};
