import {getDisplayValue, getValue} from '@devsnc/library-opws-mapping-shared';
import {i18n} from '@devsnc/library-opws-mapping-shared';

/**
 *
 * @param groupedPopulation
 * @param state
 * @returns {*}
 */
export const groupByPopulation = (groupedPopulation, state) => {
	const {properties: {tilemapsFormat}} = state;

	const newGroupedPopulation = runGroupByStrategy(groupedPopulation, state, tilemapsFormat.groupByField);

	return newGroupedPopulation;
};

/**
 *  Return the array grouped by groupByField
 *
 * @param groupedPopulation
 * @param state
 * @param groupByField
 * @returns {*}
 */
const runGroupByStrategy = (groupedPopulation, state, groupByField) => {

	const newGroupedPopulation = groupedPopulation.reduce((result, item) => {
		const groupItemFieldValue = groupByField ? item[groupByField] : '';
		const fieldValue = (groupItemFieldValue) ? groupItemFieldValue : i18n.NOC_DEFAULT_GROUP_LABEL;
		const groupKey = getValue(fieldValue);
		const type  =  (groupItemFieldValue) ? groupItemFieldValue.type || 'string' : 'string';

		let newResult;

		let group = result[groupKey];
		if (!group) {

			group = {
				groupField: {
					displayValue: getDisplayValue(fieldValue) || i18n.NOC_DEFAULT_GROUP_DISPLAY_VALUE,
					value: groupKey,
					name: groupByField,
					type: type
				},
				population: []
			};

			// mutate only if required to add a new group
			newResult = {
				...result,
				[groupKey]: group
			};
		} else {
			newResult = result;
		}

		group.population.push(item);

		return newResult;
	}, {});

	return newGroupedPopulation;
};
