const LABEL_CLOSE = t('Close');
const buttonLabelTruncateConfig = {
	separator: ' ',
	length: 21
};
//DEF0047223: truncating labels to ensure that buttons won't overflow when translation applied.
const VIEW_SERVICE_DETAILS = _truncate(t('Service Details'), buttonLabelTruncateConfig);
const VIEW_SERVICE_MAP = _truncate(t('Service Map'), buttonLabelTruncateConfig);
const BREAKDOWN_SEVERITY = t('Severity breakdown');

const OPEN_SERVICE_DETAILS = t('Open service details');
const OPEN_SERVICE_MAP = t('Open service map');
const TOOLTIP_PRIMARY_ALERTS = t('Open primary alerts');

const CLOSE_BUTTON_TOOLTIP_ID = 'CLOSE_BUTTON_TOOLTIP_ID';
const ALERT_BREAKDOWN_ITEM_TOOLTIP_ID = 'ALERT_BREAKDOWN_ITEM_TOOLTIP_ID';
const TOTAL_PRIMARY_ALERTS_TOOLTIP_ID = 'TOTAL_PRIMARY_ALERTS_TOOLTIP_ID';

// TODO for Orlando
const SERVICE_MAP_IN_WORKSPACE = true;

