const openPrimaryAlertsForm = (serviceId, className, dispatch) => () => {
	// Related Alerts by class
	const DEFAULT_TABS = {
		cmdb_ci_service_discovered: 'REL:1651427b5fa033003983c87a3f7313d5',
		cmdb_ci_query_based_service: 'REL:d2e3ceb85f9973003983c87a3f731328'
	};

	dispatch('PREVIEW_RECORD', {
		table: className,
		sys_id: serviceId,
		params: {defaultTab: DEFAULT_TABS[className]}
	});
};

const openServiceForm = (serviceId, className, dispatch) => () => {
	dispatch('ITEM_SELECTED', {
		table: className,
		sys_id: serviceId,
		parent_table: className,
		parent_sys_id: serviceId
	});
};


const openServiceMap = (serviceId, name, className, dispatch) => () => {
	const isEm = true;
	//TODO:abed remove 'SERVICE_MAP_IN_WORKSPACE` at all after verifying that the map opened as expected in tab
	if (SERVICE_MAP_IN_WORKSPACE) {
		const mapId = genUniqueSysId(serviceId);
		//referenced to a record in cmdb_ci_service_discovered
		dispatch('ITEM_SELECTED', {
			table: 'noc_service_map',
			sys_id: mapId,
			params: {
				componentId: mapId,
				isEm,
				serviceId
			},
			parent_table: className,
			parent_sys_id: serviceId
		});
	} else {
		const bsName = `&sysparm_bsname=${encodeURIComponent(name)}`;
		const noBackButton = '&sysparm_no_back=true';
		const uri = CONSTANTS.GLOBALS.BS_URL
			.replace('{0}', serviceId)
			.replace('{1}', isEm ? CONSTANTS.GLOBALS.ASSURANCE : CONSTANTS.GLOBALS.MAPPING)
			.concat(bsName).concat(noBackButton);

		window.open(uri, '_blank');
	}
};

/**
 * HOC to create `on-click` handler for [close] button on service preview.
 * Sends action to clear service preview.
 */
const closePreview = dispatch => event => {
	if (event) {
		event.stopImmediatePropagation();
	}

	dispatch(nocServicePreviewBehaviors.ACTION_TYPES.NOC_CLEAR_SERVICE_PREVIEW, {});
};

const layoutOverviewFields = field => ({
	label: String(field.displayName) || ' ',
	value: String(field.displayValue) || ' '
});

const getNonEmptyDisplayValueItems = item => {
  return !_isEmpty(item.displayValue);
};
const generateJsxHeader = (servicePreviewData, workspace, dispatch) => {
	const {
		name,
		color,
		overviewFields,
		className,
		iconUrl
	} = servicePreviewData;

	const backgroundContrast = shouldUseWhiteText(color);
	const headerTextColor = `header${backgroundContrast ? ' white-text' : ''}`;

	const style = {
		backgroundColor: color
	};

	const filteredOverviewFields = overviewFields.filter(getNonEmptyDisplayValueItems);
	const overviewItems = filteredOverviewFields.map(layoutOverviewFields);

	//TODO need 'favorite' or 'star' icon
	// const favoriteIcon = favorite ? 'address-book-fill' : 'address-book-outline';
	//const iconWeight = favorite ? 'bold' : 'regular';

	const CLOSE_BUTTON_CONFIG_ARIA = {'aria-label': LABEL_CLOSE};

	return (
		<div className={headerTextColor} style={style}>
			<div className='title'>
				<now-image className='icon' src={iconUrl} alt={className}/>
				<now-heading className='name' label={name} variant='title-primary' hasNoMargin level='3' wont-wrap/>
				{/*
				 //TODO: We are not supporting favorites in NY need to return this in O

				 <span className='favorite'>
				 <eds-legacy-icon
				 fixed-width
				 size='lg'
				 weight={iconWeight}
				 type='Star'
				 on-click={favoriteToggle(dispatch, favorite, workspace, sysId)}/>
				 </span> */}

				<noc-element-with-tooltip
					className='close-button-wrapper'
					tooltip={LABEL_CLOSE}>

					<now-button-iconic
						className='button'
						aria-labelledby={CLOSE_BUTTON_TOOLTIP_ID}
						bare
						icon='close-outline'
						size='md'
						variant='tertiary'
						config-aria={CLOSE_BUTTON_CONFIG_ARIA}
						//tooltip-content={LABEL_CLOSE} //- ##BUG??## tooltip position is off-charts
						on-click={closePreview(dispatch)}/>

					<label className='now-a11y-label' id={CLOSE_BUTTON_TOOLTIP_ID}>
						{LABEL_CLOSE}
					</label>

				</noc-element-with-tooltip>

			</div>
			<now-label-value-stacked className='eds-label' align='horizontal' items={overviewItems} truncated/>
		</div>
	);
};

const generateJsxSeverityBreakdownItem = (metadata, item) => {

	const displayValue = String(item.display_value);
	metadata.primaryAlertsAmount += Number(item.value);

	return (
		<li>
			<noc-element-with-tooltip
				tooltip={TOOLTIP_PRIMARY_ALERTS}>

				<now-highlighted-value
					aria-describedby={ALERT_BREAKDOWN_ITEM_TOOLTIP_ID}
					label={`${displayValue} ${item.display_name}`}
					show-icon
					status={EM_SEVERITY_TO_EDS_STATUS[String(item.name)]}/>

				<label className='now-a11y-label' id={ALERT_BREAKDOWN_ITEM_TOOLTIP_ID}>
					{TOOLTIP_PRIMARY_ALERTS}
				</label>

			</noc-element-with-tooltip>
		</li>
	);
};

const getNonZeroValueItems = item => {
    return !_isUndefined(item.value) && item.value !== "0" ;
};

const generateJsxSeverityBreakdown = (servicePreviewData, dispatch) => {
	const {severityBreakdown, sysId, className} = servicePreviewData;
	const metadata = {
		primaryAlertsAmount: 0
	};

	const filteredSeverityBreakdown = severityBreakdown.filter(getNonZeroValueItems);
	const severityListLI = filteredSeverityBreakdown.map(generateJsxSeverityBreakdownItem.bind(null, metadata));

	const {primaryAlertsAmount} = metadata;
	const PRIMARY_ALERTS_COUNT = t('{0} Primary alerts', primaryAlertsAmount);
	const labelId = 'severityLabel';

	return (
		<button
			className='severity-breakdown-button'
			on-click={openPrimaryAlertsForm(sysId, className, dispatch)}
			aria-labelledby={labelId}
			aria-describedby={TOTAL_PRIMARY_ALERTS_TOOLTIP_ID}>

			<noc-element-with-tooltip
				tooltip={TOOLTIP_PRIMARY_ALERTS}>
				<now-heading
					id={labelId}
					className='primary-alerts-label'
					label={PRIMARY_ALERTS_COUNT}
					variant='title-primary'
				/>
				<label className='now-a11y-label' id={TOTAL_PRIMARY_ALERTS_TOOLTIP_ID}>
					{TOOLTIP_PRIMARY_ALERTS}
				</label>
			</noc-element-with-tooltip>

			<label className='highlighted-label'>{BREAKDOWN_SEVERITY}</label>
			<ul className='severity-list'>
				{severityListLI}
			</ul>
		</button>
	);
};

/**
 * Check all severity breakdown elements to see if there is at least one alert,
 * in this case it will return true
 * @param servicePreviewData
 * @returns {boolean} true if there is at least one alert
 */
const hasAlerts = servicePreviewData => {

	const {severityBreakdown} = servicePreviewData;
	return _some(severityBreakdown, item => Number(item.value) > 0);
};

const getServiceLinks = (servicePreviewData, dispatch) => {
	const {
		sysId, 
		name, 
		className,
		hasMap = false
	} = servicePreviewData;
	
	const serviceFormButton = hasMap ?
		(
			<now-button
				className="button now-align -inline-end"
				on-click={openServiceMap(sysId, name, className, dispatch)}
				size='md'
				label={VIEW_SERVICE_MAP}
				tooltip-content={OPEN_SERVICE_MAP}
				variant='tertiary'/>
		) :
		null;

	return (
		<div className="buttons-container now-align">
			<now-button
				className="button now-align -inline-start"
				on-click={openServiceForm(sysId, className, dispatch)}
				label={VIEW_SERVICE_DETAILS}
				size='md'
				tooltip-content={OPEN_SERVICE_DETAILS}
				variant='primary'/>

			{serviceFormButton}
		</div>
	);
};

export const view = (state, dispatch) => {
	const {servicePreviewData} = state.properties;
	// const {servicePreviewData, workspace} =state.properties;
	//temporary removing workspace for WS integration
	const workspace = 'nocpit';

	if (!servicePreviewData) {
		return null;
	}

	const header = generateJsxHeader(servicePreviewData, workspace, dispatch);
	const severityBreakdown = hasAlerts(servicePreviewData) ? generateJsxSeverityBreakdown(servicePreviewData, dispatch) : null;
	const serviceLinks = getServiceLinks(servicePreviewData, dispatch);

	return (
		<section className='noc-service-preview'>
			{header}

			<div className='body'>
				{severityBreakdown}
				{serviceLinks}
			</div>
		</section>
	);
};
