import {CONSTANTS} from '@devsnc/library-opws-mapping-shared';
import {isEmpty as _isEmpty, isUndefined as _isUndefined} from 'lodash';

const {EM_SEVERITY_TO_EDS_STATUS} = CONSTANTS.GENERAL;

const constructTree = (severities, result, node) => {
    if(_isUndefined(node) || _isEmpty(node)) {
        return result;
    }

    const {
        data: {
            id,
            type,
            name,
            attributes
        },
        children = []
    } = node;

    const {severity} = attributes;

    if (type !== 'GROUP')
        return result;

    const newChildren = children.reduce(constructTree.bind(null, severities), []);

    const highlightedValueProps = [
        {
            label: severities[severity].label,
            status: EM_SEVERITY_TO_EDS_STATUS[severity]
        }
    ];

    const newTree = {
        id,
        label: name,
        children: newChildren,
        highlightedValueProps,
        actionable: true
    };

    if (newChildren.length > 0)
        newTree.iconName = 'document-outline'; //TODO:Need to be changed to folder icon

    result.push(newTree);
    return result;
};

export default function (severities, tree) {
    if (!severities || !tree) {
        console.error('NOC: constructServiceGroupsTree didn\'t get the mandatory arguments',severities,  tree);
        return null;
    }
    const serviceGroupTree = constructTree(severities, [], tree);
    return serviceGroupTree;
};