import {LOGGING} from '@devsnc/library-opws-mapping-shared';
import '@devsnc/library-opws-mapping-shared/src/components/sn-opws-is-visible';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {getNocSidePanelJsx} from './snNocSidePanelJsx.js';
import {getNocTilemapContainerJsx} from './snNocTilemapContainerJsx.js';
import {getNocSpectrumJsx} from './snNocSpectrumJsx';
import nocOnVisibleChangeEffect from './nocVisibilityChangeEffect';

const packageJson = require('../../package.json');
LOGGING.info(`${packageJson.name} package version: ${packageJson.version}`, {force: true});

export const view = state => {
    const {nocConfig} = state.properties;

    if (!nocConfig)
        return null;

    const {isEm} = nocConfig;
    const nocAppRef = createRef();
    const workspace = 'nocpit';

    const opwsIsVisible = <sn-opws-is-visible
        anchor-elm={nocAppRef}
        action-on-change={nocOnVisibleChangeEffect.ACTION_TYPES.NOC_VISIBILITY_CHANGED}/>;

    const spectrumJsx   =  getNocSpectrumJsx(state, isEm);
    const tilemapContainerJsx = getNocTilemapContainerJsx(state, nocAppRef, workspace, isEm);

    // because the side panel needs the tilemap container on its 'side', we first want to make sure we got it
    const nocSidePanelJsx =  tilemapContainerJsx ? getNocSidePanelJsx(state, isEm) : null;

    return (
        <main className='noc-app-root' ref={nocAppRef} >
            {opwsIsVisible}
            {spectrumJsx}
            <section className='noc-app-root-section'>
                {tilemapContainerJsx}
                {nocSidePanelJsx}
            </section>
        </main>
    );
};
