import {nocServicePreviewBehaviors} from '../noc-service-preview-behaviors';

const ENTER_KEY_CODE = 13;
const SPACE_KEY_CODE = 32;

const nocPopoverClickedEffect = coeffects => {
    const {action: {payload: {event}}} = coeffects;

    if(event.type === 'click' )
        stopAllPropagations(event);

    if (event.type === 'keyup' && (event.keyCode === ENTER_KEY_CODE || event.keyCode === SPACE_KEY_CODE))
        stopAllPropagations(event);
};

const nocDocumentClickEffect = coeffects => {
    const {dispatch, action: {payload: {event}}} = coeffects;

    if(event.type === 'click')
        closePopOver(dispatch);

    if (event.type === 'keyup' && (event.keyCode === ENTER_KEY_CODE || event.keyCode === SPACE_KEY_CODE)) {
        closePopOver(dispatch);
    }
};

const stopAllPropagations = (event) => {
    event.stopPropagation();
    event.stopImmediatePropagation();
};

const closePopOver = (dispatch) => {
    dispatch(nocServicePreviewBehaviors.ACTION_TYPES.NOC_CLEAR_SERVICE_PREVIEW, {});
};

export const effectHandlers = {
    nocPopoverClickedEffect,
    nocDocumentClickEffect
};

export default {
    effectHandlers
};